import {TPNIntegrationType} from "@revdesk/model/build/mediation"
import {DataClass, dataClass} from "data-class-copy"
import * as memoizeOne from "memoize-one"
import {observable} from "mobx"
import {BrowseState, Filter, Query} from "../browse"
import {TpnAppState} from "../tpn-app"
import {TPNGridCardState} from "./tpnGridCardState"

export interface TPNGridFilters {
  type: Filter<string[]>
  search: Filter<string>
  status: Filter<string>
  placementTypes: Filter<string[]>
}

export type TPNGridQuery = Query<TPNGridFilters>

export interface TPNGridStateParams {
  tpnApp: TpnAppState
  activeNetworks: TPNGridCardState[]
  disabledNetworks: TPNGridCardState[]
  integrationToEdit: TPNGridCardState | null
  browse: BrowseState<TPNGridCardState, TPNGridFilters>
  integrationType: TPNIntegrationType
}

export interface TPNGridState extends TPNGridStateParams, DataClass<TPNGridStateParams> {

}

const memoDSP = memoizeOne(
  (cards: TPNGridCardState[]) => {
    return cards.map(value => ({key: value.tpnIntegration.networkId, value}))
  }
)

@dataClass
export class TPNGridState {
  static empty = () => new TPNGridState({
    browse: BrowseState.empty<TPNGridCardState, TPNGridFilters>({
      type: new Filter([]),
      search: new Filter(""),
      status: new Filter("All"),
      placementTypes: new Filter([]),
    }).changeLimit(50),
    integrationType: TPNIntegrationType.unknown,
    integrationToEdit: null,
    activeNetworks: [],
    disabledNetworks: [],
    tpnApp: TpnAppState.empty()
  })

  @observable integrationToEdit!: TPNGridCardState | null
  @observable activeNetworks!: TPNGridCardState[]
  @observable disabledNetworks!: TPNGridCardState[]

  constructor(params: TPNGridStateParams) {
    Object.assign(this, params)
  }

  get dsp() {
    return memoDSP(this.browse.result.records)
  }

  get isLoading() {
    return this.browse.isLoading
  }
}
