import {DataClass, dataClass} from "data-class-copy"

export interface FetchStateParams {
  id: string
}

export interface FetchState extends FetchStateParams, DataClass<FetchStateParams> {

}

@dataClass
export class FetchState {
  static loading = new FetchState({
    id: "loading"
  })
  static error = new FetchState({
    id: "error"
  })
  static resolved = new FetchState({
    id: "resolved"
  })
  static noData = new FetchState({
    id: "noData"
  })

  static fromArray<T>(arr: T[]) {
    return arr.isEmpty ? FetchState.noData : FetchState.resolved
  }

  constructor(params: FetchStateParams) {
    Object.assign(this, params)
  }

  get isLoading() {
    return this == FetchState.loading
  }

  get isResolved() {
    return this == FetchState.resolved
  }

  get isNoData() {
    return this == FetchState.noData
  }

  get isError() {
    return this == FetchState.error
  }

  toJSON() {
    return this.id
  }
}
