import {AppActions} from "@revdesk/state-actions/build/app/appActions"
import * as assert from "assert"
import * as React from "react"
import {ComponentClass} from "react"

export interface WithAppActionsProps {
  children: (appActions: AppActions) => React.ReactElement<any>
}

export class WithAppActions extends React.Component<WithAppActionsProps> {
  static contextType: React.Context<AppActions>
  context!: AppActions

  render() {
    return this.props.children(this.context)
  }
}

export function withAppActions(ComponentClass: ComponentClass<any, any>) {
  assert(WithAppActions.contextType != null)
  ComponentClass.contextType = WithAppActions.contextType
}
