import {stringMatch} from "@revdesk/util";
import {dataClass, DataClass} from "data-class-copy/lib"
import {Nullable} from "tsla-util/lib/nullable"
import {AccessLevel} from "./access.level";
import {Permission} from "./permission"
import {Resource} from "./resource"
import {Option} from "tsla-util/lib/option"

export interface ActionParams<T = any> {
  read: Nullable<Permission<T>>;
  write: Nullable<Permission<T>>;
  resource: Resource<T>;
}

export interface Action<T = any> extends ActionParams<T>, DataClass<Action<T>> {

}

@dataClass
export class Action<T = any> {
  static EMPTY = new Action<any>({
    read: Permission.EMPTY,
    write: Permission.EMPTY,
    resource: Resource.UNKNOWN
  })

  static actionsFromPermissions(permissions: Permission[], includeAllResources = true) {
    const resourceIdToPermissions = permissions.groupBy((p) => p.resource.id)
    return Resource.selectableValues
      .filter(
        resource => includeAllResources || resourceIdToPermissions[resource.id] != null
      )
      .map(
        resource => Action.fromPermissions(resource, resourceIdToPermissions[resource.id] || [])
      )
  }

  static fromPermissions<T>(resource: Resource<T>, permissions: Permission<T>[]): Action<T> {
    const map = permissions.keyBy(p => p.accessLevel.id)
    return Action.EMPTY.copy({
      read: Option.of<Permission>(map.read || map.write).map(e => e.copy({accessLevel: AccessLevel.READ})).get,
      write: map.write || null,
      resource
    })
  }

  constructor(params: ActionParams) {
    Object.assign(this, params)
  }

  changeAccessLevel(accessLevel: AccessLevel) {
    const permission: Permission<T> = Permission.EMPTY.copy({
      resource: this.resource,
      accessLevel,
    })
    const map: Record<string, Permission<T>> = [permission].keyBy(p => p.accessLevel.id)
    return this.copy({
      read: map.read || map.write || null,
      write: map.write || null,
    })
  }

  createPermission(accessLevel: AccessLevel, resourceData: T = this.resource.emptyData()): Permission<T> {
    return Permission.EMPTY.copy({
      accessLevel,
      resource: this.resource,
      resourceData
    })
  }

  getOrCreatePermission(accessLevel: AccessLevel, resourceData: T = this.resource.emptyData()): Permission<T> {
    return stringMatch({
      [AccessLevel.READ.id]: () => this.read || this.createPermission(accessLevel, resourceData),
      [AccessLevel.WRITE.id]: () => this.write || this.createPermission(accessLevel, resourceData),
      Else: () => Permission.EMPTY
    })(accessLevel.id)
  }

  get permissions(): Permission[] {
    return [this.read!, this.write!].filter(permission => permission != null)
  }

  canRead(data: T) {
    return this.resource.canRead(this, data)
  }

  canWrite(data: T) {
    return this.resource.canWrite(this, data)
  }
}
