import Grid, {GridClassKey, GridProps} from "@material-ui/core/Grid"
import createStyles from "@material-ui/core/styles/createStyles"
import withStyles from "@material-ui/core/styles/withStyles"
import * as classNames from "classnames"
import * as React from "react"
import {Classes} from "../../util"

const styles = createStyles({
  pointer: {
    cursor: "pointer"
  }
})

export interface ButtonBaseProps extends Partial<GridProps> {
  classes?: Classes<typeof styles> & Record<GridClassKey, string>
  children?: React.ReactNode
}

export const ButtonBase: React.FunctionComponent<ButtonBaseProps> = (withStyles(styles) as any)(
  function ButtonBase({classes, className, ...rest}: ButtonBaseProps) {
    return <Grid className={classNames(className, classes!.pointer)} {...rest}/>
  }
)
