import {boolMatch} from "../patternMatch"

export interface ShallowParseOptions {
  addPrefix?: boolean
  prefix?: string
  pairDelimiter?: string
  keyValDelimiter?: string
}

export function shallowStringify(
  data: Record<string, any>,
  {
    addPrefix = true, prefix = "?",
    pairDelimiter = "&", keyValDelimiter = "="
  }: ShallowParseOptions = {},
  encode = encodeURIComponent
) {
  const keys = Object.keys(data)
  if (!keys.length) {
    return ""
  }
  return boolMatch({
    True: () => prefix,
    False: () => ""
  })(addPrefix) + keys
    .map(key => `${encode(key)}${keyValDelimiter}${encode(data[key])}`)
    .join(pairDelimiter)
}
