import {Platform} from "../platform"

const idToAppType: Record<string, AppType> = {}

export interface AppTypeParams {
  id: "app" | "site" | "all" | string;
  name: string;
  platforms: Platform[];
}

export interface AppType extends AppTypeParams {

}

export class AppType {
  static parse(params: string) {
    return idToAppType[params] || AppType.UNKNOWN
  }

  static UNKNOWN = new AppType({
    id: "",
    name: "",
    platforms: []
  })

  static ALL = new AppType({
    id: "all",
    name: "All",
    platforms: []
  })

  static APP = new AppType({
    id: "app",
    name: "In-App",
    platforms: [Platform.ANDROID, Platform.iOS]
  })

  static SITE = new AppType({
    id: "site",
    name: "Mobile Site",
    platforms: [Platform.MOBILE_WEB]
  })

  constructor(params: AppTypeParams) {
    this.id = params.id
    this.name = params.name
    this.platforms = params.platforms
    idToAppType[params.id] = this
  }

  toJSON() {
    return this.id
  }

  toPlatformQuery() {
    const {platforms} = this
    return platforms.length ? {
      platform: {
        $or: [{
          $in: platforms.map(p => p.id),
        }, {
          $eq: null
        }]
      }
    } : {}
  }
}
