import {observer} from "mobx-react"
import * as React from "react"
import {AppActions} from "@revdesk/state-actions/build/app/appActions"
import {AppRoute} from "@revdesk/state-actions/build/app-route/appRoute"
import {SuspenseAuthPage} from "../app/suspenseAuthPage"
import {SuspenseBasicPage} from "../app/suspenseBasicPage"
import {SuspenseMenuPage} from "../app/suspenseMenuPage"
import {lazyLike} from "./lazyLike"

const RestorePassword = React.lazy(() => import(
  /* webpackChunkName: "restorePassword" */ /* webpackPreload: true */ "./restorePasswordRoute"
  ))

const Login = React.lazy(() => import(
  /* webpackChunkName: "login" */ /* webpackPreload: true */ "./loginRoute"
  ))

const ResetPassword = React.lazy(() => import(
  /* webpackChunkName: "resetPassword" */ "./resetPasswordRoute"
  ))

const SignUp = React.lazy(() => import(
  /* webpackChunkName: "signUp" */ /* webpackPreload: true */  "./signUpRoute"
  ))

const AddApp = React.lazy(() => import(/* webpackChunkName: "addApp" */ "./addAppRoute"))

const EditApp = React.lazy(() => import(/* webpackChunkName: "editApp" */ "./editAppRoute"))

const Apps = React.lazy(() => import(/* webpackChunkName: "apps" */"./appManagementRoute"))

const AddPlacement = React.lazy(() => import(/* webpackChunkName: "addPlacement" */ "./addPlacementRoute"))

const Placements = React.lazy(() => import(/* webpackChunkName: "placements" */ "./placementManagementRoute"))

const DemandSource = React.lazy(() => import(/* webpackChunkName: "demandSource" */ "./demandSourceRoute"))

const EditPlacement = React.lazy(() => import(/* webpackChunkName: "editPlacement" */ "./placementConfigRoute"))

const Users = React.lazy(() => import(/* webpackChunkName: "Users" */ "./usersRoute"))

const pivotRoute = () => import(/* webpackChunkName: "pivotRoute" */ "./pivotRoute")
const tipaltiRoute = () => import(/* webpackChunkName: "tipaltiRoute" */ "./tipaltiRoute")

const Overview = lazyLike(
  pivotRoute,
  PivotRoute => <PivotRoute cube="mediation"/>
)

const Programmatic = lazyLike(
  pivotRoute,
  PivotRoute => <PivotRoute cube="auctions"/>
)

const Bids = lazyLike(
  pivotRoute,
  PivotRoute => <PivotRoute cube="bids"/>
)

const collinsFrame = async () => {
  const {CollinsFrame} = await import(/* webpackChunkName: "collinsFrame" */ "../pages/collins-frame")
  return {default: CollinsFrame}
}

const BlockingControls = lazyLike(
  collinsFrame,
  CollinsFrame => <CollinsFrame path="demand-control/settings"/>
)

const Audience = lazyLike(
  collinsFrame,
  CollinsFrame => <CollinsFrame path="audiences/edit"/>
)

const UserProfile = lazyLike(
  collinsFrame,
  CollinsFrame => <CollinsFrame path="settings"/>
)

const ChangePassword = lazyLike(
  collinsFrame,
  CollinsFrame => <CollinsFrame path="change-password"/>
)

const tipalti = {
  details: lazyLike(
    tipaltiRoute,
    TipaltiRoute => <TipaltiRoute path="details"/>
  ),
  history: lazyLike(
    tipaltiRoute,
    TipaltiRoute => <TipaltiRoute path="history"/>
  ),
  invoices: lazyLike(
    tipaltiRoute,
    TipaltiRoute => <TipaltiRoute path="invoices"/>
  ),
}

export interface RoutesProps {
  actions: AppActions
}

@observer
export class Routes extends React.Component<RoutesProps> {
  login = {
    path: "/login",
    component: () => <SuspenseAuthPage promise={Login}/>
  }

  restorePassword = {
    path: "/restore-password",
    component: () => <SuspenseAuthPage promise={RestorePassword}/>
  }

  changePassword = {
    path: "/reset-password",
    component: () => <SuspenseAuthPage promise={ResetPassword}/>
  }

  signUp = {
    path: "/sign-up",
    component: () => <SuspenseAuthPage promise={SignUp}/>
  }

  demandSource = {
    path: "/demand-source",
    component: () => <SuspenseMenuPage title="Demand Sources" promise={DemandSource}/>
  }

  apps = {
    path: "/apps",
    component: () => <SuspenseMenuPage title={this.props.actions.state.userSession.user.hasApps ? "App Management" : ""}
                                       promise={Apps}/>
  }

  editApp = {
    path: "/edit-app",
    component: () => <SuspenseBasicPage promise={EditApp}/>
  }

  addApp = {
    path: "/add-app",
    component: () => <SuspenseBasicPage promise={AddApp}/>
  }

  placements = {
    path: "/placements",
    component: () => <SuspenseMenuPage title="Placements" promise={Placements}/>
  }

  addPlacement = {
    path: "/add-placement",
    component: () => <SuspenseBasicPage promise={AddPlacement}/>
  }

  editPlacement = {
    path: "/edit-placement",
    component: () => <SuspenseBasicPage promise={EditPlacement}/>
  }

  users = {
    path: "/users",
    component: () => <SuspenseMenuPage title="Users" promise={Users}/>
  }

  routeMatch: Record<string, () => React.ReactElement<any>> = {
    "/restore-password": () => this.restorePassword.component(),
    "/reset-password": () => this.changePassword.component(),
    "/login": () => this.login.component(),
    "/demand-source": () => this.demandSource.component(),
    "/apps": () => this.apps.component(),
    "/add-app": () => this.addApp.component(),
    "/edit-app": () => this.editApp.component(),
    "/sign-up": () => this.signUp.component(),
    "/add-placement": () => this.addPlacement.component(),
    "/edit-placement": () => this.editPlacement.component(),
    "/placements": () => this.placements.component(),
    [AppRoute.overview.path]: () => <SuspenseMenuPage title={AppRoute.overview.name}
                                                      titleProps={{variant: "s28w600"}}
                                                      promise={Overview}/>,
    [AppRoute.programmatic.path]: () => <SuspenseMenuPage title={AppRoute.programmatic.name}
                                                          titleProps={{variant: "s28w600"}}
                                                          promise={Programmatic}/>,
    [AppRoute.bids.path]: () => <SuspenseMenuPage title={AppRoute.bids.name}
                                                          titleProps={{variant: "s28w600"}}
                                                          promise={Bids}/>,
    "/blocking-controls": () => <SuspenseMenuPage title="Blocking Controls" promise={BlockingControls}/>,
    "/users": () => this.users.component(),
    "/audience": () => <SuspenseMenuPage title="Audience" promise={Audience}/>,
    [AppRoute.financeDetails.path]: () => <SuspenseMenuPage title={AppRoute.financeDetails.name}
                                                            promise={tipalti.details}/>,
    [AppRoute.financeInvoices.path]: () => <SuspenseMenuPage title={AppRoute.financeInvoices.name}
                                                             promise={tipalti.invoices}/>,
    [AppRoute.financeHistory.path]: () => <SuspenseMenuPage title={AppRoute.financeHistory.name}
                                                            promise={tipalti.history}/>,
    [AppRoute.userProfile.path]: () => <SuspenseMenuPage title={AppRoute.userProfile.name}
                                                         promise={UserProfile}/>,
    [AppRoute.changePassword.path]: () => <SuspenseMenuPage title={AppRoute.changePassword.name}
                                                            promise={ChangePassword}/>,
  }

  render() {
    const {state} = this.props.actions
    const route = this.routeMatch[state.router.path]
    if (route != null) {
      return route()
    }
    return <div/>
  }
}
