import Grid, {GridProps} from "@material-ui/core/Grid/Grid"
import * as React from "react"
import {Padding} from "../../layout"
import {RevDeskTypography} from "../revDeskTypography"
import {OnBoardBackground} from "./onBoardBackground"
import {OnBoardBox, OnBoardBoxParams} from "./onBoardBox"

export interface OnboardLayoutParams extends GridProps {
  title?: string
  iconPosition?: "tr" | "br"
  loading?: boolean
  onboardBoxProps?: OnBoardBoxParams
}

export class OnboardLayout extends React.Component<OnboardLayoutParams> {
  render() {
    const {children, title, iconPosition, loading, onboardBoxProps, ...props} = this.props
    return <OnBoardBackground container direction="column" wrap="nowrap" alignItems="center"
                              justify="center" {...props}>
      <OnBoardBox loading={loading} iconPosition={iconPosition} {...onboardBoxProps}>
        <Padding xsb={16}>
          <Grid item>
            <RevDeskTypography variant="s80w700">
              {title}
            </RevDeskTypography>
          </Grid>
        </Padding>
        {children}
      </OnBoardBox>
    </OnBoardBackground>
  }
}
