import {DataClass, dataClass} from "data-class-copy"
import {parseString, parseNumber} from "tsla-util/lib/parse"
import {Currency} from "./currency"
import {RewardingType} from "./rewardingType"

export interface RewardedConfigSchema {
  currencyId: string | number
  placementId: string | number
  amount: number
  rewardingType: string
}

export interface RewardedConfigParams {
  id: string
  currency: Currency
  amount: number
  rewardingType: RewardingType
  placementId: string
}

export interface RewardedConfig extends RewardedConfigParams, DataClass<RewardedConfigParams> {

}

@dataClass
export class RewardedConfig {
  static parse(params: any = {}) {
    return RewardedConfig.empty.copy({
      id: parseString(params.id),
      currency: Currency.parse(params.currency),
      amount: parseNumber(params.amount),
      rewardingType: RewardingType.parse(params.rewardingType),
      placementId: parseString(params.placementId)
    })
  }

  static empty = new RewardedConfig({
    id: "",
    currency: Currency.empty,
    amount: 0,
    rewardingType: RewardingType.client,
    placementId: ""
  })

  constructor(params: RewardedConfigParams) {
    Object.assign(this, params)
  }

  get isServerSide() {
    return this.rewardingType === RewardingType.server
  }
}
