import {Trait} from "ts-trait/build"

export type EventHandler<T=any> = (event?: any) => void;

export interface EventEmitter extends Trait {

}

export class EventEmitter extends Trait {
  callbacks: Record<string, EventHandler[]> = {}

  on(type: string, handler: EventHandler) {
    (this.callbacks[type] || (this.callbacks[type] = [])).push(handler)
    return () => this.off(type, handler)
  }

  off(type: string, handler: EventHandler) {
    const {callbacks} = this
    if (callbacks[type]) {
      if (handler) {
        const index = callbacks[type].indexOf(handler)
        index !== -1 && callbacks[type].splice(index, 1)
      } else {
        callbacks[type] = []
      }
    }
  }

  emit(type: string, evt?: any) {
    (this.callbacks[type] || []).map((handler) => {
      handler(evt);
    });
  }
}
