import {DataClass, dataClass} from "data-class-copy"
import {Option} from "tsla-util/lib/option"
import {parseNumber, parseString} from "tsla-util/lib/parse"
import {AppStatus} from "../inventory/appStatus"
import {BiddingType} from "./biddingType"
import {Freq} from "./freq"
import {MediationConfig, MediationConfigInstance, MediationConfigParams} from "./mediationConfig"
import {MediationConfigCappingPacingInstance} from "./mediationConfigCappingPacing"
import {MediationConfigTargetingInstance} from "./mediationConfigTargeting"
import {TPNIntegration, TPNIntegrationInstance} from "./tpnIntegration"
import {TPNPlacement, TPNPlacementInstance} from "./tpnPlacement"
import {TargetingType} from "./targetingType"
import {FreqType} from "./freqType"

const uniqueId = require("lodash/uniqueId")

export interface PlacementTPNConfigTargetingInstance extends MediationConfigTargetingInstance {
  placementTPNConfigId: string | number
}

export interface PlacementTPNConfigCappingInstance extends MediationConfigCappingPacingInstance {
  placementTPNConfigId: string | number
}

export interface PlacementTPNConfigSchema {
  id?: number | string
  unitId: number | string
  tpnPlacementId: number | string
  ecpm: number | null
  rank: number | null
  status: string
  updatedAt?: Date
  enabled: boolean
}

export interface PlacementTPNConfigInstance extends PlacementTPNConfigSchema, MediationConfigInstance {
  tpnPlacement?: TPNPlacementInstance
  tpnIntegration?: TPNIntegrationInstance
  cappingPacing: PlacementTPNConfigCappingInstance[]
  capping?: PlacementTPNConfigCappingInstance
  pacing?: PlacementTPNConfigCappingInstance
  targeting?: PlacementTPNConfigTargetingInstance[]
}


export interface PlacementTPNConfigParams extends MediationConfigParams {
  id: string
  key: string
  status: AppStatus
  ecpm: number | null
  rank: number | null
  tpnPlacement: TPNPlacement
  tpnIntegration: TPNIntegration
}

export interface PlacementTPNConfig extends PlacementTPNConfigParams, DataClass<PlacementTPNConfigParams> {
}

@dataClass
export class PlacementTPNConfig {
  static empty = () => new PlacementTPNConfig({
    id: "",
    key: "",
    status: AppStatus.UNKNOWN,
    ecpm: null,
    rank: null,
    tpnPlacement: TPNPlacement.empty,
    tpnIntegration: TPNIntegration.empty,
    capping: Freq.hour,
    pacing: Freq.oneMinute,
    geo: {
      countries: [],
      include: true,
    },
    connectivity: [],
    targetingEnabled: false,
  })

  static parse(params: PlacementTPNConfigInstance) {
    const id = parseString(params.id)
    return PlacementTPNConfig.empty().mutate({
      id,
      key: id,
      status: AppStatus.parse(Option.of(params.status).map(parseString).get),
      ecpm: Option.of(params.ecpm).map(parseNumber).get,
      rank: Option.of(params.rank).map(parseNumber).get,
      tpnPlacement: TPNPlacement.parse(params.tpnPlacement),
      tpnIntegration: TPNIntegration.parse(params.tpnIntegration),
      ...MediationConfig.parse(params)
    })
  }

  constructor(params: PlacementTPNConfigParams) {
    Object.assign(this, params)
  }

  get biddingType() {
    return this.ecpm == null ? BiddingType.auto : BiddingType.fixed
  }

  toRecord(unitId: string) {
    const {ecpm, rank, status, tpnPlacement} = this
    return {
      tpnPlacementId: tpnPlacement.id,
      unitId,
      ecpm,
      rank,
      status: status.id,
      enabled: status.isActive
    }
  }

  toTargetingRecords() {
    const {geo, id} = this
    const {countries, include} = geo
    return [{
      id: "",
      type: TargetingType.geo.id,
      enabled: !!countries.length,
      data: {
        countries: countries.map(e => e.code),
        include
      },
      placementTPNConfigId: id,
    }]
  }

  get duplicate() {
    return this.copy({
      id: "",
      key: `tc_${uniqueId()}`
    })
  }

  get toCappingRecords() {
    const {id, pacing, capping} = this
    return [{
      id: "",
      enabled: capping.enabled,
      unit: capping.unit.id,
      value: capping.value,
      placementTPNConfigId: id,
      cappingType: FreqType.capping.bit
    }, {
      id: "",
      enabled: pacing.enabled,
      unit: pacing.unit.id,
      value: pacing.value,
      placementTPNConfigId: id,
      cappingType: FreqType.pacing.bit
    }]
  }

  changeTpnIntegrationFrom(tpnIntegrations: TPNIntegration[]) {
    return this.copy({
      tpnIntegration: tpnIntegrations.findOne(
        tpnIntegration => this.tpnPlacement.tpnIntegrationId === tpnIntegration.id
      ).getOrElse(TPNIntegration.empty)
    })
  }

  changeTPNPlacementFrom(tpnPlacements: TPNPlacement[]) {
    return this.copy({
      tpnPlacement: tpnPlacements.findOne(
        tpnPlacement => this.tpnPlacement.id === tpnPlacement.id
      ).getOrElse(TPNPlacement.empty)
    })
  }
}
