import {DataClass, dataClass} from "data-class-copy"
import {observable, action} from "mobx"

export interface TabsStateParams<T> {
  tabs: T[]
  tab: number
  value: T | null
}

export interface TabsState<T> extends TabsStateParams<T>, DataClass<TabsStateParams<T>> {

}

@dataClass
export class TabsState<T> {
  static none = <T>() => TabsState.empty<T>(null, -1)

  static empty = <T>(initialValue: T | null, tab = 0) => new TabsState({
    tabs: [],
    tab,
    value: initialValue
  })

  static fromArray = <T>(tabs: T[], tab = 0) => new TabsState({
    tabs,
    tab,
    value: tabs[tab]
  })

  @observable tab!: number
  @observable tabs!: T[]
  @observable value!: T | null

  constructor(params: TabsStateParams<T>) {
    Object.assign(this, params)
  }

  @action changeTab(tab: number) {
    this.mutate({
      tab, value: this.tabs[tab]
    })
  }

  @action changeValue(value: T) {
    const tab = this.tabs.indexOf(value)
    if (tab !== -1) {
      this.changeTab(tab)
    }
  }

  changeArray(tabs: T[], tab = 0) {
    return this.mutate(({
      tabs,
      tab,
      value: tabs[tab]
    }))
  }
}
