import {CreativeType, Param, ParamValue, PlacementType, TPNPlacement, ValueType} from "@revdesk/model/build/mediation"
import {boolMatch} from "@revdesk/util"
import {DataClass, dataClass} from "data-class-copy"
import {Validator} from "formstate"
import {observable} from "mobx"
import {firstErrorIfArray, FormState} from "../extensions/formstate"
import {FetchState} from "../fetch-state"
import {ParamValueFieldState, ParamValueValidator} from "../param-value-form"
import {CommonValidators} from "../validators/commonValidators"
import {PlacementValidator, TPNPlacementFormState} from "./tpnPlacementFormState"

export interface TPNPlacementStateParams {
  tpnIntegrationId: string
  form: FormState<TPNPlacementFormState[]>
  fetchState: FetchState
  placementType: PlacementType
}

export interface TPNPlacementState extends TPNPlacementStateParams, DataClass<TPNPlacementStateParams> {

}

@dataClass
export class TPNPlacementState {
  static empty = () => new TPNPlacementState({
    tpnIntegrationId: "",
    form: new FormState([]),
    fetchState: FetchState.noData,
    placementType: PlacementType.INTERSTITIAL,
  })

  static messages = {
    valueAlreadyExists: "Value already exists",
    placementNameAlreadyExists: "Instance name already exists"
  }

  static createCreativeTypeForms(
    params: Param[],
    placementType: PlacementType,
    validators: ParamValueValidator[],
    placementValidators: PlacementValidator[]
  ) {

    const creativeTypes = [
      ...params.flatMap(p => p.creativeTypes),
      CreativeType.unknown
    ]
      .filter(e => params.some(param => param.supportsCreativeType(e)))
      .distinctBy(e => e.id)

    const placements = creativeTypes.map(
      creativeType => boolMatch({
        True: () => TPNPlacement.empty.copy({
          name: "",
          placementType,
          paramValues: params.filter(param => param.creativeTypes.isEmpty).map(ParamValue.emptyFromParam)
        }),
        False: () => TPNPlacement.empty.copy({
          name: "",
          creativeType,
          placementType,
          paramValues: params.filter(param => param.supportsCreativeType(creativeType)).map(ParamValue.emptyFromParam)
        })
      })(creativeType.isUnknown)
    )

    return new FormState(
      placements.map(
        placement => new TPNPlacementFormState({placement, validators, placementValidators})
      )
    )
  }

  static toTPNPlacements(form: FormState<TPNPlacementFormState[]>) {
    return form.$.map(
      e => e.placement.copy({
        name: e.$.placementName.value,
        paramValues: e.$.paramValues.$.map(p => p.toParamValue)
      })
    )
  }

  @observable form!: FormState<TPNPlacementFormState[]>
  @observable fetchState!: FetchState

  constructor(params: TPNPlacementStateParams) {
    Object.assign(this, params)
  }

  get toTPNPlacements() {
    return TPNPlacementState.toTPNPlacements(this.form).filter(
      e => e.name.trim().length > 0 && e.paramValues.every(e => e.isValid)
    )
  }

  isOtherPlacementValid = {
    form: (form: TPNPlacementFormState, validator: Validator<TPNPlacementFormState>) => (
      this.form.$.some(
        e => e !== form && !validator(e)
      ) &&
      !!firstErrorIfArray(
        form.$.paramValues.$.flatMap(e => e.validateParamValidations())
      )  // only skip if entire row is empty
    ),
    field: (field: ParamValueFieldState, validator: Validator<ValueType>) => {
      const form = this.form.$.find(e => e.$.paramValues.$.containsAll([field]))!
      return (
        this.form.$.some(
          e => (
            e !== form &&
            e.$.paramValues.$.some(e => !validator(e.value))
          )
        ) &&
        !!CommonValidators.nonEmpty(form.$.placementName.value) // only skip if entire row is empty
      )
    }
  }
}
