export interface HasID {
  id: string;
  name: string;
}

export class HasID {
  static distinct = (e: HasID) => e.id

  // equals(other: any) {
  //   return other instanceof this.constructor && other.id == this.id
  // }
}

export interface Searchable extends HasID {
  name: string;
}

export class Searchable {
  search(q: string = "") {
    return this.id == q || this.name.toLowerCase().indexOf(q.toLowerCase()) !== -1
  }
}
