import * as React from "react"

export function lazyLike<P>(
  factory: () => Promise<{ default: React.ComponentType<P> }>,
  fn: (Component: React.ComponentType<P>) => React.ReactElement<P>
) {
  return React.lazy(
    async () => {
      const {default: Component} = await factory()
      return {
        default: function LazyLike() {
          return fn(Component)
        }
      }
    }
  )
}
