import {autoBind} from "@revdesk/util/build/auto.bind"
import {Observe} from "@revdesk/util/build/observe"
import * as jQuery from "jquery"
import * as React from "react"
import {HTMLAttributes, ReactElement} from "react"
import {Column, ColumnProps} from "./flex/flex"
import {OuterClick} from "./outerClick"

export interface BubbleMenuProps extends HTMLAttributes<any> {
  dismissOnClickOutside?: boolean
  open?: boolean;
  onOpen?: (open: boolean) => unknown;
  header: ReactElement<any>;
  zIndex?: number;
  bodyWidth?: string;
  minSize?: number;
  placement?: string;
  sticky?: boolean;
  disabled?: boolean;
  bodyStyle?: React.CSSProperties
  disableFocus?: boolean
  disableClick?: boolean
  disableEdgeCheck?: boolean
  columnProps?: Partial<ColumnProps>
  children: React.ReactNode | ((onClose: () => any) => React.ReactNode)
}

function isChildrenFunction(
  e: React.ReactNode | ((onClose: () => any) => React.ReactNode)
): e is (onClose: () => any) => React.ReactNode {
  return typeof e === "function"
}

export interface BubbleMenuState {
  open: boolean;
  edge: boolean;
}

// tslint:disable
export interface PlacementDesc {
  "true": any;
  "false": any;
  checkEdge: any;
}

const placements: Record<string, PlacementDesc> = {
  overlay: {
    "true": {bottom: 0},
    "false": {top: 0},
    checkEdge($element: JQuery, $body: JQuery, minSize: number) {
      // const bubbleHeight = $element[0].scrollHeight
      const bubbleTop = $element.offset().top
      const windowEnd = jQuery("body")[0].scrollHeight
      const edge = bubbleTop + minSize
      return edge >= windowEnd
    }

  },
  bottom: {
    "true": {bottom: "100%"},
    "false": {top: "100%"},
    checkEdge($element: JQuery, $body: JQuery, minSize: number) {
      const bubbleTop = $element.offset().top
      const windowEnd = jQuery("body")[0].scrollHeight
      const edge = bubbleTop + minSize
      return edge >= windowEnd
    }
  },
}

// tslint:enable
export class BubbleMenu extends React.Component<BubbleMenuProps, BubbleMenuState> {
  static defaultProps = {
    dismissOnClickOutside: true,
    minSize: 0,
    placement: "overlay"
  }

  $element: JQuery
  $body: JQuery

  state = {
    open: false,
    edge: false
  }

  esc = (ev: any) => {
    if (ev.keyCode === 27 && this.state.open && this.props.dismissOnClickOutside) {
      this.setOpen(false)
    }
  }

  componentDidMount() {
    document.addEventListener("keydown", this.esc)
  }

  componentWillUnmount() {
    document.removeEventListener("keydown", this.esc)
  }

  componentWillReceiveProps(nextProps: BubbleMenuProps) {
    if (nextProps.open != null && nextProps.open !== this.state.open) {
      this.setState(this.buildState(nextProps.open, nextProps))
    }
  }

  buildState(open: boolean, props = this.props) {
    const newState = {open} as BubbleMenuState
    if (!props.disableEdgeCheck && open) {
      newState.edge = this.checkEdge(props)
    }
    return newState
  }

  throttleLastOpen = Observe.throttle(this.setOpen)

  handleClick = () => {
    const {sticky, disabled} = this.props
    if (!disabled && (!sticky || !this.state.open)) {
      this.throttleLastOpen(!this.state.open)
    }
  }

  handleFocus = () => {
    this.throttleLastOpen(true)
  }

  handleOuterClick = () => {
    const {dismissOnClickOutside} = this.props
    if (dismissOnClickOutside) {
      this.setOpen(false)
    }
  }

  @autoBind
  setOpen(open: boolean) {
    const {onOpen} = this.props
    this.setState(this.buildState(open), () => {
      onOpen && onOpen(open)
    })
  }

  close = () => this.setOpen(false)

  checkEdge(props = this.props) {
    const {$element, $body} = this
    const {minSize} = props
    return this.stylesByEdge().checkEdge($element, $body, minSize)
  }

  ref = (outerClick: any) => {
    if (outerClick) {
      this.$element = jQuery(outerClick.element)
    }
  }

  bodyRef = (bodyDiv: any) => {
    if (bodyDiv) {
      this.$body = jQuery(bodyDiv)
    }
  }

  stylesByEdge(props = this.props): PlacementDesc {
    const {placement} = props
    return placements[placement]
  }

  render() {
    const {edge} = this.state
    const {
      children, header, zIndex = 1, bodyWidth = "100%", style, sticky, onOpen,
      dismissOnClickOutside, minSize, placement, bodyStyle, disableFocus, disableClick,
      disableEdgeCheck, columnProps, ...rest
    } = this.props
    const Header = header.type
    const placementDesc = this.stylesByEdge() as any
    return <OuterClick condition={this.state.open} allowHidden ref={this.ref}
                       onOuterClick={this.handleOuterClick}
                       style={{position: "relative", ...style}} {...rest}>
      <Column {...columnProps}>
        <Header onClick={disableClick ? undefined : this.handleClick}
                onFocus={disableFocus ? undefined : this.handleFocus}
                {...header.props}/>
        <div ref={this.bodyRef} style={{
          zIndex,
          width: bodyWidth,
          position: "absolute",
          ...placementDesc[edge as any],
          display: `${this.state.open ? "block" : "none"}`,
          ...bodyStyle
        }}>
          {isChildrenFunction(children) ? children(this.close) : children}
        </div>
      </Column>
    </OuterClick>
  }
}

export const Toggleable = (WrappedComponent: any) => ({onToggle, ...props}: any) =>
  <WrappedComponent onClick={onToggle} {...props} />
