import * as propTypes from "prop-types"
import * as React from "react"

export function withContext(...args: string[]) {
  return function (ctor: any) {
    ctor.contextTypes = ctor.contextTypes || {}
    for (const arg of args) {
      ctor.contextTypes[arg] = propTypes.any
    }
  }
}

export function providesContext(...args: string[]) {
  return function (ctor: any) {
    ctor.childContextTypes = ctor.childContextTypes || {}
    for (const arg of args) {
      ctor.childContextTypes[arg] = propTypes.any
    }
  }
}
