import {DataClass, dataClass} from "data-class-copy"
import {Option} from "tsla-util/lib/option"
import {parseArray, parseBool} from "tsla-util/lib/parse"
import {Country} from "../country"
import {ConnectivityType} from "./connectivityType"
import {Freq} from "./freq"
import {MediationConfigCappingPacingInstance} from "./mediationConfigCappingPacing"
import {ConnectivitySchema, GeoSchema, MediationConfigTargetingInstance} from "./mediationConfigTargeting"

export interface Geo {
  include: boolean,
  countries: Country[]
}

export interface MediationConfigInstance {
  cappingPacing?: MediationConfigCappingPacingInstance[]
  capping?: MediationConfigCappingPacingInstance
  pacing?: MediationConfigCappingPacingInstance
  targeting?: MediationConfigTargetingInstance[]
  geoCodes?: GeoSchema
  geo?: Partial<Geo>
  connectivitySchema: Option<ConnectivitySchema>
  targetingEnabled?: boolean
  connectivity?: string[]
}

export interface MediationConfigParams {
  geo: Geo
  connectivity: ConnectivityType[]
  capping: Freq
  pacing: Freq
  targetingEnabled: boolean
}

export interface MediationConfig extends MediationConfigParams, DataClass<MediationConfigParams> {

}

@dataClass
export class MediationConfig {
  static empty = () => new MediationConfig({
    capping: Freq.hour,
    pacing: Freq.oneMinute,
    geo: {
      countries: [],
      include: true,
    },
    connectivity: [],
    targetingEnabled: false,
  })

  static parse(params: Partial<MediationConfigInstance> = {}) {
    return MediationConfig.empty().copy({
      capping: Freq.parse(params.capping),
      pacing: Freq.parse(params.pacing),
      geo: Option.of(params.geo)
        .map(e => ({
          countries: parseArray(e.countries, Country.parse),
          include: parseBool(e.include),
        }))
        .getOrElse({
          countries: [],
          include: true,
        }),
      connectivity: parseArray(params.connectivity, ConnectivityType.parse),
      targetingEnabled: parseBool(params.targetingEnabled),
    })
  }

  constructor(params: MediationConfigParams) {
    Object.assign(this, params)
  }

  get cappingPair() {
    return [this.capping, this.pacing]
  }

  get hasCappingOrPacing() {
    return this.cappingPair.some(e => e.isSavedAndEnabled)
  }
}
