import {DataClass, dataClass} from "data-class-copy"
import {parseString} from "tsla-util/lib/parse"

export interface CurrencySchema {
  appId: string | number
  currency: string
}

export interface CurrencyParams {
  id: string
  appId: string
  currency: string
}

export interface Currency extends CurrencyParams, DataClass<CurrencyParams> {

}

@dataClass
export class Currency {
  static parse(params: any = {}) {
    return Currency.empty.copy({
      id: parseString(params.id),
      appId: parseString(params.appId),
      currency: parseString(params.currency),
    })
  }

  static empty = new Currency({
    id: "",
    appId: "",
    currency: "",
  })

  constructor(params: CurrencyParams) {
    Object.assign(this, params)
  }

  get isDefined() {
    return !!this.currency
  }

  get value() {
    return this.currency
  }

  get label() {
    return this.currency
  }
}
