import {NestedItem} from "@revdesk/util"
import {dataClass, DataClass} from "data-class-copy/lib"
import {applyMixins} from "ts-trait/build"
import {DataTypes} from "../dataTypes"
import {
  FreqType,
  MediationConfig,
  MediationConfigInstance,
  PlacementTPNConfig,
  PlacementTPNConfigInstance
} from "../mediation"
import {HasID, Searchable} from "../searchable"
import {UnitFormat, UnitType} from "../spots"
import {BaseUnit, ConfigCreator} from "../spots/unit-config"
import {AppStatus} from "./appStatus"
import {FilteredAPI} from "./filteredAPI"
import {FilteredProtocol} from "./filteredProtocol"
import {FloorPrice} from "./floorPrice"
import {Spot} from "./spot"
import {UnitConfigInstance} from "./unitConfig"

export interface UnitSchema {
  id: string | number
  name: string
  contentName: string
  fullName: string
  spotId: string | number
  status: string
  coppa: boolean;
}

export interface UnitInstance extends UnitSchema, MediationConfigInstance {
  tpnConfigs: PlacementTPNConfigInstance[]
  unitConfig?: UnitConfigInstance
  unitFilteredAPIs?: { api: string }[]
  unitFilteredProtocols?: { protocol: string }[]
  mediationConfig?: MediationConfigInstance
}

export interface UnitParams extends HasID {
  name: string;
  spotId: string;
  contentName: string;
  fullName: string;
  coppa: boolean;

  spot: Spot | null;
  floorPrices: FloorPrice[]

  unitType: UnitType
  format: UnitFormat

  config: BaseUnit
  mediationConfig: MediationConfig
  tpnConfigs: PlacementTPNConfig[]
  filteredAPIs: FilteredAPI[]
  filteredProtocols: FilteredProtocol[]
}

export interface Unit extends UnitParams, Searchable, NestedItem<Spot, any>, DataClass<UnitParams> {

}

@dataClass
@applyMixins([NestedItem, Searchable])
export class Unit implements NestedItem<Spot, any> {
  static EMPTY = new Unit({
    id: "",
    name: "",
    spotId: "",
    contentName: "",
    fullName: "",
    spot: null,
    coppa: false,
    floorPrices: [],

    unitType: UnitType.INTERSTITIAL,
    format: UnitFormat.DISPLAY,
    config: ConfigCreator.createUnitConfig(UnitFormat.DISPLAY, UnitType.INTERSTITIAL),
    tpnConfigs: [],
    mediationConfig: MediationConfig.empty(),
    filteredAPIs: [],
    filteredProtocols: []
  })

  static parse(params: any = {}) {
    const unitType = UnitType.parse(params.unitType)
    const format = UnitFormat.parse(params.format)
    return Unit.EMPTY.copy({
      id: params.id != null ? String(params.id) : "",
      spotId: params.spotId != null ? String(params.spotId) : "",
      name: params.name != null ? String(params.name) : "",
      fullName: params.fullName != null ? String(params.fullName) : "",
      contentName: params.contentName != null ? String(params.contentName) : "",
      spot: params.spot && Spot.parse(params.spot),
      floorPrices: (params.floorPrices || []).map(FloorPrice.parse),
      unitType,
      format,
      config: params.config != null ?
        ConfigCreator.createUnitConfig(format, params.config) :
        ConfigCreator.createUnitDefaultConfig(format, unitType),
      tpnConfigs: (params.tpnConfigs || []).map(PlacementTPNConfig.parse),
      mediationConfig: MediationConfig.parse(params.mediationConfig),
      filteredAPIs: (params.filteredAPIs || []).map(FilteredAPI.parse),
      filteredProtocols: (params.filteredProtocols || []).map(FilteredProtocol.parse),
      coppa: Boolean(params.coppa)
    })
  }

  constructor(params: UnitParams) {
    Object.assign(this, params)
  }

  get parent() {
    return this.spot
  }

  withoutChildren(): Unit {
    return this
  }

  changeChildren() {
    return this
  }

  get lite() {
    return {
      unitId: this.id
    }
  }

  getTypeId() {
    return this.unitType.id
  }

  supportsFloating() {
    return UnitType.BANNER.id === this.getTypeId() && this.isMobileWeb()
  }

  supportsPadding() {
    return [UnitType.LANDSCAPE.id, UnitType.SQUARE.id].indexOf(this.getTypeId()) !== -1
  }

  supportsViewability() {
    return this.getTypeId() !== UnitType.REWARDED.id &&
      [UnitFormat.VIDEO.id, UnitFormat.NATIVE.id].indexOf(this.getFormatId()) !== -1
  }

  getFormatId() {
    return this.format.id
  }

  supportsCompanion() {
    return [UnitType.INTERSTITIAL.id, UnitType.REWARDED.id].indexOf(this.getTypeId()) !== -1
  }

  isMobileWeb() {
    return this.spot && this.spot.app && this.spot.app.isMobileWeb()

  }

  get dataType() {
    return DataTypes.UNIT
  }

  setParentOnChildren() {
    return this
  }

  get publisherId() {
    return this.spot && this.spot.publisherId
  }

  get appId() {
    return this.spot && this.spot.appId
  }

  get toCreativeTypes() {
    return this.format.toCreativeTypes(this.unitType)
  }

  get app() {
    return this.spot && this.spot.app
  }

  hideable() {
    return this.unitType.id === UnitType.INTERSTITIAL.id ||
      (this.isMobileWeb() && this.unitType.id === UnitType.BANNER.id)
  }

  closeable() {
    return this.unitType.id === UnitType.INTERSTITIAL.id ||
      (this.isMobileWeb() && this.unitType.id === UnitType.BANNER.id)
  }

  refreshable() {
    return [UnitType.BANNER.id, UnitType.MRECT.id].indexOf(this.unitType.id) !== -1
  }

  isFullScreenUnit() {
    return [UnitType.INTERSTITIAL.id].indexOf(this.unitType.id) !== -1
  }

  supportsSkip() {
    return [UnitType.INTERSTITIAL.id, UnitType.REWARDED.id].indexOf(this.unitType.id) !== -1
  }

  changeFormatAndType(format: UnitFormat, unitType: UnitType) {
    return this.copy({
      format, unitType,
      config: ConfigCreator.createUnitDefaultConfig(format, unitType)
    })
  }

  get toConfigRecord() {
    return {
      id: this.id,
      unitType: this.unitType.id,
      format: this.format.id,
      freq: 1,
      serverConfig: this.config.toServerJS(this),
      restOfConfig: this.config.toJS(this)
    }
  }

  get richMedia() {
    return this.filteredAPIs.indexOf(FilteredAPI.mraid) === -1
  }

  get toRecord() {
    return {
      id: this.id,
      name: this.name,
      contentName: this.contentName,
      fullName: this.fullName,
      spotId: this.spotId,
      publisherId: this.publisherId,
      coppa: this.coppa,
      status: AppStatus.ACTIVE.id
    }
  }

  get toCappingRecords() {
    const {mediationConfig, id} = this
    const {capping, pacing} = mediationConfig
    return [{
      id: "",
      enabled: capping.enabled,
      unit: capping.unit.id,
      value: capping.value,
      unitId: id,
      cappingType: FreqType.capping.bit
    }, {
      id: "",
      enabled: pacing.enabled,
      unit: pacing.unit.id,
      value: pacing.value,
      unitId: id,
      cappingType: FreqType.pacing.bit
    }]
  }

  get isVideo() {
    const {format} = this
    return format.id === UnitFormat.VIDEO.id || format.id === UnitFormat.VIDEO_AND_DISPLAY.id
  }

  get isDisplay() {
    const {format} = this
    return format.id === UnitFormat.DISPLAY.id || format.id === UnitFormat.VIDEO_AND_DISPLAY.id
  }

  get isNative() {
    const {format} = this
    return format.id === UnitFormat.NATIVE.id
  }
}
