export type AccessLevelID = "read" | "write" | "lock" | ""

const values: Record<AccessLevelID, AccessLevel> = {} as any

export interface AccessLevelParams {
  id: AccessLevelID;
  name: string;
  actions: Array<string>;
}

export interface AccessLevel extends AccessLevelParams {

}

export class AccessLevel {
  static READ = new AccessLevel({id: "read", name: "Read", actions: ["read"]})
  static WRITE = new AccessLevel({id: "write", name: "Write", actions: ["read", "create", "update", "delete"]})
  static UNKNOWN = new AccessLevel({id: "", name: "", actions: []})
  static LOCK = new AccessLevel({id: "lock", name: "Lock", actions: []})

  static read = AccessLevel.READ
  static write = AccessLevel.WRITE

  static parse(params: AccessLevelID) {
    return values[params] || AccessLevel.UNKNOWN
  }

  constructor(params: AccessLevelParams) {
    this.id = params.id
    this.name = params.name
    this.actions = params.actions
    values[params.id] = this
  }

  toJSON() {
    return this.id
  }
}
