import {Observe} from "@revdesk/util/build/observe"
import {EventEmitter} from "events"
import {action, runInAction} from "mobx"
import "rxjs/add/operator/filter"
import {Observable} from "rxjs/Observable"
import {HasEmitter, hasEmitter} from "tsla-util/lib/has-emitter"
import {FetchState} from "../fetch-state"
import {BrowseState} from "./browseState"
import {Entity} from "./entity"
import {Filters} from "./query"
import {QueryResult} from "./queryResult"
import debounce = require("lodash/debounce")

export interface BrowseActionsParams<T extends Entity, E extends Filters<E>> {
  state: BrowseState<T, E>
  fetchData: () => Promise<QueryResult<T>>
}

export interface BrowseActions<T extends Entity, E extends Filters<E>> extends BrowseActionsParams<T, E>, HasEmitter {
  on(type: "result" | "hoverIndexArrow", fn: () => any): any

  on(type: "scrollToItem", fn: (offsetTop: number, height: number) => any): any
}

@hasEmitter
export class BrowseActions<T extends Entity, E extends Filters<E>> implements HasEmitter {

  emitter = new EventEmitter()

  constructor(params: BrowseActionsParams<T, E>) {
    Object.assign(this, params)
  }

  private debounceNext = new Observe(
    obs => obs
      .debounce(() => Observable.timer(350))
  ).next

  private refreshNext = new Observe(
    obs => obs
      .switchMap(async () => {
        await this.debounceNext()
        return this.fetchData()
      })
  ).next

  @action.bound refresh() {
    this.reset()
    return this.fetchNextPage()
  }

  fetchNextPageIfNeeded() {
    if (!this.state.fetchState.isLoading && !this.state.result.fetchedAll) {
      this.fetchNextPage()
    }
  }

  removePageOf(value: T) {
    const page = this.state.pageOf(value)
    if (page != null) {
      return this.state.removePage(page)
    }
    return this
  }

  async refreshPageOf(value: T, fn?: (current: T, next: T) => T) {
    const page = this.state.pageOf(value)
    if (page != null) {
      this.state.fetchState = FetchState.loading
      const {from} = this.state.query.pagination.pageBounds(page)
      this.state.query.pagination.moveToPage(page)
      try {
        const result = await this.refreshNext()
        runInAction(() => {
          this.state.mutate({
            result: this.state.result.replacePageResult(result, from, fn),
            resultCopy: this.state.resultCopy.replacePageResult(result, from, fn),
            fetchState: FetchState.resolved,
          })
          this.emit("result")
        })
      } catch (e) {
        console.error(e)
        runInAction(() => {
          this.state.mutate({
            fetchState: FetchState.error
          })
        })
      }
    }
  }

  @action.bound
  async fetchNextPage() {
    this.state.query.pagination.nextPage()
    this.state.fetchState = FetchState.loading
    try {
      const result = await this.refreshNext()
      runInAction(() => {
        this.state.mutate({
          result: this.state.result.addPageResult(result),
          resultCopy: this.state.resultCopy.addPageResult(result),
          fetchState: FetchState.fromArray(result.records),
        })
        this.emit("result")
      })
    } catch (e) {
      console.error(e)
      runInAction(() => {
        this.state.mutate({
          fetchState: FetchState.error
        })
      })
    }
  }

  @action
  reset() {
    this.state.query.reset()
    this.state.result.clear()
    this.state.resultCopy.clear()
  }

  debounceRefresh = debounce(this.refresh, 300)

  @action handleMouseEnter(index: number) {
    this.state.hoverIndex = index
  }

  @action.bound handleMouseLeave() {
    this.state.hoverIndex = -1
  }

  @action.bound handleKeyDown(key: string) {
    const {hoverIndex, result: {records: {length}}} = this.state
    switch (key) {
      case "ArrowUp":
      case "ArrowDown":
        const skip = key === "ArrowUp" ? length - 1 : 1;
        this.state.hoverIndex = (hoverIndex + skip) % length
        this.emit("hoverIndexArrow")
        break
    }
  }

  @action.bound handleItemScrollTo(offsetTop: number, height: number) {
    this.emit("scrollToItem", offsetTop, height)
  }
}
