import {User} from "./user"

export interface RoleParams {
  id: string;
  authId?: number;
  name: string;
  toPublisherId: (user: User, publisherId?: string[], superUserPublishers?: boolean) => string[];
}

export interface Role extends RoleParams {
}

const values: Record<string, Role> = {}

export class Role {
  static parse(params: string) {
    return values[params] || Role.UNKNOWN
  }

  static ADMIN = new Role({
    id: "Inneractive administrator",
    name: "Admin",
    authId: 4,
    toPublisherId: (user, publisherId, superUserPublishers) => publisherId
  })

  static SUPER_USER = new Role({
    id: "Inneractive Publisher administrator",
    name: "Account Manager",
    authId: 7,
    toPublisherId: (user, publisherId, superUserPublishers) =>
      superUserPublishers ? user.superUserPublishers : publisherId
  })

  static PUBLISHER = new Role({
    id: "Default publisher authorizations",
    name: "Publisher",
    authId: 1,
    toPublisherId: (user, publisherId, superUserPublishers) => [user.publisherId]
  })

  static DEMAND = new Role({
    id: "Advertiser",
    name: "Demand",
    authId: 6,
    toPublisherId: (user, publisherId, superUserPublishers) => [null]
  })

  static UNKNOWN = new Role({
    id: "",
    name: "Unknown Role",
    toPublisherId: (user, publisherId, superUserPublishers) => [null]
  })

  constructor(params: RoleParams) {
    Object.assign(this, params)
    values[params.id] = this
  }

  get isAdminOrSuperUser() {
    return this.id === Role.ADMIN.id || this.id === Role.SUPER_USER.id
  }

  toJSON() {
    return this.id
  }
}
