import {
  CreativeType,
  IntegrationParam,
  Param,
  ParamValue,
  PlacementType,
  TPNIntegration,
  TPNPlacement
} from "@revdesk/model/build/mediation"
import {DeletedPlacementsFilters} from "@revdesk/state-actions/build/tpn-placement-list"
import {QueryResult, Query} from "@revdesk/state-actions/build/browse"
import {CollinsFetch} from "@revdesk/state-actions/build/client"
import {TPNGridCardState, TPNGridQuery} from "@revdesk/state-actions/build/tpn-grid"

export interface TPNFetchClientParams {
  fetch: CollinsFetch
}

export interface TPNFetchClient extends TPNFetchClientParams {

}

export class TPNFetchClient {
  constructor(params: TPNFetchClientParams) {
    Object.assign(this, params)
  }

  async getApp(appId: string, networkId: string) {
    const raw = await this.fetch.get("/api/v2/tpn/app", {
      appId, networkId
    })
    return {
      tpnIntegration: TPNIntegration.parse(raw.tpnIntegration),
      placementParams: (raw.placementParams || []).map(Param.parse)
    }
  }

  async getPlacementForm(appId: string, networkId: string, placementType: PlacementType) {
    const raw = await this.fetch.get("/api/v2/tpn/placement-params", {
      appId, networkId, placementType: placementType.id
    })
    return raw.map(Param.parse)
  }

  async saveAppAndPlacements(tpnIntegration: TPNIntegration) {
    const raw = await this.fetch.post("/api/v2/tpn/placements", tpnIntegration.toLite)
    return TPNIntegration.parse(raw)
  }

  async saveApp(tpnIntegration: TPNIntegration) {
    const raw = await this.fetch.post("/api/v2/tpn/app", tpnIntegration.toLite)
    return TPNIntegration.parse(raw)
  }

  async updatePlacement(placement: TPNPlacement) {
    const raw = await this.fetch.put("/api/v2/tpn/placement", placement.toLite)
    return TPNPlacement.parse(raw)
  }

  async paginatePlacements(
    appId: string,
    networkId: string,
    placementType: PlacementType,
    search = "",
    creativeTypes: CreativeType[]
  ) {
    const raw = await this.fetch.post("/api/v2/tpn/get-placements", {
      appId,
      networkId,
      placementType: placementType.id,
      search,
      creativeTypes
    })
    return raw.map(TPNPlacement.parse)
  }

  uniqueAppParamValue(paramValue: ParamValue) {
    return this.fetch.post("/api/v2/tpn/app/unique", paramValue)
  }

  uniquePlacementParamValue(paramValue: ParamValue) {
    return this.fetch.post("/api/v2/tpn/placements/unique-value", paramValue)
  }

  uniquePlacementName(placement: TPNPlacement) {
    return this.fetch.post("/api/v2/tpn/placements/unique-name", placement.toLite)
  }

  uniqueAdUnitNetworkInstance(placement: TPNPlacement, unitId: string) {
    return this.fetch.post("/api/v2/tpn/placements/unique-network-instance", {placement: placement.toLite, unitId})
  }

  async paginate(appId: string, query: TPNGridQuery) {
    const raw = await this.fetch.post("/api/v2/tpn", {
      appId,
      query
    })
    return QueryResult.parse(raw, TPNGridCardState.parse)
  }

  async setIntegrationStatus(tpnIntegration: TPNIntegration) {
    const raw = await this.fetch.put("/api/v2/tpn/app", tpnIntegration.toLite)
    return TPNIntegration.parse(raw)
  }

  deletePlacement(placement: TPNPlacement) {
    return this.fetch.delete("/api/v2/tpn/placements", placement.toLite)
  }

  getTPNPlacementLinkedPlacements(query: Query<DeletedPlacementsFilters>) {
    return this.fetch.post("/api/v2/tpn/get-tpn-placement-linked-placements", query)
  }

  equalsGroupAppParamValue(integrationParam: IntegrationParam) {
    return this.fetch.post("/api/v2/tpn/app/equals-group-value", integrationParam)
  }
}
