import {DataClass} from "data-class-copy/lib"
import {Actions} from "./actions"

export abstract class StateHolder<T> {
  abstract get state(): T;

  abstract setState(t: T, event?: string): void;
}

export class SelfStateHolder<T> implements StateHolder<T> {
  private _state: T;

  constructor(initialState: T) {
    this._state = initialState
  }

  get state(): T {
    return this._state
  }

  set state(t: T) {
    this._state = t
  }

  setState(value: T, event?: string) {
    this._state = value
  }
}

export class FunctionStateHolder<T> implements StateHolder<T> {
  constructor(private getState: () => T, public setState: (state: T, event?: string) => void) {
  }

  get state(): T {
    return this.getState()
  }
}
