import {Theme} from "@material-ui/core/styles/createMuiTheme"
import withStyles from "@material-ui/core/styles/withStyles"
import * as classNames from "classnames"
import * as React from "react"
import {breakpoints, breakpointStyles} from "./breakpointStyles"
import {SizeProps} from "./size"

const sizes = [
  -100, 2, -4, -8, -12, -24,
  ...new Array(15).fill(null).map((_, i) => i * 4),
  "auto"
]

const styles = (theme: Theme) => ({
  ...breakpointStyles(
    theme,
    sizes,
    breakpoints,
    "marginLeft",
    (size) => ({marginLeft: size})
  ),
  ...breakpointStyles(
    theme,
    sizes,
    breakpoints,
    "marginRight",
    (size) => ({marginRight: size})
  ),
  ...breakpointStyles(
    theme,
    sizes,
    breakpoints,
    "margin",
    (size) => ({margin: size})
  ),
  ...breakpointStyles(
    theme,
    sizes,
    breakpoints,
    "marginBottom",
    (size) => ({marginBottom: size})
  ),
  ...breakpointStyles(
    theme,
    sizes,
    breakpoints,
    "marginTop",
    (size) => ({marginTop: size})
  ),
})

export interface MarginProps<P extends { className?: string }> extends SizeProps {
  children: React.ReactElement<P>
  classes?: Record<string, string>
  className?: string
}

export const Margin = withStyles(styles, {name: "Margin", index: 1})(
  <P extends { className?: string }>(props: MarginProps<P>) => {
    const {
      children, classes, className: classNameProp, xsl, xsr, xsrl, xst, xstb, xs, xsb, ...rest
    } = props
    const {type: ComponentType, props: componentProps} = children
    const className = classNames(classNameProp, componentProps.className, {
      [classes![`margin-xs-${xs}`]]: xs != null,
      [classes![`marginRight-xs-${xsr}`]]: xsr != null,
      [classes![`marginLeft-xs-${xsl}`]]: xsl != null,
      [classes![`marginBottom-xs-${xsb}`]]: xsb != null,
      [classes![`marginTop-xs-${xst}`]]: xst != null
    })
    return <ComponentType {...rest} {...componentProps} className={className}/>
  }
)
