import SvgIcon, {SvgIconProps} from "@material-ui/core/SvgIcon"
import * as React from "react"
import {Fill, FillColor} from "../../../layout"
import {fyberLong} from "./path"

export interface FyberLogoParams extends SvgIconProps {
  fill?: FillColor
}

export class FyberLogo extends React.Component<FyberLogoParams> {
  render() {
    const {fill, ...rest} = this.props
    return <SvgIcon viewBox="0 0 120 33" {...rest}>
      <g stroke="none" strokeWidth="1" fillRule="evenodd">
        <Fill color={fill}>
          <path d={fyberLong}/>
        </Fill>
      </g>
    </SvgIcon>
  }
}
