import {DataClass, dataClass} from "data-class-copy"
import {Option} from "tsla-util/lib/option"
import {parseString} from "tsla-util/lib/parse"
import {Param} from "./param"
import {ValueType} from "./paramType"

export interface ParamValueParams<T extends ValueType = ValueType> {
  id: string
  paramId: string
  tpnIntegrationId: string
  param: Param<T>
  value: T
}

export interface ParamValue<T extends ValueType = ValueType> extends ParamValueParams<T>,
  DataClass<ParamValueParams<T>> {
  copy<E extends ValueType = ValueType>(params?: Partial<ParamValueParams<E>>): this
}

@dataClass
export class ParamValue<T extends ValueType = ValueType> {
  static empty = new ParamValue<ValueType>({
    id: "",
    param: Param.empty,
    value: "",
    paramId: "",
    tpnIntegrationId: "",
  })

  static parse<T extends ValueType = ValueType>(params: any = {}) {
    const param = Param.parse(params.param || params.tpnParam)
    return ParamValue.empty.copy({
      id: parseString(params.id),
      param,
      paramId: parseString(params.paramId || params.tpnParamId),
      tpnIntegrationId: parseString(params.tpnIntegrationId || params.id),
      value: param.type.parse(params.value),
    })
  }

  static fromParamAndValue<T extends ValueType = ValueType>(param: Param<T>, value: T) {
    return ParamValue.empty.copy({
      paramId: param.id,
      param,
      value: param.type.parse(value)
    })
  }

  static emptyFromParam<T extends ValueType = ValueType>(param: Param<T>) {
    return ParamValue.fromParamAndValue(param, param.defaultValue)
  }

  constructor(params: ParamValueParams<T>) {
    Object.assign(this, params)
  }

  get toLite() {
    return {
      paramId: this.paramId,
      value: this.value
    }
  }

  get toRecord() {
    return {
      tpnParamId: this.paramId,
      value: this.stringifiedValue
    }
  }

  get stringifiedValue() {
    return this.param.type.stringify(this.value)
  }

  replaceParam(params: Param<T>[]) {
    const param = Option.of(params.find(p => p.id === this.paramId)).getOrElse(Param.empty as Param<T>)
    return this.mutate({param})
  }

  replaceParamFromParamValues(paramValues: ParamValue[]) {
    const paramValue = paramValues.findOne(e => e.paramId === this.paramId).getOrElse(ParamValue.empty)
    return this.mutate({
      id: paramValue.id,
      param: paramValue.param as any as Param<T>,
    })
  }

  get complete() {
    return this.param.optional || Boolean(this.id) // saved in db, mean this param is complete
  }

  get isValid() {
    return this.param.validators.every(validator => !validator(this.value))
  }
}
