import * as  React from "react"
import {TransitionProps} from "react-transition-group/Transition"
import {Transitioned} from "../../../components/transitioned"

const styles = {
  root: {
    transitionProperty: "transform",
    transitionTimingFunction: "ease-in",
    transform: "rotate(0deg)",
  },
  entering: {
    transform: "rotate(180deg)",
  },
  entered: {
    transform: "rotate(180deg)",
  },
  enter: {
    transform: "rotate(180deg)",
  }
}

export interface RotateProps extends TransitionProps {
  children?: React.ReactElement<any>
}

export class Rotate extends React.Component<RotateProps> {
  render() {
    const {children, timeout, ...rest} = this.props
    return (
      <Transitioned timeout={timeout} {...rest}>
        {
          (state) => {
            const {type: Component, props} = children
            const style = {
              transitionDuration: `${timeout}ms`,
              ...styles.root,
              ...styles[state as keyof typeof styles],
              ...props.style
            }
            return <Component style={style} {...props}/>
          }
        }
      </Transitioned>
    );
  }
}
