import {DataClass, dataClass} from "data-class-copy"
import {Option} from "tsla-util/lib/option"
import {parseBool, parseString} from "tsla-util/lib/parse"
import {CreativeType} from "./creativeType"
import {Param, TPNParamInstance} from "./param"
import {ParamValue} from "./paramValue"
import {PlacementType} from "./placementType"
import {TPNIntegration, TPNIntegrationInstance} from "./tpnIntegration"

export interface TPNPlacementSchema {
  id: number | string
  tpnIntegrationId: number | string
  name: string
  placementType: string
  creativeType: number | string
  prog: boolean

  tpnParamId: number | string
  value: string
  enabled: boolean
  updatedAt: Date
}

export interface TPNPlacementInstance extends TPNPlacementSchema {
  param: TPNParamInstance
  tpnIntegration?: TPNIntegrationInstance
  count?: number
}

export interface TPNPlacementParams {
  id: string
  tpnIntegrationId: string
  name: string
  placementType: PlacementType
  creativeType: CreativeType
  prog: boolean
  paramValues: ParamValue[]
}

export interface TPNPlacement extends TPNPlacementParams, DataClass<TPNPlacementParams> {

}

@dataClass
export class TPNPlacement {
  static empty = new TPNPlacement({
    id: "",
    tpnIntegrationId: "",
    name: "",
    placementType: PlacementType.UNKNOWN,
    creativeType: CreativeType.unknown,
    prog: false,
    paramValues: [],
  })

  static parse(params: any = {}) {
    return TPNPlacement.empty.copy({
      id: parseString(params.id),
      tpnIntegrationId: parseString(params.tpnIntegrationId),
      name: parseString(params.name),
      placementType: PlacementType.parse(params.placementType),
      prog: parseBool(params.prog),
      creativeType: CreativeType.parse(params.creativeType),
      paramValues: (params.paramValues || []).map(ParamValue.parse)
    })
  }

  constructor(params: TPNPlacementParams) {
    Object.assign(this, params)
  }

  get toLite() {
    const retVal: Partial<TPNPlacementParams> = {
      id: this.id,
      tpnIntegrationId: this.tpnIntegrationId,
      name: this.name,
      placementType: this.placementType,
      creativeType: this.creativeType,
      prog: this.prog
    }

    if (this.paramValues.isNotEmpty) {
      retVal.paramValues = this.paramValues.map(e => e.toLite as ParamValue)
    }

    return retVal
  }

  toRecord(tpnIntegration: TPNIntegration) {
    const {value, tpnParamId} = this.paramValue.toRecord
    return {
      tpnIntegrationId: tpnIntegration.id,
      name: this.name,
      placementType: this.placementType.toRecord,
      creativeType: this.creativeType.toRecord,
      prog: tpnIntegration.isProgMediation,
      value,
      tpnParamId
    }
  }

  replaceParams(params: Param[]) {
    return this.mutate({
      paramValues: this.paramValues.map(e => e.replaceParam(params))
    })
  }

  get concatValues() {
    return this.paramValues.map(e => e.value).join("/")
  }

  get paramValue() {
    return Option.of(this.paramValues[0]).getOrElse(ParamValue.empty)
  }

  get stringifiedValue() {
    return this.paramValue.stringifiedValue
  }

  get hasUnsavedParam() {
    return this.paramValues.isEmpty || this.paramValues.some(e => !e.paramId)
  }

  get typeSupportedByParams() {
    return this.paramValues.every(
      e => e.param.supportsPlacementType(this.placementType)
    )
  }

  get creativeTypeSupportedByParams() {
    return this.paramValues.every(
      e => e.param.supportsCreativeType(this.creativeType)
    )
  }
}
