export function lazyVal(target: any, propertyKey: string, descriptor: PropertyDescriptor) {
  const mode = typeof descriptor.get === "function" ? "get" : "value"
  const original = descriptor[mode]

  const cacheKey = `$$${propertyKey}`

  function setLazyVal(this: any, ...args: any[]) {
    const scoped = original.apply(this, args)
    Object.defineProperty(this, cacheKey, {
      get() {
        return scoped
      }
    })
    return this[cacheKey]
  }

  descriptor[mode] = function (this: any, ...args: any[]) {
    return this.hasOwnProperty(cacheKey) ? this[cacheKey] : setLazyVal.apply(this, args)
  }
}
