import {Fun1} from "@revdesk/base-types"
import "rxjs/add/observable/of"
import "rxjs/add/observable/timer"
import "rxjs/add/operator/debounce"
import "rxjs/add/operator/switchMap"
import "rxjs/add/operator/takeUntil"
import "rxjs/add/operator/throttle"
import {Observable} from "rxjs/Observable"
import {Subscriber} from "rxjs/Subscriber"
import {defer, Defer} from "tsla-util/lib/promise/defer"
import {autoBind} from "./auto.bind"

export class Observe<T> {
  static simple<T>() {
    return new Observe<T>(
      obs => obs
    )
  }

  static throttle<T>(fn: Fun1<T>, ms = 250) {
    return new Observe<T>(
      obs => obs
        .throttle(() => Observable.timer(ms))
        .switchMap(open => Observable.of(open)),
      fn
    ).next
  }

  static debounce<T>(fn: Fun1<T>, ms = 250) {
    return new Observe<T>(
      obs => obs
        .debounce(() => Observable.timer(ms))
        .switchMap(open => Observable.of(open)),
      fn
    ).next
  }

  subscriber!: Subscriber<T>;
  setSubscriber = (subscriber: Subscriber<T>) => this.subscriber = subscriber
  obs: Observable<T>
  completer: Defer<T> | null = null

  constructor(fn: Fun1<Observable<any>, Observable<T>>, subscription?: Fun1<T>, onError?: Fun1<Error>) {
    this.obs = fn(Observable.create(this.setSubscriber))
    this.obs.subscribe({
      next: value => {
        subscription && subscription(value)
        if (this.completer != null) {
          this.completer.resolve(value)
        }
      },
      error: err => {
        onError && onError(err)
        if (this.completer != null) {
          this.completer.reject(err)
        }
      }
    })
  }

  @autoBind
  next(value?: any) {
    this.completer = defer()
    this.subscriber.next(value)
    return this.completer.promise
  }
}
