export type OrientationTypeValue = "landscape" | "portrait" | "user";

export interface OrientationType {
  id: OrientationTypeValue;
  name: string;
}

export  class OrientationType {

  static LANDSCAPE = new OrientationType({
    id: "landscape",
    name: "Landscape"
  });

  static PORTRAIT = new OrientationType({
    id: "portrait",
    name: "Portrait"
  });

  static USER = new OrientationType({
    id: "user",
    name: "Device orientation"
  });

  constructor({id, name}: OrientationType) {
    this.id = id
    this.name = name
  }
}
