import {DataClass, dataClass} from "data-class-copy"
import {parseBool, parseString} from "tsla-util/lib/parse"
import {CreativeType} from "./creativeType"
import {EntityLevel} from "./entityLevel"
import {ParamType, ValueType} from "./paramType"
import {PlacementType} from "./placementType"
import {TPNParamCreativeTypeInstance} from "./tpnParamCreativeType"
import {TPNParamEnumValueInstance} from "./tpnParamEnumValue"
import {TPNParamPlacementTypeInstance} from "./tpnParamPlacementType"

const optionalValidator = (val: any) => !val && "This field is required"

export interface TPNParamSchema {
  id: string | number
  networkId: string | number
  name: string
  key: string
  type: string | number
  levelId: string | number
  optional: boolean
  unique: boolean
  defaultValue: string | null
  enabled: boolean
}

export interface TPNParamInstance extends TPNParamSchema {
  tpnParamPlacementTypes?: TPNParamPlacementTypeInstance[]
  tpnParamCreativeTypes?: TPNParamCreativeTypeInstance[]
  tpnParamEnumValues?: TPNParamEnumValueInstance[]
}

export interface ParamParams<T extends ValueType = ValueType> {
  id: string
  key: string
  name: string
  type: ParamType<T>
  optional: boolean
  unique: boolean
  defaultValue: T
  creativeTypes: CreativeType[]
  placementTypes: PlacementType[]
  enumValues: string[]
  level: EntityLevel
  enabled: boolean
}

export interface Param<T extends ValueType = ValueType> extends ParamParams<T>, DataClass<ParamParams<T>> {

}

@dataClass
export class Param<T extends ValueType = ValueType> {
  static empty = new Param<ValueType>({
    id: "",
    key: "",
    name: "",
    type: ParamType.string,
    optional: false,
    unique: false,
    defaultValue: "",
    creativeTypes: [],
    placementTypes: [],
    enumValues: [],
    level: EntityLevel.unknown,
    enabled: true
  })

  static parse(params: any = {}) {
    const type = ParamType.parse(params.type)
    return Param.empty.copy({
      id: parseString(params.id),
      key: parseString(params.key),
      name: parseString(params.name),
      optional: parseBool(params.optional),
      unique: parseBool(params.unique),
      type,
      defaultValue: type.parse(params.defaultValue),
      creativeTypes: (params.creativeTypes || []).map(CreativeType.parse),
      placementTypes: (params.placementTypes || []).map(PlacementType.parse),
      enumValues: (params.enumValues || []).map(parseString),
      level: EntityLevel.parse(params.level || params.levelId),
      enabled: parseBool(params.enabled)
    })
  }

  constructor(params: ParamParams<T>) {
    Object.assign(this, params)
  }

  get validators() {
    return [
      ...this.optional ? [] : [optionalValidator],
      ...this.type.validators
    ]
  }

  supportsCreativeType(creativeType: CreativeType) {
    if (this.creativeTypes.isEmpty && creativeType.isUnknown) {
      // parameter is not linked to specific creative types, it means is supports the "empty" creative type
      return true
    }
    return this.creativeTypes.some(e => e.id === creativeType.id)
  }

  supportsPlacementType(placementType: PlacementType) {
    if (this.placementTypes.isEmpty && placementType.isUnknown) {
      // parameter is not linked to specific placement types, it means is supports the "empty" placement type
      return true
    }
    return this.placementTypes.some(e => e.id === placementType.id)
  }

  get isPlacementLevel() {
    return this.level.isPlacementLevel
  }

  get isAppLevel() {
    return this.level.isAppLevel
  }
}
