const rgx = /[A-Z]/g
const del = /-|_|\s+|\n+|\t+| +/
export function deliCase(str: string): string[] {
  return str.replace(/[^a-zA-Z0-9]/g, "_").replace(/[A-Z]/g, s => `_${s}`)
    .split(del)
    .filter((s) => !!s.length)
}

export function initials(str: string) {
  return deliCase(str).slice(0, 2).map(s => s.substr(0, 1).toUpperCase()).join("")
}
