import {lazyVal, NestedItem} from "@revdesk/util"
import {dataClass, DataClass} from "data-class-copy/lib"
import {applyMixins} from "ts-trait/build"
import {Option} from "tsla-util/lib/option"
import {DataTypes} from "../dataTypes"
import {CreativeType, PlacementType} from "../mediation"
import {RewardedConfig} from "../mediation/rewardedConfig"
import {Searchable} from "../searchable"
import {ConfigCreator} from "../spots/unit-config"
import {UnitFormat} from "../spots/unitFormat"
import {App} from "./app"
import {AppStatus} from "./appStatus"
import {Unit, UnitInstance} from "./unit"

export interface SpotSchema {
  id: string | number,
  name: string,
  appId: string | number,
  status: string
  placementType: string | number
}

export interface SpotInstance extends SpotSchema {
  app?: App | null
  units?: UnitInstance[]
}

export interface SpotParams {
  id: string;
  name: string;
  appId: string;
  app?: App | null;
  units: Unit[];
  placementType: PlacementType;
  creativeTypes: CreativeType[];
  status: AppStatus
  rewardedConfig: RewardedConfig
}

export interface Spot extends SpotParams, NestedItem<App, Unit>, DataClass<SpotParams> {

}

@dataClass
@applyMixins([NestedItem, Searchable])
export class Spot implements NestedItem<App, Unit> {
  static EMPTY = new Spot({
    id: "",
    name: "",
    appId: "",
    app: null,
    units: [],
    placementType: PlacementType.UNKNOWN,
    creativeTypes: [],
    status: AppStatus.UNKNOWN,
    rewardedConfig: RewardedConfig.empty
  })

  static parse(params: any = {}) {
    return Spot.EMPTY.copy({
      id: params.id != null ? String(params.id) : "",
      name: params.name != null ? String(params.name) : "",
      appId: params.appId != null ? String(params.appId) : "",
      units: (params.units || []).map(Unit.parse),
      app: (params.app || params.appSite) && App.parse(params.app || params.appSite),
      placementType: PlacementType.parse(params.placementType),
      creativeTypes: (params.creativeTypes || []).map(CreativeType.parse),
      status: AppStatus.parse(params.status),
      rewardedConfig: RewardedConfig.parse(params.rewardedConfig || {})
    })
  }

  constructor(params: SpotParams) {
    Object.assign(this, params)
  }

  get parent() {
    return this.app
  }

  get children() {
    return this.units || []
  }

  changeChildren(units: Array<Unit>) {
    return this.copy({units})
  }

  setParentOnChildren() {
    this.units = this.units.map(unit => unit.copy({spot: this}))
    return this
  }

  withoutChildren(): Spot {
    return this.copy({
      units: []
    })
  }

  get size() {
    const {units} = this
    return units.length
  }

  get type() {
    return "spot"
  }

  get hash() {
    return this.type + "_" + this.id
  }

  get lite() {
    return {
      spotId: this.id
    }
  }

  get dataType() {
    return DataTypes.SPOT
  }

  get publisherId() {
    return this.app && this.app.publisherId
  }

  @lazyVal
  get unitsMap() {
    return new Map<string, Unit>(this.units.map(unit => [unit.id, unit] as [string, Unit]))
  }

  get toUnit() {
    const format = UnitFormat.fromCreativeTypes(this.creativeTypes)
    const unitType = this.placementType.oldType(this.creativeTypes)
    return Unit.EMPTY.copy({
      name: this.name,
      fullName: this.name,
      spotId: this.id,
      unitType,
      format,
      config: ConfigCreator.createUnitDefaultConfig(format, unitType),
    })
  }

  get firstUnit() {
    return Option.of(this.units[0]).getOrElse(Unit.EMPTY)
  }

  get tpnConfigs() {
    return this.firstUnit.tpnConfigs
  }

  get hasActiveTPNConfig() {
    return this.tpnConfigs.some(e => e.status.isActive)
  }

  get emptyTPNConfig() {
    return this.tpnConfigs.isEmpty
  }

  get mediationConfig() {
    return this.firstUnit.mediationConfig
  }

  get floorPrices() {
    return Option.of(this.units[0].floorPrices).getOrElse([])
  }

  get firstUnitId() {
    return this.firstUnit.id
  }

  get isMREC() {
    return this.creativeTypes.some(e => e.isMREC)
  }

  get isRewarded() {
    return this.placementType === PlacementType.rewarded
  }
}
