import ButtonBase from "@material-ui/core/ButtonBase"
import {observer} from "mobx-react"
import * as React from "react"
import {Href} from "../../button"
import {Tile} from "../../components/tile"
import {Padding} from "../../layout"
import {FontSize} from "../../layout/fontSize"
import {RevDeskTypography} from "../revDeskTypography"
import {MenuItemBG} from "./menuItemBG"
import {MenuItemFade} from "./menuItemFade"
import {SideMenuActions} from "@revdesk/state-actions/build/side-menu/sideMenuActions"
import {SideMenuBox} from "@revdesk/state-actions/build/side-menu/sideMenuBox"
import {RenderIcon} from "./renderIcon"

export interface SideNavItemProps {
  actions: SideMenuActions
  value: SideMenuBox
  children?: React.ReactNode
  external?: boolean
}

@observer
export class MenuItem extends React.Component<SideNavItemProps> {
  state = {
    expanded: false
  }

  render() {
    const {value, actions, children, external} = this.props
    const {expanded} = actions.state
    const {icon} = value
    const {current} = actions.router.state
    const isActive = current.path === value.firstLevel.href ||
      value.secondLevel.some(e => e.href === current.path)
    const aInternal = <a target={external ? "_blank" : undefined}
                         href={external ? value.firstLevel.href : undefined}
                         style={{textDecoration: "none"}}>
      <Padding xsrl={16}>
        <ButtonBase component="div"
                    disableRipple
                    style={{
                      height: "48px",
                      width: "100%",
                      boxSizing: "border-box",
                      justifyContent: "flex-start"
                    }}>
          <Tile
            spaceBetween={8}
            avatar={
              <FontSize fontSize="24">
                <RenderIcon value={icon}/>
              </FontSize>
            }
            alignItems="center"
            header={
              expanded ? <RevDeskTypography variant="s15w400" color="white">
                {value.firstLevel.name}
              </RevDeskTypography> : undefined
            }
          />
        </ButtonBase>
      </Padding>
    </a>

    return <MenuItemBG isActive={isActive}>
      <MenuItemFade in={isActive}>
        <div>
          {
            external
              ? aInternal
              : <Href path={value.firstLevel.href}>{aInternal}</Href>
          }
        </div>
      </MenuItemFade>
    </MenuItemBG>
  }
}
