import {Fun1} from "@revdesk/base-types"

export async function promiseReduceWhile<T>(condition: Fun1<T, boolean>,
                                            next: Fun1<T, Promise<T>>,
                                            initialValue: T) {
  const results: T[] = []
  while (condition(initialValue)) {
    const result = await next(initialValue)
    results.push(result)
    initialValue = result
  }
  return results
}
