import {applyMixins} from "ts-trait/build"
import {Unit} from "../../inventory/unit"

import {BaseUnit, BaseUnitParams} from "./BaseUnit";

export interface NativeVideo extends BaseUnitParams {

}

@applyMixins([BaseUnit])
export class NativeVideo implements BaseUnitParams {
  static fromJS(baseUnitJS: BaseUnitParams = BaseUnit.DEFAULT_BASE_UNIT) {
    return new NativeVideo(baseUnitJS)
  }

  constructor(baseUnitJS: BaseUnitParams) {
    Object.assign(this, baseUnitJS)
  }

  toJS(unit: Unit) {
    return BaseUnit.prototype.toJS.call(this, unit)
  }

  toServerJS(unit: Unit): null {
    return null
  }
}
