import {stringMatch} from "@revdesk/util"
import {AccessLevel} from "./access.level"
import {Action} from "./action"
import {Permission} from "./permission"

export interface ListResourceParams {
  action: Action<string[]>
  accessLevel: AccessLevel
  newData: string[]
  defaultData: string[]
}

export interface ListResource extends ListResourceParams {

}

export class ListResource {
  constructor(params: ListResourceParams) {
    Object.assign(this, params)
  }

  removeFromResourceData(al: AccessLevel): Permission<string[]> | null {
    const {action, newData} = this
    const permission = action.getOrCreatePermission(al, this.defaultData)

    const resourceData = permission!.resourceData.filter(roleId => newData.indexOf(roleId) === -1)

    if (resourceData.isEmpty) {
      return null
    }

    return permission!.copy({
      resourceData
    })
  }

  removeFromResourceDataExclude(al: AccessLevel): Permission<string[]> | null {
    const {action, newData} = this
    const permission = action.getOrCreatePermission(al, this.defaultData)

    const resourceData = permission!.resourceData.filter(roleId => newData.indexOf(roleId) === -1)

    return permission!.copy({
      resourceData
    })
  }

  addToResourceData(al: AccessLevel): Permission<string[]> {
    const {action, newData} = this

    const permission = action.getOrCreatePermission(al, this.defaultData)

    const mergedData = [...permission!.resourceData, ...newData].distinctBy(roleId => roleId)
    return permission!.copy({
      resourceData: mergedData
    })
  }

  applyInclude() {
    const {action, accessLevel} = this
    return stringMatch({
      [AccessLevel.WRITE.id]: () => action.copy({
        read: this.addToResourceData(AccessLevel.READ),
        write: this.addToResourceData(AccessLevel.WRITE)
      }), // add to all
      [AccessLevel.READ.id]: () => action.copy({
        read: this.addToResourceData(AccessLevel.READ),
        write: this.removeFromResourceData(AccessLevel.WRITE)
      }), // add to read, remove from write
      [AccessLevel.LOCK.id]: () => action.copy({
        read: this.removeFromResourceData(AccessLevel.READ),
        write: this.removeFromResourceData(AccessLevel.WRITE)
      }), // remove from all
      Else: () => action
    })(accessLevel.id)
  }

  applyExclude() {
    const {action, accessLevel} = this
    return stringMatch({
      [AccessLevel.WRITE.id]: () => action.copy({
        read: this.removeFromResourceDataExclude(AccessLevel.READ),
        write: this.removeFromResourceDataExclude(AccessLevel.WRITE)
      }), // delete from all
      [AccessLevel.READ.id]: () => action.copy({
        read: this.removeFromResourceDataExclude(AccessLevel.READ),
        write: this.addToResourceData(AccessLevel.WRITE)
      }), // delete from read, add to write,
      [AccessLevel.LOCK.id]: () => action.copy({
        read: this.addToResourceData(AccessLevel.READ),
        write: this.addToResourceData(AccessLevel.WRITE)
      }), // add to all
      Else: () => action
    })(accessLevel.id)
  }
}
