import {EventEmitter} from "events"
import {action} from "mobx"
import {HasEmitter, hasEmitter} from "tsla-util/lib/has-emitter"
import {TabsState} from "./tabsState"

export interface TabsActionsParams<T> {
  state: TabsState<T>
}

export interface TabsActions<T> extends TabsActionsParams<T>, HasEmitter {
  on(type: "tab", fn: (state: TabsState<T>) => any): any
}

@hasEmitter
export class TabsActions<T> implements HasEmitter {
  emitter = new EventEmitter()

  constructor(params: TabsActionsParams<T>) {
    Object.assign(this, params)
  }

  @action changeTab(tab: number) {
    this.state.changeTab(tab)
    this.emit("tab", this.state)
  }

  @action changeValue(value: T) {
    this.state.changeValue(value)
    this.emit("tab", this.state)
  }
}
