import {dataClass, DataClass} from "data-class-copy"
import {action, observable} from "mobx"
import {parseString} from "tsla-util/lib/parse"

export interface RouteParams {
  path: string
  params: Record<string, string>
  hash: string
}

export interface Route extends RouteParams, DataClass<RouteParams> {

}

@dataClass
export class Route {
  static empty = () => new Route({
    path: "",
    params: {},
    hash: ""
  })

  static parse(params: any = {}) {
    return new Route({
      path: parseString(params.path),
      params: Object.assign({}, params.params),
      hash: parseString(params.hash)
    })
  }

  @observable hash!: string;
  @observable path!: string;
  @observable params!: Record<string, string>;

  constructor(params: RouteParams) {
    Object.assign(this, params)
  }

  @action pushHistory(params: Record<string, string>, inherit = true) {
    return this.mutate({
      params: inherit ? {
        ...this.params,
        ...params
      } : params
    })
  }

  @action navTo({path, params, hash}: RouteParams, inherit = false) {
    return this.mutate({
      path,
      hash,
      params: inherit ? {
        ...this.params,
        ...params
      } : params
    })
  }
}
