import {UserSession} from "@revdesk/model/build/userSession"
import {App} from "@revdesk/model/build/inventory"
import {DataClass, dataClass} from "data-class-copy"
import {action, observable} from "mobx"
import {pathToRoute} from "../app-route"
import {Route, RouteParams, RouterState} from "../router"
import {FetchState} from "../fetch-state"
import {lazyVal} from "@revdesk/util/build/lazy.val"
import {User} from "@revdesk/model/build/user"

export interface AppStateParams {
  userSession: UserSession
  menuApp: App,
  router: RouterState
  fetchState: FetchState
  err: string | null
  messages: string[]
}

export interface AppState extends AppStateParams, DataClass<AppStateParams> {

}

@dataClass
export class AppState {
  static empty = () => new AppState({
    userSession: UserSession.EMPTY,
    menuApp: App.EMPTY,
    router: RouterState.empty().mutate({
      routes: pathToRoute
    }),
    fetchState: FetchState.loading,
    err: null,
    messages: []
  })

  @observable fetchState!: FetchState
  @observable userSession!: UserSession
  @observable menuApp!: App
  @observable err!: string | null
  @observable messages!: string[]

  constructor(params: AppStateParams) {
    Object.assign(this, params)
  }

  @action changeFetchState(fetchState: FetchState) {
    return this.mutate({fetchState})
  }

  @action changeError(err: string | null) {
    return this.mutate({err})
  }

  @action changeUserSession(userSession: UserSession) {
    return this.mutate({userSession})
  }

  get isLoggedIn() {
    return this.userSession !== UserSession.EMPTY
  }

  get suAsPub() {
    return !!this.user.superUserId
  }

  @lazyVal
  get readUsers() {
    return !!this.user.permissions.user
  }

  get tipalti() {
    return this.user.permissions.tipalti != null
  }

  get tipaltiDetails() {
    return this.tipalti && this.user.permissions.tipalti!.write != null
  }

  get user(): User {
    return this.userSession.user
  }

  @action pushHistory(params: Record<string, string>, inherit = true) {
    return this.mutate({router: this.router.pushHistory(params, inherit)})
  }

  @action navTo({path, params, hash}: RouteParams, inherit = false) {
    return this.mutate({router: this.router.navTo({path, params, hash})})
  }

}
