import {TPNPlacement} from "@revdesk/model/build/mediation/tpnPlacement"
import {action, runInAction} from "mobx"
import {TPNClient} from "../api"
import {ToggleableFieldActions} from "../toggleable-field"
import {TPNPlacementState} from "../tpn-placement-form"
import {TPNPlacementItemState} from "./TPNPlacementItemState"

export interface TPNPlacementItemActionsParams {
  onPlacementSaved: (placement: TPNPlacement) => unknown
  state: TPNPlacementItemState
  tpnClient: TPNClient
}

export interface TPNPlacementItemActions extends TPNPlacementItemActionsParams {

}

export class TPNPlacementItemActions {

  toggleableField: ToggleableFieldActions

  constructor(params: TPNPlacementItemActionsParams) {
    this.state = params.state
    this.tpnClient = params.tpnClient
    this.onPlacementSaved = params.onPlacementSaved

    this.state.initValidators(
      (name) => this.tpnClient.uniquePlacementName(this.state.placement.copy({name}))
        .then(() => null)
        .catch(() => TPNPlacementState.messages.placementNameAlreadyExists)
    )

    this.toggleableField = new ToggleableFieldActions({
      state: this.state.toggleableField,
      onSubmit: this.submitChange,
      defaultValue: () => this.state.placement.name
    })
  }


  @action.bound
  async submitChange() {
    const {placement, toggleableField: {fieldState}} = this.state

    const placementToSave = placement.copy({name: fieldState.value})
    this.toggleableField.startSubmitting()

    const {hasError} = await fieldState.validate()

    runInAction(async () => {
      if (!hasError) {
        try {
          await this.tpnClient.updatePlacement(placementToSave)
          this.toggleableField.endEditing()
          this.onPlacementSaved(placementToSave)
        } catch (e) {
          console.error(e)
        }
      }

      this.toggleableField.endSubmitting()
    })
  }
}
