import {Theme} from "@material-ui/core/styles/createMuiTheme"
import withStyles from "@material-ui/core/styles/withStyles"
import * as React from "react"
import {Classes} from "../util"
import * as classNames from "classnames"

const styles = (theme: Theme) => ({
  darkBlue: {
    stroke: theme.palette.custom.darkBlue
  },
  green: {
    stroke: theme.palette.custom.green
  },
  grey: {
    stroke: theme.palette.custom.grey
  }
})

export type StrokeColor = keyof ReturnType<typeof styles>

export interface StrokeProps<P extends { className?: string }> {
  color: StrokeColor
  children: React.ReactElement<P>
  classes?: Classes<typeof styles>
  className?: string
}

export const Stroke = withStyles(styles)(
  function Stroke<P extends { className?: string }>({color, children, className, classes}: StrokeProps<P>) {
    const {type: WrappedComponent, props: wrappedProps} = children
    return <WrappedComponent
      {...wrappedProps}
      className={classNames(wrappedProps.className, className, classes![color])}/>
  }
)
