export type DistributorID = "ipad" | "iphone" | "androidTablet" | "androidPhone" | "wp" | "bb" | ""

export interface DistributorParams {
  id: DistributorID;
  name: string;
  values: string[];
}

export interface Distributor extends DistributorParams {

}

const valueToDistributor: Record<string, Distributor> = {}

export class Distributor {
  static fromValue(params: string) {
    return valueToDistributor[params] || Distributor.UNKNOWN
  }

  static UNKNOWN = new Distributor({
    id: "",
    name: "Other",
    values: []
  })

  static iPAD = new Distributor({
    id: "ipad",
    name: "iPad",
    values: ["946", "947"]
  })

  static iPhone = new Distributor({
    id: "iphone",
    name: "iPhone",
    values: ["642", "632"]
  })

  static ANDROID_TABLET = new Distributor({
    id: "androidTablet",
    name: "Android Tablet",
    values: ["958"]
  })

  static ANDROID_PHONE = new Distributor({
    id: "androidPhone",
    name: "Android Smartphone",
    values: ["559", "600"]
  })

  static WP = new Distributor({
    id: "wp",
    name: "Windows Phone",
    values: ["659", "778"]
  })

  static BB = new Distributor({
    id: "bb",
    name: "BlackBerry",
    values: ["634", "635"]
  })

  constructor(params: DistributorParams) {
    Object.assign(this, params)
    for (const value of params.values) {
      valueToDistributor[value] = this
    }
  }
}
