import {DataType, NestedItem} from "@revdesk/util"
import {tryGet} from "@revdesk/util"
import {DataClass, dataClass} from "data-class-copy/lib";
import {applyMixins} from "ts-trait/build"
import {DataTypes} from "./dataTypes"
import {Network} from "./demand/network"
import {GroupType} from "./groupType"
import {Publisher} from "./inventory/publisher"
import {Searchable} from "./searchable"

export interface GroupSchema {
  id: string | number
  name: string
  groupType: string
}

export interface GroupInstance extends GroupSchema {

}

export interface GroupParams {
  id: string;
  name: string;
  groupType: GroupType;
  adminId: string;
  publisher: Publisher | null;
  networks: Network[];
}

export interface Group extends GroupParams, DataClass<Group>, NestedItem<any, any> {

}

@applyMixins([NestedItem, Searchable])
@dataClass
export class Group implements NestedItem<any, any> {
  static EMPTY = new Group({
    id: "",
    name: "",
    groupType: GroupType.UNKNOWN,
    adminId: "",
    publisher: null,
    networks: []
  });

  static parse(params: any = {}) {
    return Group.EMPTY.copy({
      groupType: GroupType.parse(params.groupType),
      id: params.id ? String(params.id) : "",
      name: params.name ? String(params.name) : "",
      adminId: params.adminId != null ? String(params.adminId) : "",
      publisher: params.publisher != null ? Publisher.parse(params.publisher) : null,
      networks: (params.networks || []).map(Network.parse)
    })
  }

  constructor(params: GroupParams) {
    Object.assign(this, params)
  }

  get dataType() {
    return DataTypes.GROUP
  }

  static searchQuery(term: string | null) {
    return term && {
      name: {
        $like: `%${term}%`
      }
    }
  }

  get publisherId() {
    return tryGet(() => this.publisher!.id, "")
  }

  toRecord() {
    return {
      id: this.id,
      name: this.name,
      groupType: this.groupType.id,
      adminId: this.adminId
    }
  }

  get internal() {
    return this.groupType.internal
  }

  get external() {
    return this.groupType.external
  }

  get isPublisher() {
    return this.groupType.isPublisher
  }
}
