import {dataClass, DataClass} from "data-class-copy"
import {parseString} from "tsla-util/lib/parse"
import {observable} from "mobx"

export type Direction = "asc" | "desc"

export interface SortParams {
  name: string
  direction: Direction
}

export interface Sort extends SortParams, DataClass<Sort> {

}

@dataClass
export class Sort {
  static parse(filter: any = {}) {
    return new Sort({
      name: parseString(filter.name),
      direction: filter.direction === "desc" ? "desc" : "asc"
    })
  }

  @observable direction!: Direction

  constructor(params: SortParams) {
    Object.assign(this, params)
  }

  toggleDirection() {
    const {direction} = this
    return this.mutate({
      direction: direction === "asc" ? "desc" : "asc"
    })
  }
}
