import {lazyVal} from "@revdesk/util/build/lazy.val"
import {dataClass, DataClass} from "data-class-copy"
import {Option} from "tsla-util/lib/option"
import {Publisher} from "./inventory"
import {User} from "./user"

export interface UserSessionParams {
  user: User
  prevUser?: User | null
}

export interface UserSession extends UserSessionParams, DataClass<UserSessionParams> {

}

@dataClass
export class UserSession {
  constructor(params: UserSessionParams) {
    Object.assign(this, params)
  }

  static EMPTY = new UserSession({
    user: User.EMPTY,
    prevUser: null
  })

  static parse(params: any = {}) {
    return UserSession.EMPTY.copy({
      user: User.parse(params.user),
      prevUser: params.prevUser != null ? User.parse(params.prevUser) : null
    })
  }

  get canSessionSaveInventory() {
    return this.user.canSaveInventory && ((this.prevUser == null) || this.prevUser.canSaveInventory)
  }

  get canSessionSaveGDPR() {
    return this.canSessionViewInventory && this.prevUser != null && this.prevUser.canSaveGDPR
  }

  get canSessionViewInventory() {
    return this.user.canViewInventory && ((this.prevUser == null) || this.prevUser.canViewInventory)
  }

  get canSessionViewDeals() {
    return this.user.canViewDeals && ((this.prevUser == null) || this.prevUser.canViewDeals)
  }

  get canSessionSaveDeals() {
    return this.user.canSaveDeals && ((this.prevUser == null) || this.prevUser.canSaveDeals)
  }

  get canViewFinance() {
    return this.user.canViewFinance
  }

  get canSaveFinance() {
    return this.prevUser == null && this.user.canSaveFinance
  }

  get canSessionViewAudience() {
    return this.user.canViewAudience
  }

  @lazyVal
  get canSaveRoles() {
    return this.user.canSaveRoles
  }

  @lazyVal
  get canViewRoles() {
    return this.user.canViewRoles
  }

  get canSaveUsers() {
    return this.user.canSaveUsers
  }

  get canUpdateBiddingType() {
    return true // https://tickets.fyber.com/browse/RD-1841
  }

  changeHasApps(hasApps: boolean) {
    return this.copy({
      user: this.user.copy({hasApps})
    })
  }

  get canSaveAppText() {
    return this.user.canSaveAppText || (this.prevUser != null && this.prevUser.canSaveAppText)
  }

  asPivotUser(superUserPublishers: Publisher[]) {
    const {canSaveAppText} = this
    const {id, highestRole: role, fullName, email, publisherId, group} = this.user
    return {
      id,
      role,
      highestRole: role,
      name: fullName,
      email,
      publisherId,
      networkId: group.networks.map(e => e.id),
      superUserPublishers: superUserPublishers.map(p => p.name),
      canSaveAppText,
      prevUser: Option.of(this.prevUser).map(e => ({
        id: e.id,
        email: e.email,
        role: e.highestRole
      })).get
    }
  }
}
