export type UserStatusID =
  "ACTIVATED"
  | "PREACTIVATED"
  | "DISACTIVATED"
  | "MORE_INFO"
  | "DISACTIVATED_FRAUD"
  | "FRAUD_SUSPECTED"
  | string

export interface UserStatusParams {
  id: UserStatusID;
  name: string;
  boolVal?: boolean;
  color?: string;
  adj: string;
}

export interface UserStatus extends UserStatusParams {

}

const values: Record<UserStatusID, UserStatus> = {} as any

export class UserStatus {
  static UNKNWON = new UserStatus({
    id: "",
    name: "Unknown",
    adj: ""
  })

  static PREACTIVATED = new UserStatus({
    id: "PREACTIVATED",
    name: "Pre-activated",
    adj: "Deactivated"
  })

  static DISACTIVATED = new UserStatus({
    id: "DISACTIVATED",
    name: "Dis-activated",
    adj: "Deactivated"
  })

  static ACTIVATED = new UserStatus({
    id: "ACTIVATED",
    name: "Active",
    boolVal: true,
    color: "#00d691",
    adj: "Active"
  })

  static MORE_INFO = new UserStatus({
    id: "MORE_INFO",
    name: "More Info",
    adj: "Deactivated"
  })

  static FRAUD_SUSPECTED = new UserStatus({
    id: "FRAUD_SUSPECTED",
    name: "Fraud Suspected",
    adj: "Deactivated"
  })

  static DISACTIVATED_FRAUD = new UserStatus({
    id: "DISACTIVATED_FRAUD",
    name: "Disactivated Fraud",
    adj: "Deactivated"
  })

  static parse(params: string) {
    return values[params] || UserStatus.UNKNWON
  }

  constructor(params: UserStatusParams) {
    Object.assign(this, params)
    this.boolVal = params.boolVal || false
    this.color = params.color || "#f8e71c"
    values[params.id] = this
  }

  toJSON() {
    return this.id
  }

  toggle() {
    return this === UserStatus.ACTIVATED ? UserStatus.DISACTIVATED : UserStatus.ACTIVATED
  }

  get isActive() {
    return this.id === UserStatus.ACTIVATED.id
  }

  get isUnknown() {
    return !this.id
  }
}
