import {dataClass, DataClass} from "data-class-copy"

export interface CountrySchema {
  code: string
  country: string
}

export interface CountryParams {
  code: string
  country: string
}

export interface Country extends CountryParams, DataClass<Country> {

}

@dataClass
export class Country {
  static nonCountryValues = ["A1", "A2", "AP", "EU", "ZZ", "YU", "FX", "ZZ"]

  static EMPTY = new Country({
    code: "",
    country: ""
  })

  static WW = new Country({
    code: "WW",
    country: "Worldwide"
  })

  static parse(params: any = {}) {
    return Country.EMPTY.copy({
      code: params.code != null ? String(params.code) : "",
      country: params.country != null ? String(params.country) : "",
    })
  }

  constructor(params: CountryParams) {
    Object.assign(this, params)
  }

  get id() {
    return this.code
  }

  get name() {
    return this.country
  }
}
