import {Distributor} from "./distributor"

const values: Platform[] = []
const selectableValues: Platform[] = []
const byValue: Record<string, Platform> = {}
const storeTypeToPlatform: Record<string, Platform> = {}

export type PlatformValue = "ios" | "android" | "mw";

export interface PlatformParams {
  id?: PlatformValue,
  value?: PlatformValue,
  name?: string,
  storeType: string,
  dbKey?: string,
  bundleLabel?: string,
  native: boolean,
  storeUrlLabel: string,
  icon: string,
  distributor: Distributor
  storeName: string
}

export interface Platform extends PlatformParams {
}

export class Platform {

  static fromJS(value: PlatformValue): Platform {
    return byValue[value]
  }

  static fromStoreType(value: string): Platform {
    return storeTypeToPlatform[value] || Platform.UNKNOWN
  }

  static iOS = new Platform({
    value: "ios",
    name: "iOS",
    storeType: "apple",
    dbKey: "iPhone",
    bundleLabel: "Bundle ID",
    native: true,
    storeUrlLabel: "iTunes URL",
    icon: "fa-apple",
    distributor: Distributor.iPhone,
    storeName: "App Store"
  })

  static ANDROID = new Platform({
    value: "android",
    name: "Android",
    storeType: "google",
    dbKey: "Android",
    bundleLabel: "Package Name",
    native: true,
    storeUrlLabel: "Google Play URL",
    icon: "fa-android",
    distributor: Distributor.ANDROID_PHONE,
    storeName: "Google Play"
  })

  static MOBILE_WEB = new Platform({
    value: "mw",
    name: "Mobile Web",
    storeType: "mw",
    dbKey: "other",
    storeUrlLabel: "URL",
    bundleLabel: "URL",
    native: false,
    icon: "fa-globe",
    distributor: Distributor.UNKNOWN,
    storeName: "Mobile Web"
  })

  static UNKNOWN = new Platform({
    value: null,
    name: "Unknown",
    storeType: "",
    dbKey: "other",
    storeUrlLabel: "URL",
    bundleLabel: "URL",
    native: false,
    icon: "",
    distributor: Distributor.UNKNOWN,
    storeName: ""
  })

  static byValue = byValue
  static values = values
  static selectableValues = selectableValues

  constructor({
                value, name, storeType, dbKey, bundleLabel, native, storeUrlLabel, icon,
                distributor, storeName,
              }: PlatformParams) {
    this.id = value
    this.value = value
    this.name = name
    this.storeType = storeType
    this.dbKey = dbKey
    this.bundleLabel = bundleLabel
    this.native = native
    this.storeUrlLabel = storeUrlLabel
    this.icon = icon
    this.distributor = distributor
    this.storeName = storeName
    values.push(this)
    if (value != null) {
      selectableValues.push(this)
    }
    byValue[value] = this
    storeTypeToPlatform[storeType] = this
  }

  googleOrApple() {
    return this.storeType === "apple" || this.storeType === "google"
  }

  toJSON() {
    return this.id
  }

  get documentationURL() {
    return `https://dev-${this.value}.fyber.com/docs`
  }
}
