import {DataClass, dataClass} from "data-class-copy"
import {parseString} from "tsla-util/lib/parse"

export interface AdUnitNetworkParams {
  id: string
}

export interface AdUnitNetwork extends AdUnitNetworkParams, DataClass<AdUnitNetworkParams> {

}

@dataClass
export class AdUnitNetwork {
  static empty = new AdUnitNetwork({
    id: ""
  })

  static fromDB(params: any) {
    return AdUnitNetwork.empty.copy({
      id: parseString(params.id),
    })
  }

  constructor(params: AdUnitNetworkParams) {
    Object.assign(this, params)
  }
}

