export type RevDeskTypographyVariant = keyof ReturnType<typeof variant>
export type RevDeskTypographyColor = keyof ReturnType<typeof colorStyles>

export interface RevDeskTypographyProps extends React.AnchorHTMLAttributes<HTMLAnchorElement> {
  variant: keyof ReturnType<typeof variant>
  classes?: any
  color?: RevDeskTypographyColor
  decoration?: keyof typeof decoration
  line?: keyof ReturnType<typeof lineHeight>
  spacing?: keyof typeof spacing
  href?: string
}

const combineStyle = (theme: Theme) => ({
  ...styles,
  ...variant(theme),
  ...lineHeight(theme),
  ...colorStyles(theme),
  ...spacing,
  ...decoration
})


export const RevDeskTypography: React.FunctionComponent<RevDeskTypographyProps> =
  (withStyles(combineStyle, {name: "RevDeskTypography", index: 1}) as any)(
    function RevDeskTypography(props: RevDeskTypographyProps) {
      const {
        classes, variant, color = "textPrimary", className: classNameProp, children, spacing,
        href, line, decoration, ...rest
      } = props
      const className = classNames(
        classes!.root,
        classes[variant],
        classes[color],
        classes[spacing!],
        classes[line!],
        classes[decoration!],
        classNameProp,
      )

      const Component = href ? "a" : "span"
      const anchorProps = href ? {href} : {}
      return <Component {...anchorProps} {...rest} className={className}>
        {children}
      </Component>
    }
  )
