import {dataClass, DataClass} from "data-class-copy/lib"
import {SideMenuBox} from "./sideMenuBox"
import {SideMenuObject} from "./sideMenuObject"
import {observable, action} from "mobx"

export interface SideMenuStateParams {
  expanded: boolean
  apps: SideMenuBox
  app: SideMenuBox
  addApp: SideMenuBox
  monetize: SideMenuBox
  audience: SideMenuBox
  documentation: SideMenuBox
  reports: SideMenuBox
  finance: SideMenuBox
  blockingControls: SideMenuObject
  users: SideMenuObject
  overview: SideMenuObject
  programmatic: SideMenuObject
  bids: SideMenuObject
  placements: SideMenuObject
  demandSource: SideMenuObject
  financeDetails: SideMenuObject
  financeInvoices: SideMenuObject
  financeHistory: SideMenuObject
}

export interface SideMenuState extends SideMenuStateParams, DataClass<SideMenuState> {

}

@dataClass
export class SideMenuState {
  static empty = () => {

    const blockingControls = new SideMenuObject({
      id: "blockingControls",
      name: "Blocking Controls",
      href: "/blocking-controls",
    })

    const users = new SideMenuObject({
      id: "users",
      name: "Users",
      href: "/users",
    })

    const overview = new SideMenuObject({
      id: "overview",
      name: "Mediation Overview",
      href: "/reports/overview",
    })

    const programmatic = new SideMenuObject({
      id: "programmatic",
      name: "Programmatic",
      href: "/reports/programmatic",
    })

    const bids = new SideMenuObject({
      id: "bids",
      name: "Buyer's Report",
      href: "/reports/bids",
    })

    const placements = new SideMenuObject({
      id: "placements",
      name: "Placements",
      href: "/placements",
    })

    const demandSource = new SideMenuObject({
      id: "demandSource",
      name: "Demand Sources",
      href: "/demand-source",
    })

    const financeDetails = new SideMenuObject({
      id: "financeDetails",
      name: "Payment Details",
      href: "/finance/details",
    })

    const financeInvoices = new SideMenuObject({
      id: "financeInvoices",
      name: "Invoices",
      href: "/finance/invoices",
    })

    const financeHistory = new SideMenuObject({
      id: "financeHistory",
      name: "Payment History",
      href: "/finance/history",
    })


    return new SideMenuState({
      expanded: true,
      apps: new SideMenuBox({
        id: "apps",
        firstLevel: new SideMenuObject({
          id: "apps",
          name: "Apps",
          href: "/apps",
        }),
        secondLevel: [],
        icon: "AppsIcon",
      }),
      app: new SideMenuBox({
        id: "app",
        firstLevel: new SideMenuObject({
          id: "app",
          name: "App",
          href: "/app",
        }),
        secondLevel: [
          placements,
          demandSource
        ],
        icon: "AppsIcon",
      }),
      addApp: new SideMenuBox({
        id: "addApp",
        firstLevel: new SideMenuObject({
          id: "addApp",
          name: "Add App",
          href: "/newApp",
        }),
        icon: "AppsIcon",
        secondLevel: [],
      }),
      monetize: new SideMenuBox({
        id: "monetize",
        firstLevel: new SideMenuObject({
          id: "monetize",
          name: "Monetize",
          href: "",
        }),
        icon: "MonetizeIcon",
        secondLevel: [
          blockingControls,
          users
        ],
      }),
      audience: new SideMenuBox({
        id: "audience",
        firstLevel: new SideMenuObject({
          id: "audience",
          name: "Audience Vault",
          href: "/audience",
        }),
        icon: "AudienceIcon",
        secondLevel: [],
      }),
      reports: new SideMenuBox({
        id: "reports",
        firstLevel: new SideMenuObject({
          id: "reports",
          name: "Dynamic Reports",
          href: "",
        }),
        icon: "DynamicReportsIcon",
        secondLevel: [
          overview,
          programmatic,
          bids
        ],
      }),
      finance: new SideMenuBox({
        id: "finance",
        firstLevel: new SideMenuObject({
          id: "finance",
          name: "Finance",
          href: "",
        }),
        icon: "FinanceIcon",
        secondLevel: [
          financeDetails,
          financeInvoices,
          financeHistory
        ],
      }),
      documentation: new SideMenuBox({
        id: "documentation",
        firstLevel: new SideMenuObject({
          id: "documentation",
          name: "Documentation",
          href: "https://developer.fyber.com/fairbid2/",
        }),
        icon: "DocumentationIcon",
        secondLevel: [],
      }),
      blockingControls,
      users,
      overview,
      programmatic,
      bids,
      placements,
      demandSource,
      financeDetails,
      financeInvoices,
      financeHistory
    })
  }

  @observable expanded!: boolean

  constructor(params: SideMenuStateParams) {
    Object.assign(this, params)
  }

  @action.bound expandCollapseMenu(expanded = !this.expanded) {
    return this.mutate({expanded})
  }
}
