export interface DataTypeParams<T=any> {
  id: string;
  name: string;
}

export interface DataType<T=any> extends DataTypeParams<T> {

}

export class DataType<T=any> {
  static ANY = new DataType({
    id: "any",
    name: "",
  })

  constructor(params: DataTypeParams<T>) {
    this.id = params.id
    this.name = params.name
  }

  toJSON() {
    return this.id
  }
}
