import Grid, {GridProps} from "@material-ui/core/Grid"
import * as React from "react"
import {Margin, MarginProps} from "../layout"
import {Size} from "../layout/size"

export interface TileProps extends GridProps {
  avatar?: React.ReactNode
  header?: React.ReactNode
  subHeader?: React.ReactNode
  trailing?: React.ReactNode
  trailingProps?: GridProps
  spaceBetween?: Size
  trailingMarginProps?: Partial<MarginProps<GridProps>>
}

export function Tile(props: TileProps) {
  const {
    header, subHeader, spaceBetween = 16, avatar, trailing, trailingProps,
    trailingMarginProps, ...rest
  } = props
  return <Grid container direction="row" wrap="nowrap" alignItems="center" {...rest}>
    {avatar && <Margin xsr={spaceBetween}>
      <Grid item>
        {avatar}
      </Grid>
    </Margin>}

    <Grid item zeroMinWidth container style={{flex: 1}} direction="column">
      {header && <Grid item container>
        {header}
      </Grid>}
      {subHeader && <Grid item container>
        {subHeader}
      </Grid>}
    </Grid>

    {trailing && <Margin xsl="auto" {...trailingMarginProps}>
      <Grid item {...trailingProps}>
        {trailing}
      </Grid>
    </Margin>}
  </Grid>
}
