import {DisplayUnit, DisplayUnitJS} from "./DisplayUnit";
import {VideoUnit, VideoUnitParams} from "./VideoUnit";
import {UnitType} from "../unitType";
import {Unit} from "../../inventory/unit";
import {BaseUnit, BaseUnitParams} from "./BaseUnit";
import {applyMixins} from "ts-trait/build"

export interface DisplayAndVideoJS extends DisplayUnitJS, VideoUnitParams, BaseUnitParams {

}

export interface DisplayAndVideo extends BaseUnit, DisplayUnit, VideoUnit {

}

@applyMixins([BaseUnit, DisplayUnit, VideoUnit])
export class DisplayAndVideo implements DisplayAndVideo, BaseUnit, DisplayUnit, VideoUnit {

  static DEFAULT_DISPLAY_AND_VIDEO: DisplayAndVideoJS = {
    ...DisplayUnit.DEFAULT_DISPLAY_UNIT,
    ...VideoUnit.DEFAULT_VIDEO_UNIT,
  }

  static defaultByUnit = (unitType: UnitType) => {
    return {
      ...DisplayUnit.defaultByType(unitType),
      ...VideoUnit.defaultByUnit(unitType)
    }
  }

  static fromJS(displayAndVideoJS: DisplayAndVideoJS = DisplayAndVideo.DEFAULT_DISPLAY_AND_VIDEO) {
    return new DisplayAndVideo(displayAndVideoJS)
  }

  constructor(params: DisplayAndVideoJS) {
    DisplayUnit.prototype.constructor.call(this, params)
    VideoUnit.prototype.constructor.call(this, params)
    this.pos = 7
    this.filteredAPIs = this.filteredAPIs && !this.filteredAPIs.length ?
      DisplayAndVideo.DEFAULT_DISPLAY_AND_VIDEO.filteredAPIs : this.filteredAPIs
  }

  toJS(unit: Unit) {
    return {
      ...DisplayUnit.prototype.toJS.call(this, unit),
      ...VideoUnit.prototype.toJS.call(this, unit)
    }
  }

  toServerJS(unit: Unit) {
    return {
      ...DisplayUnit.prototype.toServerJS.call(this, unit),
      ...VideoUnit.prototype.toServerJS.call(this, unit)
    }
  }
}
