import {FieldState} from "formstate/lib/core/fieldState"
import {applyValidators, Validator} from "formstate/lib/core/types"
import {runInAction} from "mobx"
import {extension} from "ts-trait/build/extension"

declare module "formstate/lib/core/types" {
  interface ComposibleValidatable<TValue> {
    validateIsolate(): Promise<string | null | undefined>
  }
}

declare module "formstate/lib/core/fieldState" {
  interface FieldState<TValue> extends ValidateIsolateFieldState<TValue> {
  }
}

interface FieldStatePrivate<T> {
  _validators: Validator<T>[]
}

@extension([FieldState])
class ValidateIsolateFieldState<T> {
  async validateIsolate(this: FieldState<T>) {
    const value = this.value
    const error = await applyValidators(value, (this as any as FieldStatePrivate<T>)._validators || [])
    if (value === this.value) {
      runInAction(() => {
        this.error = error ? error : undefined
        if (!error) {
          this.$ = value
        }
      })
    }
    return error
  }
}
