import {Param, PlacementType, TPNIntegration} from "@revdesk/model/build/mediation"
import {EventEmitter} from "events"
import {action} from "mobx"
import {HasEmitter} from "tsla-util/lib/has-emitter"
import {TPNClient} from "../api"
import {ParamValueFieldState} from "../param-value-form"
import {RouterActions} from "../router"
import {CommonValidators} from "../validators/commonValidators"
import {PlacementValidator, TPNPlacementFormState} from "./tpnPlacementFormState"
import {TPNPlacementState} from "./tpnPlacementState"

export interface TPNPlacementActionsParams {
  tpnClient: TPNClient
  router: RouterActions
  state: TPNPlacementState
}

export interface TPNPlacementActions extends TPNPlacementActionsParams, HasEmitter {
}


export class TPNPlacementActions implements HasEmitter {

  emitter = new EventEmitter()

  // https://tickets.fyber.com/browse/RD-1546
  skipValidationOtherPlacementNameValid = (validator: PlacementValidator): PlacementValidator => (
    form => (
      this.state.isOtherPlacementValid.form(form, validator) ? null : validator(form)
    )
  )

  validators = [
    (field: ParamValueFieldState) => field.paramValue.param.validators.map(
      validator => (
        this.state.isOtherPlacementValid.field(field, validator) ? null : validator(field.value)
      )
    ),
    CommonValidators.skipValidationIfEmpty(
      (field: ParamValueFieldState) => this.state.form.$.flatMap(e => e.$.paramValues.$).some(
        e => {
          return e.paramValue.paramId !== field.paramValue.paramId && e.value === field.value
        }
      ) && TPNPlacementState.messages.valueAlreadyExists,
      field => field.stringifiedValue
    ),
    CommonValidators.skipValidationIfEmpty(
      async (field: ParamValueFieldState) => this.tpnClient.uniquePlacementParamValue(
        field.toParamValue.mutate({tpnIntegrationId: this.state.tpnIntegrationId})
      ).then(() => null).catch(() => TPNPlacementState.messages.valueAlreadyExists),
      field => field.stringifiedValue
    )
  ]

  placementValidators = [
    this.skipValidationOtherPlacementNameValid(
      form => CommonValidators.nonEmpty(form.$.placementName.value)
    ),
    CommonValidators.skipValidationIfEmpty(
      (form: TPNPlacementFormState) => this.state.form.$.some(
        e => {
          return e.placement.creativeType !== form.placement.creativeType && e.$.placementName.value === form.$.placementName.value
        }
      ) && TPNPlacementState.messages.placementNameAlreadyExists,
      form => form.$.placementName.value
    ),
    CommonValidators.skipValidationIfEmpty(
      async (form: TPNPlacementFormState) => this.tpnClient.uniquePlacementName(
        form.toPlacement.mutate({tpnIntegrationId: this.state.tpnIntegrationId})
      ).then(() => null).catch(() => TPNPlacementState.messages.placementNameAlreadyExists),
      form => form.$.placementName.value
    )
  ]

  constructor(params: TPNPlacementActionsParams) {
    Object.assign(this, params)
  }

  @action
  changeParams(placementType: PlacementType, params: Param[], tpnIntegration: TPNIntegration) {
    this.state.mutate({
      tpnIntegrationId: tpnIntegration.id,
      placementType,
      form: TPNPlacementState.createCreativeTypeForms(
        params,
        placementType,
        this.validators,
        this.placementValidators
      ),
    })
  }

  validate() {
    return this.state.form.validate()
  }

  clearForm() {
    this.state.form.$.forEach(e => e.clear())
  }

  @action
  handlePlacementNameChange(form: TPNPlacementFormState) {
    this.state.form.$.forEach(
      e => {
        const {placementName} = e.$
        if (e !== form) {
          placementName.validateStream()
          e.$.paramValues.$.forEach(
            e => {
              e.validateStream()
            }
          )
        }
      }
    )
  }

  @action
  handleParamValueChange(form: TPNPlacementFormState) {
    this.state.form.$.forEach(
      e => {
        if (e !== form) {
          e.$.placementName.validateStream()
          e.$.paramValues.$.forEach(
            e => {
              e.validateStream()
            }
          )
        }
      }
    )
  }
}
