import {lazyVal} from "@revdesk/util"
import {dataClass, DataClass} from "data-class-copy/lib"
import {Map} from "immutable"
import {FormControl} from "./formControl"

export interface FormStateParams {
  controls: Map<string, FormControl>;
}

export interface FormState extends FormStateParams, DataClass<FormState> {

}

@dataClass
export class FormState {
  static EMPTY = new FormState({
    controls: Map()
  })

  static create(params: Record<string, FormControl>) {
    return new FormState({
      controls: Map(params)
    })
  }

  constructor(params: FormStateParams) {
    this.controls = params.controls
  }

  changeControl(name: string, control: FormControl) {
    return this.copy({
      controls: this.controls.set(name, control)
    })
  }

  changeControls(controls: Record<string, Partial<FormControl>>) {
    return this.copy({
      controls: this.controls.map(
        (control: FormControl) => control.copy(controls[control.name])
      ).toMap()
    })
  }

  getControl(name: string) {
    return this.controls.get(name)
  }

  @lazyVal
  get valid(): boolean {
    return this.controls.every(control => control.valid)
  }

  changeSubmitted(): FormState {
    return this.copy({
      controls: this.controls.map(control => control.changeSubmitted()).toMap()
    })
  }
}
