import {FormState} from "./formState"

export type ValidationFn<T> = (value: T, formControl?: FormState) => Promise<boolean> | boolean;

export class ValidationError {
  static NONE = new ValidationError("", null)

  constructor(public message: string, public validator: Validator<any>, public err?: Error) {
  }
}

export interface ValidatorParams<T> {
  fn: ValidationFn<T>;
  message: string;
  name: string;
}

export interface Validator<T> extends ValidatorParams<T> {

}

export class Validator<T> {
  constructor(params: ValidatorParams<T>) {
    Object.assign(this, params)
  }

  async validate(value: T, formControl?: FormState) {
    try {
      const result = await Promise.resolve(this.fn(value, formControl))
      if (!result) {
        return new ValidationError(this.message, this)
      }
    } catch (err) {
      return new ValidationError(this.message, this)
    }
    return null
  }
}
