import {dataClass, DataClass} from "data-class-copy/lib"
import {ValidationError, Validator} from "./validator"

export type FormControlStatus = "valid" | "invalid" | "pending" | "disabled"

export interface FormControlParams<T=any> {
  name: string;
  pristine: boolean;
  touched: boolean;
  focused: boolean;
  pendingValue: T;
  value: T;
  status: FormControlStatus;
  errors: ValidationError[];
}

export interface FormControl<T=any> extends FormControlParams<T>, DataClass<FormControl<T>> {

}

@dataClass
export class FormControl<T=any> {
  static EMPTY = new FormControl({
    name: "",
    pristine: true,
    touched: false,
    focused: false,
    pendingValue: null,
    value: null,
    status: "valid",
    errors: []
  })

  static create<T>(value: T, validators: Validator<any>[] = []): FormControl<T> {
    return FormControl.EMPTY.copy({value, pendingValue: value})
  }

  constructor(params: FormControlParams<T>) {
    Object.assign(this, params)
  }

  get dirty() {
    return !this.pristine
  }

  get untouched() {
    return !this.touched
  }

  get valid() {
    return this.status === "valid"
  }

  get invalid() {
    return this.status !== "valid"
  }

  get hasError() {
    return !!this.errors.length
  }

  get firstError() {
    return this.errors[0] || ValidationError.NONE
  }

  changePendingValue(pendingValue: T) {
    this.pendingValue = pendingValue
    this.status = "pending"
    this.pristine = false
    this.errors = []
    return this
  }

  persistPendingValue() {
    return this.copy({
      status: "valid",
      touched: true,
      focused: false,
      errors: [],
      value: this.pendingValue
    })
  }

  changeErrors(errors: ValidationError[]) {
    return this.copy({
      status: errors.length ? "invalid" : "valid",
      errors
    })
  }

  changeFocus() {
    return this.copy({
      touched: true,
      focused: true
    })
  }

  changeSubmitted() {
    return this.copy({
      touched: true
    })
  }

  get diff() {
    return this.value !== this.pendingValue
  }
}
