import {dataClass, DataClass} from "data-class-copy"
import {action, observable} from "mobx"
import {parseNumber} from "tsla-util/lib/parse"
import {Entity} from "./entity"

export interface QueryResultParams<T extends Entity> {
  records: T[]
  total: number
}

export interface QueryResult<T extends Entity> extends QueryResultParams<T>, DataClass<QueryResult<any>> {
  copy<U extends Entity>(newValue?: Partial<QueryResult<U>>): this;
}

@dataClass
export class QueryResult<T extends Entity> {
  static empty = <T extends Entity>() => new QueryResult<T>({
    records: [],
    total: 0
  })

  static fromArray = <T extends Entity>(arr: T[]) => new QueryResult<T>({
    records: arr,
    total: arr.length
  })

  static parse<T extends Entity>(params: any, mapper: (e: any) => T): QueryResult<T> {
    return new QueryResult({
      total: parseNumber(params.total),
      records: (params.records || []).map(mapper)
    })
  }

  @observable records!: Array<T>
  @observable total!: number

  constructor(params: QueryResultParams<T>) {
    Object.assign(this, params)
  }

  @action addAll(other: QueryResult<T>) {
    return this.mutate({
      total: this.total + other.total,
      records: [...this.records, ...other.records]
    })
  }

  @action addPageResult(other: QueryResult<T>) {
    return this.mutate({
      total: other.total,
      records: [...this.records, ...other.records]
    })
  }

  @action replacePageResult(other: QueryResult<T>, from: number, fn?: (current: T, next: T) => T) {
    const records = fn == null ? other.records : other.records.joinLeft(
      this.records, e => e.id, e => e.id, (next, current) => {
        if (current != null) {
          fn(current, next)
        }
        return next
      }
    )
    return this.mutate({
      total: other.total,
      records: [
        ...this.records.filter((_, i) => i < from),
        ...records
      ]
    })
  }

  @action replaceBy<K>(item: T, fn: (e: T) => K) {
    return this.mutate({
      records: this.records.map(e => fn(e) === fn(item) ? item : e)
    })
  }

  @action moveTo<K>(item: T, fn: (e: T) => K, i = 0) {
    const records = this.records.filter(e => fn(e) !== fn(item))
    records.splice(i, 0, item)
    return this.mutate({
      records
    })
  }

  map<U extends Entity>(callbackfn: (value: T, index: number, array: T[]) => U, thisArg?: any): QueryResult<U> {
    return QueryResult.empty<U>().copy({
      total: this.total,
      records: this.records.map(callbackfn, thisArg)
    })
  }

  get fetchedAll() {
    return this.total <= this.records.length
  }

  indexOf(value: T) {
    return this.records.indexOf(value)
  }

  @action clear() {
    return this.mutate({
      records: []
    })
  }

  @action applyArray(arr: T[]) {
    return this.mutate({
      records: arr,
      total: arr.length
    })
  }
}
