import {Theme} from "@material-ui/core/styles/createMuiTheme"
import withStyles from "@material-ui/core/styles/withStyles"
import * as React from "react"
import {Classes} from "../util"
import * as classNames from "classnames"

const styles = (theme: Theme) => ({
  textPrimary: {
    color: theme.palette.text.primary
  },
  darkBlue: {
    color: theme.palette.custom.darkBlue
  },
  green: {
    color: theme.palette.custom.green
  },
  grey: {
    color: theme.palette.custom.grey
  },
  grey2: {
    color: theme.palette.custom.grey2
  },
  grey7: {
    color: theme.palette.custom.grey7
  },
  white: {
    color: theme.palette.common.white
  },
  danger: {
    color: theme.palette.error.main,
  },
})

export type ColorType = keyof ReturnType<typeof styles>

export interface ColorProps<P extends { className?: string }> {
  color?: ColorType
  children: React.ReactElement<P>
  classes?: Classes<typeof styles>
  className?: string
}

export const Color = withStyles(styles)(
  function Color<P extends { className?: string }>({color = "textPrimary", children, classes, ...rest}: ColorProps<P>) {
    const {type: WrappedComponent, props: wrappedProps} = children
    return <WrappedComponent
      {...rest}
      {...wrappedProps}
      className={classNames(wrappedProps.className, rest.className, classes![color])}/>
  }
)
