import {action, runInAction} from "mobx"
import {SideMenuBox} from "./sideMenuBox"
import {SideMenuState} from "./sideMenuState"
import {RouterActions} from "../router"
import {AppActions} from "../app/appActions"

export interface SideMenuActionsParams {
  state: SideMenuState
  router: RouterActions
  appActions: AppActions
}

export interface SideMenuActions extends SideMenuActionsParams {
  stateHolder: SideMenuState
}

export class SideMenuActions {
  constructor(params: SideMenuActionsParams) {
    Object.assign(this, params)
  }

  changeExpanded = (expanded?: boolean) => {
    if (expanded != null) {
      runInAction(() => this.state.expanded = expanded)
    }
    return this.state.expanded
  }

  @action changeMenuItemExpanded(value: SideMenuBox) {
    if (!this.state.expanded) {
      value.changeExpanded(true)
    } else {
      value.changeExpanded()
    }
    this.changeExpanded(true)
  }

  @action reset() {
    this.state.mutate({
      expanded: true
    })
    this.state.apps.expanded = false
    this.state.app.expanded = false
    this.state.addApp.expanded = false
    this.state.reports.expanded = false
    this.state.monetize.expanded = false
    this.state.finance.expanded = false
  }
}
