import {Theme} from "@material-ui/core/styles/createMuiTheme"
import withStyles from "@material-ui/core/styles/withStyles"
import * as React from "react"
import {Classes} from "../util"
import * as classNames from "classnames"

const styles = (theme: Theme) => ({
  darkBlue: {
    fill: theme.palette.custom.darkBlue
  },
  green: {
    fill: theme.palette.custom.green
  },
  grey: {
    fill: theme.palette.custom.grey
  },
  white: {
    fill: theme.palette.common.white
  },
  blue: {
    fill: theme.palette.custom.blue
  },
  midGreen: {
    fill: theme.palette.custom.midGreen
  },
  midPurple: {
    fill: theme.palette.custom.midPurple
  },
  none: {
    fill: "none"
  }
})

export type FillColor = keyof ReturnType<typeof styles>

export interface FillProps<P extends { className?: string }> {
  color?: FillColor
  children: React.ReactElement<P>
  classes?: Classes<typeof styles>
  className?: string
}

export const Fill = withStyles(styles)(
  function Fill<P extends { className?: string }>({color = "darkBlue", className, children, classes}: FillProps<P>) {
    const {type: WrappedComponent, props: wrappedProps} = children
    return <WrappedComponent
      {...wrappedProps}
      className={classNames(wrappedProps.className, className, classes![color])}/>
  }
)
