import {Unit} from "../../inventory/unit"

export type MonitorTrackValue = "errors" | "viewability";

export interface MonitorTrack {
  id: MonitorTrackValue;
  name: string;
}

export class MonitorTrack {

  static ERRORS = new MonitorTrack({
    id: "errors",
    name: "Errors"
  });

  constructor({id, name}: MonitorTrack) {
    this.id = id;
    this.name = name;
  }
}

const DEFAULT_MONITOR = {
  track: [MonitorTrack.ERRORS.id]
};

export interface Monitored {
  track: MonitorTrack
}

export class Monitored {
  toJS(unit: Unit) {
    return {
      track: this.track
    }
  }

  static DEFAULT_MONITOR = DEFAULT_MONITOR;

  constructor(monitored: Monitored) {
    Object.assign(this, monitored)
  }
}
