import {DisplayUnit} from "./DisplayUnit";
import {VideoUnit} from "./VideoUnit";
import {UnitType} from "../unitType";
import {DisplayAndVideo} from "./DisplayAndVideo";
import {UnitFormat} from "../unitFormat";
import {BaseUnit} from "./BaseUnit";
import {NativeVideo} from "./NativeVideo";

const {DISPLAY, VIDEO, VIDEO_AND_DISPLAY, NATIVE} = UnitFormat
type ConfigCreatorFn = (...args: any) => any;

type UnitFormatToConfigCreator = Record<string, ConfigCreatorFn>

const configDefaultsByUnit: UnitFormatToConfigCreator = {
  [DISPLAY.id]: (unitType: UnitType) => DisplayUnit.defaultByType(unitType),
  [VIDEO.id]: VideoUnit.defaultByUnit,
  [VIDEO_AND_DISPLAY.id]: DisplayAndVideo.defaultByUnit,
  [NATIVE.id]: () => BaseUnit.DEFAULT_BASE_UNIT
}

const configCreators: UnitFormatToConfigCreator = {
  [DISPLAY.id]: DisplayUnit.fromJS,
  [VIDEO.id]: (config: any) => VideoUnit.fromJS(config),
  [VIDEO_AND_DISPLAY.id]: DisplayAndVideo.fromJS,
  [NATIVE.id]: NativeVideo.fromJS
}

function createUnitConfig(format: UnitFormat, config: any) {
  const configFactory =  configCreators[format.id] || BaseUnit.fromJS
  return configFactory(config)
}

function createUnitDefaultConfig(format: UnitFormat, type: UnitType) {
  const configFactory = configDefaultsByUnit[format.id] || (() => BaseUnit.DEFAULT_BASE_UNIT)
  const defaultConfig = configFactory(type)
  return createUnitConfig(format, defaultConfig)
}

export class ConfigCreator {
  static createUnitConfig = createUnitConfig;
  static createUnitDefaultConfig = createUnitDefaultConfig;
}
