import {TPNIntegrationType} from "@revdesk/model/build/mediation"
import {action} from "mobx"
import {applyMixins} from "ts-trait"
import {ReactBottomListener} from "tsla-util/lib/dom/reactBottomListener"
import {TPNClient} from "../api"
import {BrowseActions} from "../browse"
import {RouterActions} from "../router"
import {TpnAppActions} from "../tpn-app"
import {TPNAppEditActions} from "../tpn-app-edit"
import {CardAnimationState} from "./cardAnimationState"
import {TPNGridCardState} from "./tpnGridCardState"
import {TPNGridFilters, TPNGridState} from "./tpnGridState"

export interface TPNGridActionsParams {
  state: TPNGridState
  router: RouterActions
  tpnClient: TPNClient
}

export interface TPNGridActions extends TPNGridActionsParams, TPNAppEditActions {
  state: TPNGridState
}

@applyMixins([TPNAppEditActions])
export class TPNGridActions {
  tpnApp: TpnAppActions
  browse: BrowseActions<TPNGridCardState, TPNGridFilters>

  constructor(params: TPNGridActionsParams) {
    Object.assign(this, params)
    this.openEditDialog = action(this.openEditDialog.bind(this))

    this.browse = new BrowseActions({
      state: this.state.browse,
      fetchData: () => {
        const {appId} = this.router.state.params
        const {query} = this.state.browse
        return this.tpnClient.paginate(appId, query)
      }
    })

    this.tpnApp = new TpnAppActions({
      state: this.state.tpnApp,
      tpnClient: this.tpnClient,
      router: this.router,
      appHolder: this
    })

    this.tpnApp.on("dismiss", this.handleTPNAppClose)

    this.browse.on("result", this.handleResult)
  }

  get appId() {
    return this.router.state.params.appId
  }

  fetchNextPage = () => {
    if (!this.state.browse.fetchState.isLoading) {
      this.browse.fetchNextPageIfNeeded()
    }
  }

  unsubReactBottom = new ReactBottomListener({
    offset: (64 + 32) * 6,
    onReachBottom: this.fetchNextPage,
    win: window,
    body: document.body
  }).subscribe(100)

  handleResult = () => {
    this.changeQueryResult()
  }

  @action unshiftToDisabledNetworks(value: TPNGridCardState) {
    this.state.disabledNetworks = [...this.state.disabledNetworks]
    this.state.disabledNetworks.unshift(value)
  }

  @action pushToActiveNetworks(value: TPNGridCardState) {
    this.state.activeNetworks = [...this.state.activeNetworks]
    this.state.activeNetworks.push(value)
  }

  @action unshiftToActiveNetworks(value: TPNGridCardState) {
    this.state.activeNetworks = [...this.state.activeNetworks]
    this.state.activeNetworks.unshift(value)
  }


  @action changeIntegrationType(type: TPNIntegrationType) {
    const {query} = this.state.browse
    this.state.integrationType = type
    query.filters.$.type.onChange([type.id])
    this.router.search({
      type: type.id
    })
    return this.browse.refresh()
  }

  handleEditClick = (card: TPNGridCardState) => {
    this.openEditDialog(card)
  }

  @action finishCollapsing(card: TPNGridCardState) {
    card.finishCollapsing()
    if (card.animation === CardAnimationState.positioningBeforeToggleOff) {
      this.unshiftToDisabledNetworks(card)
    }
  }

  finishToggleOff(card: TPNGridCardState) {
    card.finishToggleOff()
    this.refreshActiveNetworks()
  }

  finishToggledOn(card: TPNGridCardState) {
    card.finishToggledOn()
    this.refreshDisabledNetworks()
  }

  toggleOff(card: TPNGridCardState) {
    card.keepPrevActivePosition()
    card.enableDisable()
    this.tpnClient.setIntegrationStatus(
      card.tpnIntegration.toggleEnabled(false)
    )
  }

  @action
  async toggleOn(card: TPNGridCardState) {
    if (card.completeParams) {
      card.keepPrevDisabledPosition()
      card.enableDisable()
      this.unshiftToActiveNetworks(card)

      this.tpnClient.setIntegrationStatus(
        card.tpnIntegration.toggleEnabled(true)
      )
    } else {
      await this.openEditDialog(card)
      this.tpnApp.state.tpnIntegration = this.tpnApp.state.tpnIntegration.copy({
        enabled: true
      })
    }
  }

  @action.bound
  async handleTPNAppClose() {
    const {tpnIntegration, saved, placementsDeleted} = this.tpnApp.state
    this.router.search({
      networkId: ""
    })
    this.state.integrationToEdit = null
    const card = this.findCardOfTPNIntegration()
    if (card != null) {
      if (saved) {
        if (card.tpnIntegration.enabled !== tpnIntegration.enabled) {
          if (card.tpnIntegration.enabled) {
            card.keepPrevActivePosition()
            card.enableDisable()
          } else {
            card.keepPrevDisabledPosition()
            card.enableDisable()
            this.unshiftToActiveNetworks(card)
          }
        }
      }

      if (saved || placementsDeleted) {
        await this.browse.refreshPageOf(card, (current, next) => {
          next.animation = current.animation
          return next
        })
      }
    }
  }

  @action
  changeStatusFilter(value: string) {
    this.state.browse.query.filters.$.status.onChange(value)
    this.browse.refresh()
  }

  @action changeQueryResult() {
    this.refreshActiveNetworks()
    this.refreshDisabledNetworks()
  }

  @action refreshActiveNetworks() {
    const {result} = this.browse.state
    this.state.activeNetworks = result.records.filter(e => e.tpnIntegration.enabled)
  }

  @action refreshDisabledNetworks() {
    const {result} = this.browse.state
    this.state.disabledNetworks = result.records.filter(e => e.tpnIntegration.disabled)
  }

  get tpnCardBrowse() {
    return this.browse
  }
}
