import {DataClass, dataClass} from "data-class-copy"

export interface EntityLevelParams {
  id: string
  name: string
}

export interface EntityLevel extends EntityLevelParams, DataClass<EntityLevelParams> {

}

const idToEntityLevel: Record<string, EntityLevel> = {}

@dataClass
export class EntityLevel {
  static parse(params: string) {
    return idToEntityLevel[params] || EntityLevel.unknown
  }

  static unknown = new EntityLevel({
    id: "",
    name: "Unknown"
  })

  static publisher = new EntityLevel({
    id: "1",
    name: "Publisher"
  })

  static app = new EntityLevel({
    id: "2",
    name: "App"
  })

  static placement = new EntityLevel({
    id: "3",
    name: "Placement"
  })

  constructor(params: EntityLevelParams) {
    Object.assign(this, params)
    idToEntityLevel[params.id] = this
  }

  toJSON() {
    return this.id
  }

  get isPlacementLevel() {
    return this === EntityLevel.placement
  }

  get isAppLevel() {
    return this === EntityLevel.app
  }
}
