import * as React from "react"
import * as memoizeOne from "memoize-one"
import {initials} from "@revdesk/util/build/initials"

const memoizedInitials = memoizeOne(initials)

export interface InitialsProps {
  children: string
}

export function Initials(props: InitialsProps) {
  return <>{memoizedInitials(props.children)}</>
}
