import {applyMixins} from "ts-trait/build"
import {Unit} from "../..";
import {Mime, MimeValue} from "../mime";
import {OrientationType, OrientationTypeValue} from "../orientationType";
import {SkipType, SkipTypeValue} from "../skipType";
import TapType, {TapTypeValue} from "../tapType";
import {UnitType} from "../unitType";
import {BaseUnit, BaseUnitParams} from "./BaseUnit";
import {CompanionType, CompanionTypeValue} from "./CompanionType";
import {PlaybackMethod} from "./PlaybackMethod";
import {default as Range, RangeJS} from "./Range";

const {REWARDED} = UnitType

export interface VideoUnitParams extends BaseUnitParams {
  minMaxBitRate: RangeJS;
  minMaxDuration: RangeJS;
  companionType: CompanionTypeValue[];
  skip: SkipTypeValue;
  muted: boolean;
  autoPlay: boolean;
  padding: number;
  orientation: OrientationTypeValue;
  tap: TapTypeValue;
  pivotBitRate: number;
  mimes: MimeValue[];
  type?: UnitType;
}

export interface IVideoUnit extends BaseUnitParams {
  minMaxBitRate: Range;
  minMaxDuration: Range;
  companionType: CompanionTypeValue[];
  skip: SkipTypeValue;
  muted: boolean;
  autoPlay: boolean;
  orientation: OrientationTypeValue;
  tap: TapTypeValue;
  pivotBitRate: number;
  mimes: MimeValue[];
  padding: number;  // landscape only
}

export interface VideoUnit extends IVideoUnit, BaseUnit {

}

@applyMixins([BaseUnit])
export class VideoUnit implements BaseUnit {

  static REWARDED_FIXED_CONFIG = {
    skip: SkipType.CANT_SKIP.id,
    muted: false,
    minMaxDuration: {
      min: 15,
      max: 60
    }
  }

  static DEFAULT_VIDEO_UNIT: VideoUnitParams = {
    ...BaseUnit.DEFAULT_BASE_UNIT,
    pos: 7,
    minMaxBitRate: {min: 0, max: 5000},
    minMaxDuration: {min: 5, max: 60},
    companionType: [CompanionType.STATIC.id, CompanionType.HTML.id, CompanionType.iFrame.id],
    skip: SkipType.FIVE.id,
    muted: true,
    autoPlay: true,
    orientation: OrientationType.USER.id,
    tap: TapType.NO_ACTION.id,
    pivotBitRate: 2048,
    mimes: [Mime.VIDEO_MP4.id],
    padding: 0,
    filteredProtocols: [],
    filteredAPIs: []
  }

  static typeToDefaults = {
    [REWARDED.id]: VideoUnit.REWARDED_FIXED_CONFIG,
  }

  static defaultByUnit = (unitType: UnitType) => {
    return {
      ...VideoUnit.DEFAULT_VIDEO_UNIT,
      ...(VideoUnit.typeToDefaults[unitType.id] || {}),
    }
  }

  toJS(unit: Unit) {
    const value = {
      ...BaseUnit.prototype.toJS.call(this, unit),
      fullScreen: true,
      pos: 7,
      minMaxBitRate: this.minMaxBitRate,
      minMaxDuration: this.minMaxDuration,
      companionType: this.companionType,
      muted: this.muted,
      autoPlay: this.autoPlay,
      orientation: this.orientation,
      tap: this.tap,
      mimes: this.mimes,
      pivotBitRate: this.pivotBitRate
    }

    if (unit.supportsSkip()) {
      value.skip = this.skip
    }

    return value
  }

  static fromJS(video: VideoUnitParams = VideoUnit.DEFAULT_VIDEO_UNIT) {
    return new VideoUnit(video)
  }

  toServerJS(unit: Unit) {
    const value = {
      ...BaseUnit.prototype.toServerJS.call(this, unit),
      fullScreen: true,
      pos: 7,
      minDuration: this.minMaxDuration.min,
      maxDuration: this.minMaxDuration.max,
      minBitrate: this.minMaxBitRate.min,
      maxBitrate: this.minMaxBitRate.max,
      playbackmethod: [PlaybackMethod.fromBools(this)!.id]
    }
    if (unit.supportsCompanion()) {
      value.companionType = this.companionType
    }
    return value
  }

  constructor(params: VideoUnitParams) {
    Object.assign(this, params)
  }
}
