import {DataClass, dataClass} from "data-class-copy"

export interface CredentialsParams {
  username: string;
  password: string;
}

export interface Credentials extends CredentialsParams, DataClass<CredentialsParams> {

}

@dataClass
export class Credentials {
  static empty = new Credentials({
    username: "",
    password: ""
  })

  static parse(params: any = {}) {
    const username = params.username || params.email
    return Credentials.empty.copy({
      username: username != null ? String(username) : "",
      password: params.password != null ? String(params.password) : "",
    })
  }

  constructor(params: CredentialsParams) {
    Object.assign(this, params)
  }

  at(url: string) {
    return this.isNotEmpty ? `${this.toUrlString}@${url}` : url
  }

  get toUrlString() {
    return `${this.username}:${this.password}`
  }

  get isNotEmpty() {
    return this.username && this.password
  }

  get isEmpty() {
    return !this.isNotEmpty
  }
}
