declare global {
  interface Window {
    _walkmeConfig: any
    walkMeUserId: string
  }
}

export function loadWalkMeScript(test: boolean) {
  const env = test ? "/test" : "";
  const walkme = document.createElement("script");
  walkme.type = "text/javascript";
  walkme.async = true;
  // tslint:disable
  walkme.src = `https://cdn.walkme.com/users/5539d3be25f74f01ba56892bf01a34db${env}/walkme_5539d3be25f74f01ba56892bf01a34db_https.js`;
  // tslint:enable
  const s = document.getElementsByTagName("script")[0];
  s.parentNode!.insertBefore(walkme, s);
  window._walkmeConfig = {smartLoad: true};
}

export function changeWalkMeUserId(walkMeUserId: string) {
  window.walkMeUserId = walkMeUserId
}
