import {TPNPlacement} from "@revdesk/model/build/mediation"
import {action, observable} from "mobx"
import {ToggleableFieldState} from "../toggleable-field"
import {CommonValidators} from "../validators/commonValidators"

export interface TPNPlacementItemStateParams {
  placement: TPNPlacement
  toggleableField: ToggleableFieldState
}

export interface TPNPlacementItemState extends TPNPlacementItemStateParams {

}

export class TPNPlacementItemState {
  @observable placement: TPNPlacement
  toggleableField: ToggleableFieldState

  constructor(params: TPNPlacementItemStateParams) {
    this.placement = params.placement
    this.toggleableField = params.toggleableField
  }

  @action initPlacement(placement: TPNPlacement) {
    this.placement = placement
    this.toggleableField.fieldState.onChange(placement.name)
  }

  initValidators(uniquePlacementName: (value: string) => Promise<string | null | undefined | false>) {
    this.toggleableField.fieldState.validators(CommonValidators.nonEmpty, uniquePlacementName)
  }
}
