import {Fun} from "@revdesk/base-types";
import {EventEmitter} from "events"
import {HasEmitter, hasEmitter} from "tsla-util/lib/has-emitter"
import {SideMenuActions} from "../side-menu/sideMenuActions"
import {PageContainerState} from "./pageContainerState"
import {RouterActions} from "../router"
import {AppActions} from "../app/appActions"
import {AppBarActions} from "../app-bar/appBarActions"

export interface PageContainerActionsParams {
  state: PageContainerState
  onLogout: Fun
  router: RouterActions
  appActions: AppActions
}

export interface PageContainerActions extends PageContainerActionsParams, HasEmitter {
}

@hasEmitter
export class PageContainerActions implements HasEmitter {
  emitter = new EventEmitter()

  sideMenuActions: SideMenuActions

  appBarActions: AppBarActions

  constructor(params: PageContainerActionsParams) {
    Object.assign(this, params)
    this.sideMenuActions = new SideMenuActions({
      state: this.state.sideMenuState,
      router: this.router,
      appActions: this.appActions
    })

    this.appBarActions = new AppBarActions({
      state: this.state.appBarState
    })

    this.appBarActions.on("logout", this.onLogout)
    this.appBarActions.on("expandCollapse", () => {
      this.state.sideMenuState.expandCollapseMenu()
    })
  }
}
