import {TPNGridCardState} from "./tpnGridCardState"

export interface CardAnimationStateParams {
  id: string
  expandCollapse: (state: TPNGridCardState) => TPNGridCardState
  enableDisable: (state: TPNGridCardState) => TPNGridCardState
  finishToggleOff: (state: TPNGridCardState) => TPNGridCardState
  finishToggledOn: (state: TPNGridCardState) => TPNGridCardState
  finishCollapsing: (state: TPNGridCardState) => TPNGridCardState
  isExpanded: boolean
  isEnabled: boolean
  isInActiveList: boolean
  isInDisabledList: boolean
}

export interface CardAnimationState extends CardAnimationStateParams {

}

export class CardAnimationState {
  static fromEnabled(enabled: boolean) {
    return enabled ? CardAnimationState.collapsedOn : CardAnimationState.toggledOff
  }

  static togglingOn: CardAnimationState = new CardAnimationState({
    id: "togglingOn",
    expandCollapse: state => state,
    enableDisable: state => state,
    finishToggleOff: state => state,
    finishToggledOn: state => state.mutate({
      animation: CardAnimationState.collapsedOn,
      tpnIntegration: state.tpnIntegration.toggleEnabled(true)
    }),
    finishCollapsing: state => state,
    isEnabled: true,
    isExpanded: false,
    isInActiveList: true,
    isInDisabledList: true,
  })

  static collapsedOn: CardAnimationState = new CardAnimationState({
    id: "collapsedOn",
    expandCollapse: state => state.mutate({
      animation: CardAnimationState.expandedOn
    }),
    enableDisable: state => state.mutate({
      animation: CardAnimationState.togglingOff
    }),
    finishToggleOff: state => state,
    finishToggledOn: state => state,
    finishCollapsing: state => state,
    isEnabled: true,
    isExpanded: false,
    isInActiveList: true,
    isInDisabledList: false,
  })

  static expandedOn: CardAnimationState = new CardAnimationState({
    id: "expandedOn",
    expandCollapse: state => state.mutate({
      animation: CardAnimationState.collapsedOn
    }),
    enableDisable: state => state.mutate({
      animation: CardAnimationState.collapsingBeforeToggleOff
    }),
    finishToggleOff: state => state,
    finishToggledOn: state => state,
    finishCollapsing: state => state,
    isEnabled: true,
    isExpanded: true,
    isInActiveList: true,
    isInDisabledList: false,
  })

  static collapsingBeforeToggleOff: CardAnimationState = new CardAnimationState({
    id: "collapsingBeforeToggleOff",
    expandCollapse: state => state,
    enableDisable: state => state,
    finishToggleOff: state => state,
    finishToggledOn: state => state,
    finishCollapsing: state => state.mutate({
      animation: CardAnimationState.positioningBeforeToggleOff
    }),
    isEnabled: false,
    isExpanded: false,
    isInActiveList: true,
    isInDisabledList: false,
  })

  static togglingOff: CardAnimationState = new CardAnimationState({
    id: "togglingOff",
    expandCollapse: state => state,
    enableDisable: state => state,
    finishToggleOff: state => state.mutate({
      animation: CardAnimationState.toggledOff,
      tpnIntegration: state.tpnIntegration.toggleEnabled(false)
    }),
    finishToggledOn: state => state,
    finishCollapsing: state => state,
    isEnabled: false,
    isExpanded: false,
    isInActiveList: true,
    isInDisabledList: true,
  })

  static toggledOff: CardAnimationState = new CardAnimationState({
    id: "toggledOff",
    expandCollapse: state => state,
    enableDisable: state => state.mutate({
      animation: CardAnimationState.positioningBeforeToggleOn
    }),
    finishToggledOn: state => state,
    finishToggleOff: state => state,
    finishCollapsing: state => state,
    isEnabled: false,
    isExpanded: false,
    isInActiveList: false,
    isInDisabledList: true,
  })

  static positioningBeforeToggleOff = new CardAnimationState({
    id: "positioningBeforeToggleOff",
    expandCollapse: state => state,
    enableDisable: state => state.mutate({
      animation: CardAnimationState.togglingOff
    }),
    finishToggledOn: state => state,
    finishToggleOff: state => state,
    finishCollapsing: state => state,
    isEnabled: false,
    isExpanded: false,
    isInActiveList: true,
    isInDisabledList: false,
  })

  static positioningBeforeToggleOn = new CardAnimationState({
    id: "positioningBeforeToggleOn",
    expandCollapse: state => state,
    enableDisable: state => state.mutate({
      animation: CardAnimationState.togglingOn
    }),
    finishToggledOn: state => state,
    finishToggleOff: state => state,
    finishCollapsing: state => state,
    isEnabled: false,
    isExpanded: false,
    isInActiveList: false,
    isInDisabledList: true,
  })

  constructor(params: CardAnimationStateParams) {
    Object.assign(this, params)
  }

  get isTogglingOff() {
    return this === CardAnimationState.togglingOff
  }

  get isToggledOff() {
    return this === CardAnimationState.toggledOff
  }

  get isCollapsingBeforeToggleOff() {
    return this === CardAnimationState.collapsingBeforeToggleOff
  }

  get isTogglingOn() {
    return this === CardAnimationState.togglingOn
  }

  get isToggledOffOrTogglingOff() {
    return this.isToggledOff || this.isTogglingOff
  }
}
