export function logDecorator(label: string = "") {
  return function (proto: any, propertyKey: string, descriptor: PropertyDescriptor) {
    const fn = descriptor.value
    if (typeof fn !== "function") {
      throw new Error(`@logDecorator can only be applied to methods not: ${typeof fn}`);
    }

    return {
      ...descriptor,
      value(this: any, ...args: any[]) {
        console.log({
          name: label || fn.name,
          args,
          state: this.state
        })
        return fn.apply(this, args)
      }
    }
  }
}
