import * as React from "react"
import {HTMLAttributes} from "react"

export const Columned = (WrappedComponent: any) => {
  return ({style, alignX = "flex-start", alignY = "flex-start", ...props}: any) =>
    <WrappedComponent
      style={{
        display: "flex",
        flexDirection: "column",
        alignItems: alignX,
        justifyContent: alignY, ...style
      }} {...props} />
}

export const Rowed = (WrappedComponent: any) => {
  return ({style, alignX = "flex-start", alignY = "flex-start", ...props}: any) =>
    <WrappedComponent
      style={{
        display: "flex",
        flexDirection: "row",
        alignItems: alignY,
        justifyContent: alignX, ...style
      }} {...props} />
}

export const ColumnContainer = Columned("div")
export const RowContainer = Rowed("div")

export interface ColumnProps extends React.HTMLAttributes<HTMLDivElement> {
  children: React.ReactNode
  alignX?: string
  alignY?: string
}

export const Column = ({children, alignX = "flex-start", alignY = "flex-start", ...props}: ColumnProps) => {
  return <ColumnContainer alignX={alignX} alignY={alignY} {...props}>{children}</ColumnContainer>
}

export const CenterColumn = ({style, ...props}: any) => {
  return <Column style={{alignItems: "center", ...style}} {...props} />
}

export const Row = (props: any) => {
  return <RowContainer {...props} />
}

export const CenterRow = ({style, ...props}: any) => {
  return <Row style={{alignItems: "center", ...style}} {...props} />
}

export const FlexRow = ({className, alignX, alignY, style, ...props}: FlexProps) =>
  <div style={{alignItems: alignY, justifyContent: alignX, ...style}}
       className={["flexbox-row", className || ""].join(" ")} {...props}/>

export const FlexColumn = ({className, ...props}: any) =>
  <div className={["flexbox-column", className || ""].join(" ")}  {...props}/>

export interface FlexProps extends HTMLAttributes<any> {
  alignX?: string | any;
  alignY?: string | any;
}
