import {Role} from "./role"

export type GroupTypeID = "admin" | "internal" | "supply" | "demand" | string

export interface GroupTypeParams {
  id: GroupTypeID;
  name: string;
  desc: string;
  oldRole: Role;
  children: GroupType[];
  internal: boolean;
  companyType: "internal" | "supply" | "demand" | ""
}

export interface GroupType extends GroupTypeParams {

}

const values: Record<GroupTypeID, GroupType> = {}

export class GroupType {
  static parse(params: string) {
    return values[params] || GroupType.UNKNOWN
  }

  static UNKNOWN = new GroupType({
    id: "",
    name: "Unknown",
    desc: "",
    children: [],
    internal: false,
    oldRole: Role.UNKNOWN,
    companyType: ""
  })

  static ADMIN = new GroupType(({
    id: "admin",
    name: "Admin",
    desc: "Internal",
    oldRole: Role.ADMIN,
    children: [],
    internal: true,
    companyType: "internal"
  }))

  static INTERNAL = new GroupType({
    id: "internal",
    name: "Internal",
    desc: "Internal",
    oldRole: Role.SUPER_USER,
    children: [],
    internal: true,
    companyType: "internal"
  })

  static INTERNAL_SUPPLY = new GroupType({
    id: "internalSupply",
    name: "Internal Supply",
    desc: "Internal",
    oldRole: Role.SUPER_USER,
    children: [],
    internal: true,
    companyType: "internal"
  })

  static INTERNAL_DEMAND = new GroupType({
    id: "internalDemand",
    name: "Internal Demand",
    desc: "Internal",
    oldRole: Role.SUPER_USER,
    children: [],
    internal: true,
    companyType: "internal"
  })

  static SUPPLY = new GroupType({
    id: "supply",
    name: "Supply",
    desc: "External",
    oldRole: Role.PUBLISHER,
    children: [],
    internal: false,
    companyType: "supply"
  })

  static DEMAND = new GroupType({
    id: "demand",
    name: "Demand",
    desc: "External",
    oldRole: Role.DEMAND,
    children: [],
    internal: false,
    companyType: "demand"
  })

  static values = {...values}

  constructor(params: GroupTypeParams) {
    Object.assign(this, params)
    values[params.id] = this
  }

  get oldAuthId() {
    return this.oldRole.authId
  }

  toJSON() {
    return this.id
  }

  get external() {
    return !this.internal
  }

  get isPublisher() {
    return this.id === GroupType.SUPPLY.id
  }
}
