export type CompanionTypeValue = 1 | 2 | 3;

export interface CompanionType {
  id: CompanionTypeValue;
  name: string;
}

export  class CompanionType {

  static STATIC = new CompanionType({
    id: 1,
    name: "Static",
  });

  static HTML = new CompanionType({
    id: 2,
    name: "Html",
  });

  static iFrame = new CompanionType({
    id: 3,
    name: "iFrame",
  });

  constructor({id, name}: CompanionType) {
    this.id = id;
    this.name = name;

  }
}
