import {dataClass, DataClass} from "data-class-copy/lib"
import {observable} from "mobx"

export interface AppManagementStateParams {
  snackOpen: boolean
  filter: string
}

export interface AppManagementState extends AppManagementStateParams, DataClass<AppManagementState> {

}

@dataClass
export class AppManagementState {
  static empty = () => new AppManagementState({
    filter: "search",
    snackOpen: false
  })

  @observable snackOpen!: boolean
  @observable filter!: string

  constructor(params: AppManagementStateParams) {
    Object.assign(this, params)
  }
}
