import {DataClass, dataClass} from "data-class-copy"
import {FreqUnit} from "./freqUnit"
import {parseNumber, parseBool, parseString} from "tsla-util/lib/parse"

export interface FreqParams {
  id: string
  value: number
  unit: FreqUnit
  enabled: boolean
}

export interface Freq extends FreqParams, DataClass<FreqParams> {

}

@dataClass
export class Freq {
  static parse(params: any = {}) {
    return Freq.empty.copy({
      id: parseString(params.id),
      unit: FreqUnit.parse(params.unit),
      value: parseNumber(params.value),
      enabled: parseBool(params.enabled)
    })
  }

  static empty = new Freq({
    id: "",
    value: 0,
    unit: FreqUnit.unknown,
    enabled: false
  })

  static oneMinute = new Freq({
    id: "",
    value: 1,
    unit: FreqUnit.minute,
    enabled: false
  })

  static hour = new Freq({
    id: "",
    value: 0,
    unit: FreqUnit.hour,
    enabled: false
  })

  constructor(params: FreqParams) {
    Object.assign(this, params)
  }

  get isNotDefined() {
    return this.unit.isUnknown
  }

  get isDefined() {
    return !this.isNotDefined
  }

  get isSavedAndEnabled() {
    return Boolean(this.id && this.enabled)
  }

  get asEnabled() {
    return this.copy({
      enabled: true
    })
  }
}
