export type AppStatusValue = "active" | "inactive" | "deleted" | "tempDeleted" | "unknown"

const idToAppStatus: Record<string, AppStatus> = {}

export interface AppStatusParams {
  id: AppStatusValue,
  name: string,
  flags: { isBlackListed: boolean },
  traffic: boolean,
  deleted?: boolean
}

export interface AppStatus extends AppStatusParams {
}

export class AppStatus {
  static parse(params: string) {
    return idToAppStatus[params] || AppStatus.UNKNOWN
  }

  static fromBool(param: boolean) {
    return param ? AppStatus.ACTIVE : AppStatus.INACTIVE
  }

  static ACTIVE = new AppStatus({
    id: "active",
    name: "Active",
    traffic: true,
    flags: {
      isBlackListed: false
    }
  })

  static INACTIVE = new AppStatus({
    id: "inactive",
    name: "Inactive",
    traffic: false,
    flags: {
      isBlackListed: true
    },
  })

  static TEMP_DELETED = new AppStatus({
    id: "tempDeleted",
    name: "Deleted",
    deleted: true,
    traffic: false,
    flags: {
      isBlackListed: true
    }
  })

  static DELETED = new AppStatus({
    id: "deleted",
    name: "Deleted",
    traffic: false,
    deleted: true,
    flags: {
      isBlackListed: true
    }
  })

  static UNKNOWN = new AppStatus({
    id: "unknown",
    name: "Unknown",
    traffic: false,
    deleted: false,
    flags: {
      isBlackListed: true
    }
  })

  static deletedValues = [AppStatus.DELETED, AppStatus.TEMP_DELETED].map(e => e.id)

  constructor(params: AppStatusParams) {
    this.id = params.id
    this.name = params.name
    this.deleted = params.deleted
    this.flags = params.flags
    this.traffic = params.traffic
    idToAppStatus[params.id] = this
  }

  toJSON() {
    return this.id
  }

  get isActive() {
    return this.id === AppStatus.ACTIVE.id
  }

  get isNotActive() {
    return !this.isActive
  }

  get isDefined() {
    return this.id !== AppStatus.UNKNOWN.id
  }

  get toggle() {
    return this.isActive ? AppStatus.INACTIVE : AppStatus.ACTIVE
  }
}
