import {dataClass, DataClass} from "data-class-copy/lib"
import {SideMenuState} from "../side-menu/sideMenuState"
import {AppBarState} from "../app-bar/appBarState"

export interface PageContainerStateParams {
  sideMenuState: SideMenuState
  appBarState: AppBarState
}

export interface PageContainerState extends PageContainerStateParams, DataClass<PageContainerState> {

}

@dataClass
export class PageContainerState {
  static empty = () => new PageContainerState({
    sideMenuState: SideMenuState.empty(),
    appBarState: AppBarState.empty()
  })

  constructor(params: PageContainerStateParams) {
    Object.assign(this, params)
  }
}
