import {dataClass, DataClass} from "data-class-copy/lib"
import {Country} from "../country"
import {parseNumber} from "tsla-util/lib/parse"

export interface FloorPriceParams {
  value: number
  country: Country
}

export interface FloorPrice extends FloorPriceParams, DataClass<FloorPrice> {
}

@dataClass
export class FloorPrice {

  static empty = new FloorPrice({
    value: 0.01,
    country: new Country({
      code: "WW",
      country: "Worldwide"
    })
  })

  static parse(params: any = {}) {
    return new FloorPrice({
      value: parseNumber(params.value) || 0.01,
      country: Country.parse(params.country),
    })
  }

  constructor(params: FloorPriceParams) {
    Object.assign(this, params)
  }

  get id() {
    return this.country.code
  }

  toRecord(entityId: string, entityType: "Content") {
    const {value, country} = this
    return {
      value,
      countryCode: country.code,
      entityId: parseNumber(entityId),
      entityType
    }
  }
}
