export interface BoolPattern<T> {
  True: () => T;
  False: () => T;
}

export function boolMatch<T>(pattern: BoolPattern<T>) {
  return (b: boolean) => b ? pattern.True() : pattern.False()
}

export interface ElsePattern<T> {
  Else: () => T
}

export type StringPattern<T> = ElsePattern<T> & Record<string, () => T>;

export function stringMatch<T>(pattern: StringPattern<T>) {
  return (s: string | null | undefined) =>
    (pattern[Object.keys(pattern).find(k => k === s) as any] || pattern.Else)()
}
