import Grid from "@material-ui/core/Grid"
import {observer} from "mobx-react"
import * as React from "react"
import {TextImgAvatar} from "../../components/mui-components"
import {Tile} from "../../components/tile"
import {AppTinyInfo} from "../components/app"
import {Reading} from "../components/reading"
import {RevDeskTypography} from "../revDeskTypography"
import {MenuItemBG} from "./menuItemBG"
import {MenuItemExpansionPanel} from "./menuItemExpansionPanel"
import {SideMenuActions} from "@revdesk/state-actions/build/side-menu/sideMenuActions"
import {SideMenuBox} from "@revdesk/state-actions/build/side-menu/sideMenuBox"
import {SideMenuSubItem} from "./sideMenuSubItem"

export interface AppMenuItemProps {
  actions: SideMenuActions
  value: SideMenuBox
}

export const AppMenuItem = observer(function AppMenuItem({actions, value}: AppMenuItemProps) {
  const {state} = actions
  const {expanded: menuExpanded} = state
  const {menuApp} = actions.appActions.state
  const {current} = actions.router.state
  const params = {appId: menuApp.id}

  const isActive = current.path === value.firstLevel.href || value.secondLevel.some(e => e.href === current.path)
  const isExpanded = menuExpanded && value.expanded

  return (
    <MenuItemBG isActive={isActive}>
      <MenuItemExpansionPanel
        expanded={isExpanded}
        onExpand={() => actions.changeMenuItemExpanded(value)}
        active={isActive}
        actions={actions}
        summary={
          <Tile
            spaceBetween={8}
            avatar={
              <TextImgAvatar
                text={menuApp.name}
                src={menuApp.imageUrl}
                style={{height: "24px", width: "24px", borderRadius: "5px"}}
              />
            }
            header={
              menuExpanded ? (
                <Reading text={menuApp.name}>
                  <RevDeskTypography variant="s15w400" color="white">
                    {menuApp.name}
                  </RevDeskTypography>
                </Reading>
              ) : (
                undefined
              )
            }
            subHeader={menuExpanded ? <AppTinyInfo app={menuApp} /> : undefined}
          />
        }
        details={
          <Grid container direction="column">
            <SideMenuSubItem actions={actions} value={state.placements} params={params} />
            <SideMenuSubItem actions={actions} value={state.demandSource} params={params} />
          </Grid>
        }
      />
    </MenuItemBG>
  )
})
