import {DataClass, dataClass} from "data-class-copy"
import {User} from "./user"

export interface FeatureFlagParams {
  id: string;
  name: string;
  users: User[];
}

export interface FeatureFlag extends FeatureFlagParams, DataClass<FeatureFlagParams> {

}

@dataClass
export class FeatureFlag {
  static parse(params: any = {}) {
    const {id, name, users, featureFlagUsers} = params
    return FeatureFlag.EMPTY.copy({
      id: id != null ? String(id) : "",
      name: name != null ? String(name) : "",
      users: (users || featureFlagUsers || []).map(User.parse)
    })
  }

  static EMPTY = new FeatureFlag({
    id: "",
    name: "",
    users: []
  })

  constructor(params: FeatureFlagParams) {
    Object.assign(this, params)
  }
}
