import {Theme} from "@material-ui/core/styles/createMuiTheme"
import withStyles from "@material-ui/core/styles/withStyles"
import * as classNames from "classnames"
import * as React from "react"
import {Classes} from "../util"

const fontSize = (theme: Theme) => ({
  "12": {
    fontSize: theme.typography.pxToRem(12)
  },
  "14": {
    fontSize: theme.typography.pxToRem(14)
  },
  "16": {
    fontSize: theme.typography.pxToRem(16)
  },
  "17": {
    fontSize: theme.typography.pxToRem(17)
  },
  "18": {
    fontSize: theme.typography.pxToRem(18)
  },
  "20": {
    fontSize: theme.typography.pxToRem(20)
  },
  "24": {
    fontSize: theme.typography.pxToRem(24)
  },
  "28": {
    fontSize: theme.typography.pxToRem(28)
  },
  "56": {
    fontSize: theme.typography.pxToRem(56)
  }
})

export interface FontSizeProps<P extends { className?: string }> {
  fontSize?: keyof ReturnType<typeof fontSize>
  children: React.ReactElement<P> | ((className: string) => React.ReactElement<P>)
  classes?: Classes<typeof fontSize>
  className?: string
}

export const FontSize = withStyles(fontSize, {name: "FontSize", index: 1})(
  function FontSize<P extends { className?: string }>(
    {fontSize, children, classes, ...rest}: FontSizeProps<P>
  ) {

    let child
    if (typeof children === "function") {
      const className = classNames(
        classes![fontSize!]
      )
      child = children(className)
    } else {
      const {type: WrappedComponent, props: wrappedProps} = children
      const className = classNames(
        rest.className,
        wrappedProps.className,
        classes![fontSize!]
      )
      child = <WrappedComponent {...rest} {...wrappedProps} className={className}/>
    }
    return child
  }
)
