import {EventEmitter} from "events"
import {HasEmitter, hasEmitter} from "tsla-util/lib/has-emitter"
import {AppBarState} from "./appBarState"
import {action} from "mobx"
export interface AppBarActionsParams {
  state: AppBarState
}

export interface AppBarActions extends AppBarActionsParams, HasEmitter {
}

@hasEmitter
export class AppBarActions implements HasEmitter {
  emitter = new EventEmitter()

  constructor(params: AppBarActionsParams) {
    Object.assign(this, params)
  }

  @action.bound expandCollapseMenu() {
    this.emit("expandCollapse")
  }

  @action.bound logOut() {
    this.emit("logout")
  }
}
