import {DataClass, dataClass} from "data-class-copy"
import {NetworkType} from "../demand"

const idToTPNIntegrationType: Record<string, TPNIntegrationType> = {}

export interface TPNIntegrationTypeParams {
  id: string
  name: string
  networkTypes: NetworkType[]
  passive: string
}

export interface TPNIntegrationType extends TPNIntegrationTypeParams, DataClass<TPNIntegrationTypeParams> {

}

export class TPNIntegrationType {
  static parse(params: string) {
    return idToTPNIntegrationType[params] || TPNIntegrationType.unknown
  }

  static unknown = new TPNIntegrationType({
    id: "",
    name: "",
    networkTypes: [],
    passive: ""
  })

  static mediation = new TPNIntegrationType({
    id: "mediation",
    name: "Mediation",
    networkTypes: [NetworkType.mediation],
    passive: "Mediated"
  })

  static pmn = new TPNIntegrationType({
    id: "pmn",
    name: "Programmatic Mediation",
    networkTypes: [NetworkType.pmn],
    passive: "Programmatic Mediated"
  })

  static prog = new TPNIntegrationType({
    id: "prog",
    name: "Programmatic",
    networkTypes: [NetworkType.rtb, NetworkType.static, NetworkType.video],
    passive: "Programmatic"
  })

  static selectable = [
    TPNIntegrationType.mediation,
    TPNIntegrationType.pmn,
    TPNIntegrationType.prog
  ]

  constructor(params: TPNIntegrationTypeParams) {
    Object.assign(this, params)
    idToTPNIntegrationType[params.id] = this
  }

  toJSON() {
    return this.id
  }
}
