
import {Fun} from "@revdesk/base-types"

export function tryGet<T>(fn: Fun<T>, fallback?: T): T | null | undefined {
  try {
    const value = fn()
    return value !== void 0 ? value : fallback
  } catch (err) {
    return fallback
  }
}

export function tryGetLazy<T>(fn: Fun<T>, fallback?: () => T): T | null | undefined {
  try {
    const value = fn()
    return value !== void 0 ? value : fallback && fallback()
  } catch (err) {
    return fallback && fallback()
  }
}

export async function tryCatch<T>(fn: Fun<Promise<T>>, fallback?: (err?: Error) => T): Promise<T | null | undefined> {
  try {
    const value = await fn()
    return value !== void 0 ? value : fallback && fallback()
  } catch (err) {
    return fallback && fallback(err)
  }
}

export function noop() {}
