import {Unit} from "../..";
import BAttr, {BAttrValue} from "./BAttr";
import {FilteredUnit, FilteredUnitJS} from "./FilteredUnit";
import {Monitored} from "./Monitored";
import {Viewability} from "./Viewability";

export interface BaseUnitParams extends FilteredUnitJS {
  fullScreen: boolean,
  pos?: number,
  battr: BAttrValue[],
}

export interface BaseUnit extends Monitored, Viewability, BaseUnitParams {

}

export class BaseUnit implements Viewability, Monitored {
  static DEFAULT_BASE_UNIT: BaseUnitParams = {
    fullScreen: true,
    pos: 7,
    battr: BAttr.DEFAULT_BATTRS,
    ...Monitored.DEFAULT_MONITOR,
    ...Viewability.DEFAULT_VIEWABILITY
  }

  toJS(unit: Unit) {
    return {
      ...Monitored.prototype.toJS.call(this, unit),
      ...Viewability.prototype.toJS.call(this, unit),
      ...FilteredUnit.prototype.toJS.call(this, unit),
      fullScreen: this.fullScreen,
      battr: this.battr,
    }
  }

  toServerJS(unit: Unit) {
    return {
      fullScreen: this.fullScreen,
      battr: this.battr,
    }
  }

  static fromJS(base: BaseUnitParams = BaseUnit.DEFAULT_BASE_UNIT) {
    return new BaseUnit(base)
  }

  constructor(unit: BaseUnitParams) {
    Object.assign(this, unit)
  }
}
