import * as React from "react"
import {Transition} from "react-transition-group"
import {TransitionProps} from "react-transition-group/Transition"

type MApValues = <T, E, K>(t: T, cb: (value: K) => E) => Record<keyof T, E>

const mapValues: MApValues = require("lodash/mapValues")
export type TransitionState = "enter" | "entering" | "entered" | "exit" | "exiting" | "exited" | null

export function isOut(state: TransitionState) {
  return state === "exit" || state === "exiting" || state == "exited"
}

const events = {
  onEnter: "enter",
  onEntering: "entering",
  onEntered: "entered",
  onExit: "exit",
  onExiting: "exiting",
  onExited: "exited"
}

export interface TransitionedProps extends TransitionProps {
  children: (value: TransitionState) => React.ReactNode
  onChange?: (value: TransitionState) => any
  beforeChange?: (value: TransitionState) => any
}

export class Transitioned extends React.PureComponent<TransitionedProps> {
  state = {
    value: null as TransitionState
  }

  handleChange = () => {
    const {onChange} = this.props
    const {value} = this.state
    onChange && onChange(value)
  }

  handlers = mapValues(events, (value) => () => {
    const {beforeChange} = this.props

    if (value != null) {
      beforeChange && beforeChange(value as TransitionState)
    }

    if (value === "entering") {
      setTimeout(() => {
        this.setState({value}, this.handleChange)
      }, 0)
    } else {
      this.setState({value}, this.handleChange)
    }
  })

  render() {
    const {children, ...props} = this.props
    return <Transition
      {...this.handlers}
      {...props}
    >
      {children(this.state.value)}
    </Transition>
  }
}
