import {PlacementType} from "@revdesk/model/build/mediation"
import {TPNPlacement} from "@revdesk/model/build/mediation/tpnPlacement"
import {EventEmitter} from "events"
import {action, runInAction} from "mobx"
import {HasEmitter, hasEmitter} from "tsla-util/lib/has-emitter"
import {TPNClient} from "../api"
import {FetchState} from "../fetch-state"
import {ParamValueFieldState} from "../param-value-form"
import {RouterActions} from "../router"
import {TabsActions} from "../tabs"
import {TPNPlacementActions} from "../tpn-placement-form"
import {TPNPlacementListActions} from "../tpn-placement-list"
import {TpnAppState} from "./tpnAppState"

export interface TpnAppActionsParams {
  tpnClient: TPNClient
  router: RouterActions
  state: TpnAppState
  appHolder: {
    readonly appId: string
  }
}

export interface TpnAppActions extends TpnAppActionsParams, HasEmitter {
}

@hasEmitter
export class TpnAppActions implements HasEmitter {
  state!: TpnAppState

  emitter = new EventEmitter()

  tpnPlacementActions: TPNPlacementActions
  listActions: TPNPlacementListActions
  placementTabs: TabsActions<PlacementType>
  tpnAppValidators = [
    (field: ParamValueFieldState) => field.paramValue.param.validators.map(
      validator => validator(field.value)
    ),
    async (field: ParamValueFieldState) => {
      const paramValue = field.toParamValue
      return paramValue.param.unique ?
        this.tpnClient.uniqueAppParamValue(paramValue)
          .then(() => null)
          .catch(() => "This value already exists") :
        null
    },
  ]

  constructor(params: TpnAppActionsParams) {
    Object.assign(this, params)
    this.tpnPlacementActions = new TPNPlacementActions({
      tpnClient: this.tpnClient,
      router: this.router,
      state: this.state.placements
    })
    this.listActions = new TPNPlacementListActions({
      tpnClient: this.tpnClient,
      router: this.router,
      state: this.state.listState,
      appHolder: this
    })

    this.placementTabs = new TabsActions({
      state: this.state.placementTabs
    })

    this.listActions.on("placements", placements =>
      runInAction(() => {
        this.state.showForm = placements.isEmpty
      })
    )
    this.listActions.on("delete", (placement: TPNPlacement) =>
      runInAction(() => {
        this.state.placementsDeleted = true
        this.state.deletedPlacements.push(placement)
      })
    )
    this.placementTabs.on("tab", this.handleTabChange)
  }

  handleTabChange = action(async () => {
    const {placementTabs, placementParams, tpnIntegration} = this.state
    const placementType = placementTabs.value
    if (tpnIntegration.supportsPlacements && placementType != null) {
      this.tpnPlacementActions.changeParams(
        placementType,
        placementParams.filter(e => e.supportsPlacementType(placementType)),
        tpnIntegration
      )
      await this.listActions.changePlacementType(placementType)
    }
  })

  get appId() {
    return this.appHolder.appId
  }

  @action.bound
  async handleAddClickAddToList() {
    const {hasError} = await this.state.placements.form.setSubmittedAndValidate()
    if (!hasError) {
      runInAction(() => {
        this.listActions.addToList(this.state.placements.toTPNPlacements)
        this.state.showForm = false
        this.tpnPlacementActions.clearForm()
      })
    }
  }

  @action
  private async submitPlacementsForm() {
    this.state.submitState = FetchState.loading
    const {showForm, toTPNIntegration} = this.state
    if (showForm) {
      const placementValidation = await this.state.placements.form.setSubmittedAndValidate()
      if (placementValidation.hasError) {
        throw new Error(placementValidation.error)
      }
    }

    const appValidation = await this.state.form.setSubmittedAndValidate()
    if (appValidation.hasError) {
      throw new Error(appValidation.error)
    }

    const savedIntegration = showForm
      ? await this.tpnClient.saveAppAndPlacements(toTPNIntegration)
      : await this.tpnClient.saveApp(toTPNIntegration)

    runInAction(() => {
      this.state
        .changeIntegrationId(savedIntegration)
        .changeParamValues(savedIntegration.paramValues, this.tpnAppValidators)
    })

    return savedIntegration
  }


  @action
  async loadTPNApp() {
    const {networkId} = this.router.state.params
    const {appId} = this.appHolder

    this.state.fetchState = FetchState.loading

    try {
      const {tpnIntegration, placementParams} = await this.tpnClient.getApp(appId, networkId)

      await runInAction(async () => {
        this.state
          .mutate({
            tpnIntegration,
            placementParams,
            placementTabs: this.state.placementTabs.changeArray(tpnIntegration.placementTypes)
          })
          .changeParamValues(tpnIntegration.paramValues, this.tpnAppValidators)
        await this.handleTabChange()
      })

      runInAction(() => {
        this.state.fetchState = FetchState.resolved
      })
    } catch (e) {
      console.error(e)
      this.state.mutate({
        fetchState: FetchState.error
      })
    }
  }

  showForm = () => {
    runInAction(() => {
      this.state.showForm = true
    })
  }

  hideForm = () => runInAction(() => {
    this.state.showForm = false
  })

  @action.bound
  async handleAddClickSubmit() {
    if (this.state.submitState.isLoading) {
      return
    }
    try {
      const placementType = this.state.listState.placementType
      const integration = await this.submitPlacementsForm()
      if (placementType === this.state.listState.placementType) {
        this.listActions.refresh()
      }
      runInAction(() => {
        this.tpnPlacementActions.clearForm()
        this.state.submitState = FetchState.resolved
        this.state.savedPlacements.push(...integration.placements)
        this.state.saved = true
      })
    } catch (err) {
      console.error(err)
      runInAction(() => {
        this.state.mutate({
          submitState: FetchState.error
        })
      })
    }
  }

  @action.bound
  async handleSaveClick() {
    if (this.state.submitState.isLoading) {
      return
    }
    try {
      await this.submitPlacementsForm()
      runInAction(() => {
        this.state.mutate({
          submitState: FetchState.resolved,
          saved: true
        })
        this.emit("saved")
        this.dismiss()
      })
    } catch (err) {
      console.error(err)
      runInAction(() => {
        this.state.mutate({
          submitState: FetchState.error
        })
      })
    }
  }

  @action.bound dismiss() {
    this.emit("dismiss", this.state)
    this.state.mutate({
      savedPlacements: [],
      deletedPlacements: [],
      saved: false,
      placementsDeleted: false
    })
  }

  @action.bound
  toggleIntegrationStatus() {
    this.state.mutate({
      tpnIntegration: this.state.tpnIntegration.toggleStatus
    })
  }
}
