import "@revdesk/array"
import {Spot} from "@revdesk/model/build/inventory/spot"
import {PlacementType, TPNPlacement} from "@revdesk/model/build/mediation"
import {EventEmitter} from "events"
import {action} from "mobx"
import "rxjs/add/observable/timer"
import {HasEmitter, hasEmitter} from "tsla-util/lib/has-emitter"
import {TPNClient} from "../api"
import {BrowseActions, QueryResult} from "../browse"
import {RouterActions} from "../router"
import {ToggleableFieldState} from "../toggleable-field"
import {TPNPlacementItemActions, TPNPlacementItemState} from "../tpn-placement-item"
import {DeletedPlacementsFilters, TPNPlacementFilters, TPNPlacementListState} from "./tpnPlacementListState"

export interface TPNPlacementListActionsParams {
  tpnClient: TPNClient
  router: RouterActions
  state: TPNPlacementListState
  appHolder: {
    readonly appId: string
  }
}

export interface TPNPlacementListActions extends TPNPlacementListActionsParams, HasEmitter {
  on(type: "placements", fn: (placements: TPNPlacement[]) => any): any

  on(type: "delete", fn: (placement: TPNPlacement) => any): any


}

@hasEmitter
export class TPNPlacementListActions implements HasEmitter {

  tpn: BrowseActions<TPNPlacement, TPNPlacementFilters>
  browse: BrowseActions<Spot, DeletedPlacementsFilters>
  emitter = new EventEmitter()
  itemsActions: TPNPlacementItemActions[] = []

  refresh() {
    return this.tpn.refresh()
  }

  constructor(params: TPNPlacementListActionsParams) {
    Object.assign(this, params)
    const {query} = this.state.browse
    this.browse = new BrowseActions({
      state: this.state.browse,
      fetchData: () => {
        return this.tpnClient.getTPNPlacementLinkedPlacements(query)
      }
    })

    this.tpn = new BrowseActions({
      state: this.state.tpn,
      fetchData: async () => {
        const {networkId} = this.router.state.params
        const {appId} = this.appHolder
        const {placementType} = this.state
        return QueryResult.fromArray(
          await this.tpnClient.paginatePlacements(appId, networkId, placementType, "", [])
        )
      }
    })

    this.tpn.on("result", () => {
      this.emit("placements", this.state.tpn.result.records)
    })
  }

  @action
  changePlacementType(placementType: PlacementType) {
    this.state.mutate({placementType})
    return this.refresh()
  }

  @action
  async deleteTPNPlacement(placement: TPNPlacement) {
    this.emit("delete", placement)
    try {
      this.state.deleting = true
      await this.tpnClient.deletePlacement(placement)
      this.state.placements = this.state.placements.filter(e => e.id !== placement.id)
      this.state.deleting = false
    } catch (e) {
      console.error(e)
      this.state.deleting = false
    }
  }


  @action
  getPlacementByTpnPlacement(placement: TPNPlacement) {
    this.state.browse.query.filters.$.search.onChange(placement.id)
    this.browse.refresh()
  }

  @action addToList(placements: TPNPlacement[]) {
    this.state.placements = [
      ...this.state.placements,
      ...placements
    ]
  }

  createTPNPlacementItemActions(placement: TPNPlacement) {
    return new TPNPlacementItemActions({
      state: new TPNPlacementItemState({
        placement,
        toggleableField: ToggleableFieldState.empty()
      }),
      tpnClient: this.tpnClient,
      onPlacementSaved: this.handlePlacementSaved
    })
  }

  addItemActions(actions: TPNPlacementItemActions, placement: TPNPlacement) {
    actions.state.initPlacement(placement)
    this.itemsActions.push(actions)
  }

  removeItemActions(actions: TPNPlacementItemActions) {
    const index = this.itemsActions.indexOf(actions)
    this.itemsActions.splice(index, 1)
  }

  @action.bound handlePlacementSaved(placement: TPNPlacement) {
    this.state.placements = this.state.placements.replaceAt(
      e => e.id === placement.id,
      placement
    )
  }
}
