import {ComponentClass, ReactElement, ReactNode, SFC} from "react"

function isReactElement(e: any): e is ReactElement<any> {
  return e.type != null
}

export interface Wrapped<P> {
  WrappedComponent: string | ComponentClass<P> | SFC<P>
  wrappedProps: P
}

export function clone<P>({type, props}: ReactElement<P>): Wrapped<P> {
  return {
    WrappedComponent: type,
    wrappedProps: props
  }
}
