import {action} from "mobx"
import {BrowseActions} from "../browse"
import {RouterActions} from "../router"
import {TpnAppActions, TpnAppState} from "../tpn-app"
import {TPNGridCardState} from "../tpn-grid"
import {TPNAppEditState} from "./tpnAppEditState"

export interface TPNAppEditActionsParams {
  state: TPNAppEditState
  router: RouterActions
  tpnApp: TpnAppActions
}

export interface TPNAppEditActions extends TPNAppEditActionsParams {
  readonly tpnCardBrowse: BrowseActions<TPNGridCardState, any>
}

export class TPNAppEditActions {
  async openEditDialog(card: TPNGridCardState) {
    this.state.tpnApp = TpnAppState.empty()
    this.state.integrationToEdit = card
    this.router.search({
      networkId: card.tpnIntegration.networkId
    })
    await this.tpnApp.loadTPNApp()
  }

  findCardOfTPNIntegration() {
    const {result} = this.tpnCardBrowse.state
    const tpnApp = this.tpnApp.state
    return result.records.find(e => e.networkId === tpnApp.networkId)
  }

  @action.bound
  clearIntegrationToEdit() {
    this.state.integrationToEdit = null
  }
}
