import {NestedItem} from "@revdesk/util"
import {dataClass, DataClass} from "data-class-copy/lib"
import {applyMixins} from "ts-trait/build"
import {parseString} from "tsla-util/lib/parse"
import {DataTypes} from "../dataTypes"
import {Platform} from "../platform"
import {Searchable} from "../searchable"
import {AppStatus} from "./appStatus"
import {Publisher} from "./publisher"
import {Spot} from "./spot"

export interface AppSchema {
  id: string | number
  name: string
  publisherId: string | number
  status: string
  platform: string
  storeType: string
  rewardedAdUrl: string
  rewardedAdSecurityToken: string
}

export interface AppSchema {
  id: string | number
  name: string
  publisherId: string | number
  bundle: string
  platform: string
  imageUrl: string
  coppa: boolean
  rewardedAdUrl: string
  rewardedAdSecurityToken: string
}

export type StoreTypeValue = "apple" | "google" | null

export interface AppParams {
  id: string;
  name: string;
  publisherId: string;
  bundle: string;
  storeUrl: string;
  generic: boolean;
  platform: Platform;
  storeType: StoreTypeValue;
  imageUrl: string;
  category1: string;
  category2: string;
  coppa: boolean;
  spots: Array<Spot>;
  publisher?: Publisher | null;
  spotsInits: number;
  totalPublisherSpots: number;
  totalPublisherUnits: number;
  status: AppStatus;
  rewardedAdUrl: string;
  rewardedAdSecurityToken: string;
}

export interface App extends AppParams, NestedItem<Publisher, Spot>, DataClass<App> {

}

@dataClass
@applyMixins([NestedItem, Searchable])
export class App implements NestedItem<Publisher, Spot> {
  static AppRow: any
  static React: any
  static EMPTY = new App({
    id: "",
    name: "",
    publisherId: "",
    bundle: "",
    storeUrl: "",
    generic: false,
    platform: Platform.UNKNOWN,
    storeType: null,
    imageUrl: "",
    category1: "",
    category2: "",
    coppa: false,
    spots: [],
    publisher: null,
    spotsInits: 0,
    totalPublisherSpots: 0,
    totalPublisherUnits: 0,
    status: AppStatus.INACTIVE,
    rewardedAdUrl: "",
    rewardedAdSecurityToken: ""
  })

  static mini = (app: App) => app.mini

  static parse(params: any = {}) {
    return App.EMPTY.copy({
      id: params.id != null ? String(params.id) : "",
      name: params.name != null ? String(params.name) : "",
      publisherId: params.publisherId != null ? String(params.publisherId) : "",
      bundle: params.bundle != null ? String(params.bundle) : "",
      storeUrl: params.storeUrl != null ? String(params.storeUrl) : "",
      imageUrl: params.imageUrl != null ? String(params.imageUrl) : "",
      category1: params.category1 != null ? String(params.category1) : "",
      category2: params.category2 != null ? String(params.category2) : "",
      generic: params.generic != null ? Boolean(params.generic) : false,
      coppa: params.coppa != null ? Boolean(params.coppa) : false,
      platform: Platform.fromJS(params.platform),
      spots: (params.spots || []).map(Spot.parse),
      publisher: params.publisher && Publisher.parse(params.publisher),
      status: AppStatus.parse(params.status),
      storeType: params.storeType != null ? params.storeType : null,
      rewardedAdUrl: parseString(params.rewardedAdUrl),
      rewardedAdSecurityToken: parseString(params.rewardedAdSecurityToken),
    })
  }

  constructor(params: AppParams) {
    Object.assign(this, params)
  }

  get parent() {
    return this.publisher
  }

  get children() {
    return this.spots || []
  }

  changeChildren(spots: Array<Spot>) {
    return this.copy({spots})
  }

  setParentOnChildren() {
    this.spots = this.spots.map(spot => spot.copy({app: this}).setParentOnChildren())
    return this
  }

  withoutChildren(): App {
    return this.copy({
      spots: []
    })
  }

  get size() {
    const {spots} = this
    return spots.length + spots.reduce((a, b) => a + b.size, 0)
  }

  render(props: any) {
    const {AppRow, React} = App
    return React.createElement(AppRow, {
      ...props,
      app: this
    })
  }

  get type() {
    return "app"
  }

  get hash() {
    return this.type + "_" + this.id
  }

  get lite() {
    return {
      appId: this.id
    }
  }

  isMobileWeb() {
    return this.platform.id == Platform.MOBILE_WEB.id
  }

  get mini(): App {
    return App.EMPTY.copy({id: this.id})
  }

  get dataType() {
    return DataTypes.APP
  }

  get isActive() {
    return this.status.isActive
  }

  get toRecord() {
    return {
      id: this.id,
      publisherId: this.publisherId,
      name: this.name,
      bundle: this.bundle,
      status: this.status.id,
      platform: this.platform.id ? this.platform.id : null,
      storeType: this.storeType as string | undefined,
      coppa: this.coppa
    }
  }

  get ssrIsNotDefined() {
    return !this.rewardedAdUrl || !this.rewardedAdSecurityToken
  }

  get ssrIsDefined() {
    return !this.ssrIsNotDefined
  }
}
