import {DataClass, dataClass} from "data-class-copy"
import {action, computed, observable} from "mobx"
import {FetchState} from "../fetch-state"
import {Entity} from "./entity"
import {Filters, Query} from "./query"
import {QueryResult} from "./queryResult"

export interface BrowseStateParams<T extends Entity, E extends Filters<E>> {
  query: Query<E>
  resultCopy: QueryResult<T>
  result: QueryResult<T>
  fetchState: FetchState
  hoverIndex: number
}

export interface BrowseState<T extends Entity, E extends Filters<E>> extends BrowseStateParams<T, E>,
  DataClass<BrowseStateParams<T, E>> {
}

@dataClass
export class BrowseState<T extends Entity, E extends Filters<E>> {
  static empty = <T extends Entity, E extends Filters<E>>(filters: E) => new BrowseState<T, E>({
    query: Query.empty(filters),
    result: QueryResult.empty(),
    resultCopy: QueryResult.empty(),
    fetchState: FetchState.noData,
    hoverIndex: -1
  })

  @observable result!: QueryResult<T>
  resultCopy!: QueryResult<T>
  @observable fetchState!: FetchState
  @observable hoverIndex!: number

  constructor(params: BrowseStateParams<T, E>) {
    Object.assign(this, params)
  }

  changeLimit(limit: number) {
    return this.mutate({
      query: this.query.changeLimit(limit)
    })
  }

  @computed
  get isLoading() {
    return this.fetchState.isLoading
  }

  pageOf(value: T) {
    const index = this.result.indexOf(value)
    if (index !== -1) {
      return this.query.pagination.pageOf(index)
    }
    return null
  }

  @action removePage(page: number) {
    const {from, to} = this.query.pagination.pageBounds(page)
    return this.mutate({
      query: this.query.mutate({
        pagination: this.query.pagination.moveToPage(page - 1)
      }),
      result: this.result.mutate({
        records: this.result.records.filter((_, i) => i < from)
      })
    })
  }

  get hoverItem() {
    return this.result.records[this.hoverIndex]
  }
}
