import {PlacementType} from "@revdesk/model"
import {TPNIntegration} from "@revdesk/model/build/mediation"
import {DataClass, dataClass} from "data-class-copy"
import {action, observable, computed} from "mobx"
import {parseBool, parseNumber} from "tsla-util/lib/parse"
import {CardAnimationState} from "./cardAnimationState"

export interface CardDOMElement {
  getBoundingClientRect(): {
    x: number;
    y: number;
  }
}

export interface TPNGridCardStateParams {
  tpnIntegration: TPNIntegration
  placementTypeCount: { placementType: PlacementType, count: number }[]
  animation: CardAnimationState
  key: string
}

export interface TPNGridCardState extends TPNGridCardStateParams, DataClass<TPNGridCardStateParams> {

}

@dataClass
export class TPNGridCardState {
  static fromTPNIntegration = (tpnIntegration: TPNIntegration) => new TPNGridCardState({
    tpnIntegration,
    placementTypeCount: [],
    animation: CardAnimationState.collapsedOn,
    key: "",
  })

  static parse(params: any = {}) {
    const tpnIntegration = TPNIntegration.parse(params.tpnIntegration)
    return new TPNGridCardState({
      tpnIntegration,
      placementTypeCount: (params.placementTypeCount || []).map((e: any = {}) => ({
        count: parseNumber(e.count),
        placementType: PlacementType.parse(e.placementType)
      })),
      animation: CardAnimationState.fromEnabled(tpnIntegration.enabled),
      key: tpnIntegration.networkId,
    })
  }

  activeElement = {
    e: null as CardDOMElement | null,
    position: {
      x: 0,
      y: 0
    },
  }

  disabledElement = {
    e: null as CardDOMElement | null,
    position: {
      x: 0,
      y: 0
    }
  }

  @observable tpnIntegration!: TPNIntegration
  @observable animation!: CardAnimationState

  constructor(params: TPNGridCardStateParams) {
    Object.assign(this, params)
  }

  get networkId() {
    return this.tpnIntegration.networkId
  }

  activeRef = (e: any) => {
    this.activeElement.e = e
    if (this.animation === CardAnimationState.positioningBeforeToggleOn) {
      this.keepPrevActivePosition()
      this.animation.enableDisable(this)
    }
  }

  disabledRef = (e: any) => {
    this.disabledElement.e = e
    if (this.animation === CardAnimationState.positioningBeforeToggleOff) {
      this.keepPrevDisabledPosition()
      this.animation.enableDisable(this)
    }
  }

  toJSON() {
    return {
      tpnIntegration: this.tpnIntegration,
      placementTypeCount: this.placementTypeCount,
    }
  }

  @action.bound expandCollapse() {
    return this.animation.expandCollapse(this)
  }

  @action.bound enableDisable() {
    return this.animation.enableDisable(this)
  }

  @action.bound finishToggleOff() {
    return this.animation.finishToggleOff(this)
  }

  @action.bound finishToggledOn() {
    return this.animation.finishToggledOn(this)
  }

  @action.bound finishCollapsing() {
    return this.animation.finishCollapsing(this)
  }

  get isEnabled() {
    return this.animation.isEnabled
  }

  get isInActiveList() {
    return this.animation.isInActiveList
  }

  get isInDisabledList() {
    return this.animation.isInDisabledList
  }

  get isExpanded() {
    return this.animation.isExpanded
  }

  get isDisabled() {
    return !this.isEnabled
  }

  get isDisabledOrTogglingOff() {
    return this.isDisabled || this.animation.isTogglingOff
  }

  get isTogglingOff() {
    return this.animation.isTogglingOff
  }

  keepPrevActivePosition = () => {
    const {activeElement} = this
    if (activeElement.e != null) {
      const {x, y} = activeElement.e.getBoundingClientRect()
      activeElement.position = {x, y}
    }
  }

  keepPrevDisabledPosition = () => {
    const {disabledElement} = this
    if (disabledElement.e != null) {
      const {x, y} = disabledElement.e.getBoundingClientRect()
      disabledElement.position = {x, y}
    }
  }

  get isToggledOff() {
    return this.animation.isToggledOff
  }

  get isTogglingOn() {
    return this.animation.isTogglingOn
  }

  get isToggledOffOrTogglingOff() {
    return this.animation.isToggledOffOrTogglingOff
  }

  get diffX() {
    const {x: x1} = this.activeElement.position
    const {x: x2} = this.disabledElement.position
    return x1 - x2
  }

  get diffY() {
    const {y: y1} = this.activeElement.position
    const {y: y2} = this.disabledElement.position
    return y1 - y2
  }

  get supportsPlacements() {
    return this.tpnIntegration.supportsPlacements
  }

  get supportsUnits() {
    return this.tpnIntegration.supportsUnits
  }

  get supportsAndCompletePlacements() {
    const {placementTypeCount} = this
    return this.supportsPlacements && placementTypeCount.some(e => e.count > 0)
  }

  get completeParams() {
    return this.tpnIntegration.completeAppParams &&
      (!this.supportsPlacements || this.supportsAndCompletePlacements)
  }

  get incompleteParams() {
    return !this.completeParams
  }

  get id() {
    return this.key
  }

  get networkName() {
    return this.tpnIntegration.network.name
  }

  get networkIcon() {
    return this.tpnIntegration.network.icon
  }

  @computed get totalPlacements() {
    return this.placementTypeCount.reduce(
      (prev, current) => prev + current.count, 0
    )
  }

  get isNotProgrammatic() {
    return this.tpnIntegration.network.networkType.isNotProgrammatic
  }
}
