import {CSSProperties} from "react"

export type Breakpoint = "xs" | "sm" | "md" | "lg" | "xl"

export const breakpoints: Breakpoint[] = ["xs" , "sm" , "md" , "lg" , "xl"]

export function breakpointStyles(
  theme: {
    breakpoints: {
      up: (breakpoint: Breakpoint) => string
    }
  },
  sizes: (number | string)[],
  breakpoints: Breakpoint[],
  prefix = "",
  cb: (size: number | string, breakpoint: Breakpoint) => CSSProperties
) {
  return breakpoints.reduce(
    (globalStyles: Record<string, CSSProperties>, breakpoint) => {
      const styles: Record<string, CSSProperties> = {}
      for (const size of sizes) {
        styles[`${prefix}-${breakpoint}-${size}`] = cb(size, breakpoint)
      }
      if (breakpoint === "xs") {
        Object.assign(globalStyles, styles)
      } else {
        globalStyles[theme.breakpoints.up(breakpoint)] = styles
      }

      return globalStyles
    },
    {}
  )
}
