import {UserSession} from "@revdesk/model/build/userSession"
import {AppStatus} from "@revdesk/model/build/inventory/appStatus"
import {AppCardState} from "../app-card-state"
import {BrowseState} from "../browse/browseState"
import {BrowseActions, Filter, QueryResult} from "../browse"
import {CollinsFetch} from "../client"
import {autoBind} from "@revdesk/util"
import {EventEmitter} from "events"
import {action, runInAction} from "mobx"
import {ReactBottomListener} from "tsla-util/lib/dom/reactBottomListener"
import {hasEmitter, HasEmitter} from "tsla-util/lib/has-emitter"
import {AppManagementState} from "./appManagementState"

export interface AppManagementActionsParams {
  collinsFetch: CollinsFetch
  location: {
    search: () => Record<string, string>
  }
  state: AppManagementState
  appActions: {
    state: {
      userSession: UserSession
    }
  }
}

export interface AppFilter {
  search: Filter<string>
  platform: Filter<string>
  date: Filter<string>
  status: Filter<string>
  placementType: Filter<string>
}

export interface AppManagementActions extends AppManagementActionsParams, HasEmitter {
}

@hasEmitter
export class AppManagementActions implements HasEmitter {

  emitter = new EventEmitter()
  browseActions: BrowseActions<AppCardState, AppFilter>
  scrollListenerUnsubscribe: () => void

  constructor(params: AppManagementActionsParams) {
    Object.assign(this, params)

    this.browseActions = new BrowseActions<AppCardState, AppFilter>({
      state: BrowseState.empty<AppCardState, AppFilter>({
        search: new Filter<string>(""),
        platform: new Filter<string>(""),
        date: new Filter<string>(""),
        status: new Filter<string>(""),
        placementType: new Filter<string>("")
      }).changeLimit(50),
      fetchData: async () => {
        const raw = await this.collinsFetch.post("api/v2/app-cards", this.browseActions.state.query)
        return QueryResult.parse(raw, AppCardState.parse)
      }
    })

    const {publisherId} = this.location.search()
    // if (publisherId) {
    //   this.state.changePublisherId(publisherId)
    // }
    this.scrollListenerUnsubscribe = new ReactBottomListener({
      offset: (250 + 12) * 6,
      onReachBottom: this.reachedBottom,
      win: window,
      body: document.body
    }).subscribe(100)

  }

  reachedBottom = () => {
    if (!this.browseActions.state.fetchState.isLoading) {
      this.browseActions.fetchNextPageIfNeeded()
    }
  }

  @autoBind
  changeFilterType(filter: keyof AppFilter) {
    this.state.filter = filter
    this.browseActions.state.query.filters.$.search.value = ""
    this.browseActions.state.query.filters.$.placementType.value = ""
    this.browseActions.state.query.filters.$.platform.value = ""
    this.browseActions.state.query.filters.$.date.value = ""
    this.browseActions.state.query.filters.$.status.value = ""
  }

  onDuplicateClickOpen = () => {
    this.state.snackOpen = true
  }

  onDuplicateClickClose = () => {
    this.state.snackOpen = false
  }

  activateApp = async (appCard: AppCardState) => {
    const status = appCard.app.status.toggle
    appCard.app = appCard.app.copy({
      status
    })
    try {
      await this.collinsFetch.post("/api/apps/set-status", {
        id: appCard.app.id,
        status: status.id
      })
    } catch (e) {
      console.log(e)
    }
  }

  @action
  async removeApp(appCard: AppCardState) {
    const {app} = appCard
    appCard.removing = true
    try {
      await this.collinsFetch.post("/api/apps/set-status", {
        id: app.id,
        status: AppStatus.TEMP_DELETED.id
      })
      await runInAction(async () => {
        await this.browseActions.refreshPageOf(appCard)
      })
      appCard.removing = false
    } catch (e) {
      appCard.removing = false
    }
  }

  get selectedFilter(): Filter<string> {
    return this.browseActions.state.query.filters.$[this.state.filter as keyof AppFilter]
  }
}
