import CircularProgress from "@material-ui/core/CircularProgress"
import * as React from "react"
import {AutoCentered} from "../../components/autoCentered"
import {OnBoardBackground} from "../onboard"
import {AuthPage} from "./authPage"
import {SuspensePage} from "./suspensePage"

export interface SuspenseAuthPageProps<T extends React.ComponentType<any>> {
  promise: T
}

export class SuspenseAuthPage<T extends React.ComponentType<any>> extends React.Component<SuspenseAuthPageProps<T>> {
  render() {
    const {promise} = this.props
    return <AuthPage>
      <SuspensePage
        promise={promise}
        fallback={
          <OnBoardBackground>
            <AutoCentered>
              <CircularProgress/>
            </AutoCentered>
          </OnBoardBackground>
        }/>
    </AuthPage>
  }
}
