import {Interceptor, Route} from "../router"
import {boolMatch, stringMatch} from "@revdesk/util"
import {tryCatch} from "tsla-util/lib/try"
import {AppRoute, pathToRoute} from "../app-route"
import {AppActions} from "./appActions"

export function RouterInterceptor(appActions: AppActions): Interceptor {
  return (path, params, hash) => {
    const {router, isLoggedIn, userSession} = appActions.state
    const openRoutes = ["/login", "/reset-password", "/restore-password", "/sign-up"]
    const home = AppRoute.userHomePage(userSession)
    let forcePush = false
    boolMatch({
      True: () => {
        stringMatch({
          "/login": () => {
            const redirect = tryCatch(() => JSON.parse(params.redirect))
              .map(Route.parse).get
            if (redirect != null) {
              params = redirect.params
              path = redirect.path
              hash = redirect.hash
            } else {
              path = home
            }
          },
          Else: () => {}
        })(path)
        if (!router.hasPath(path)) {
          path = home
        }
      },
      False: () => {
        stringMatch({
          Else: () => {
            if (openRoutes.every(p => p !== path)) {
              params = {
                redirect: JSON.stringify({path, params, hash})
              }
              path = "/login"
              forcePush = true
            } else if (path === "" || path === "/" || !router.hasPath(path)) {
              path = home
            }
          }
        })(path)
      }
    })(isLoggedIn)

    const appRoute = pathToRoute[path]
    if (appRoute != null) {
      const {authorized} = appRoute
      if (authorized != null && !authorized(appActions.state.userSession)) {
        path = home
      }
      params = appRoute.cleanParams(params)
    }

    if (params.appId != null) {
      appActions.fetchGlobalApp(params.appId)
    }

    return {forcePush, path, params, hash}
  }
}
