import {Theme} from "@material-ui/core/styles/createMuiTheme"
import {CollinsFetch, IStateServiceBridge} from "@revdesk/state-actions/build/client"
import {CollinsCluster} from "@revdesk/util/build/collinsCluster"
import {shallowParse} from "@revdesk/util/build/query-string/shallowParse"
import {History} from "history"
import createBrowserHistory from "history/createBrowserHistory"
import "../../extensions/material-ui/createPalette"
import {nfpLight, onboardDark} from "../../theme"
import {TPNFetchClient} from "../api"
import {AppActions} from "@revdesk/state-actions/build/app/appActions"
import {AppState} from "@revdesk/state-actions/build/app/appState"

declare global {
  interface Window {
    __cluster: string
  }
}

export const colors = {
  GREEN: "#3fc59d",
  YELLOW: "#ffcb11",
  PINK: "#ff2267",
  PURPLE: "#9553f5",
  DARK_PURPLE: "#2b0468",
  BLUE: "#3e53de",
  DARK_BLUE: "#1d0047",
}

export class Container {
  cookies = {
    get(cookieName: string) {
      const [key, value] = document.cookie.split(";")
        .map(s => s.trim())
        .map(s => s.split("="))
        .find(s => s[0] === cookieName) || ["", ""]

      return decodeURIComponent(value)
    },
    set(key: string, value: string) {
      const d = new Date()
      d.setTime(d.getTime() + (7 * 24 * 60 * 60 * 1000))
      document.cookie = `${key}=${value}; expires=${d.toUTCString()}`
    }
  }

  collinsFetch = new CollinsFetch(this.cookies)

  tpnClient = new TPNFetchClient({
    fetch: this.collinsFetch
  })

  stateService: IStateServiceBridge = {
    go: async (to, params) => {
      this.appActions.router.navTo(to, params)
    },
    search: async (params) => {
      this.appActions.router.search(params)
    }
  }

  location = {
    pathname: () => this.history.location.pathname,
    search: () => shallowParse(this.history.location.search),
    hash: () => this.history.location.hash
  }

  history: History = createBrowserHistory()

  collinsCluster = CollinsCluster.parse(window.__cluster)

  palette = Object.keys(colors).map(k => (colors as any)[k])

  appActions = new AppActions({
    collinsFetch: this.collinsFetch,
    location: this.location,
    cookies: this.cookies,
    history: this.history,
    tpnClient: this.tpnClient,
    state: AppState.empty(),
    collinsCluster: this.collinsCluster,
    palette: this.palette,
    stateService: this.stateService,
  })

  nfpLight: Theme = nfpLight
  onboardDark: Theme = onboardDark

  constructor() {
    this.history.listen((location) => {
      const {pathname, search, hash} = container.location
      this.appActions.router.navTo(pathname(), search(), {
        fromListener: true,
        hash: hash()
      })
    })

    this.collinsFetch.interceptors.push(this.appActions.httpInterceptor)
  }
}

export const container = new Container()
