import {lazyVal} from "@revdesk/util"
import {DataType, NestedItem} from "@revdesk/util"
import {dataClass, DataClass} from "data-class-copy/lib"
import {applyMixins} from "ts-trait/build"
import {DataTypes} from "../dataTypes"
import {GroupType} from "../groupType"
import {Searchable} from "../searchable"
import {Action} from "./action"
import {Permission} from "./permission"
import {Resource} from "./resource"

export interface RollParams {
  id: string;
  name: string;
  groupType: GroupType;
  permissions: Array<Permission<any>>;
  lead: boolean;
}

export interface Roll extends RollParams, DataClass<RollParams>, NestedItem<any, any> {

}

@dataClass
@applyMixins([NestedItem, Searchable])
export class Roll implements NestedItem<any, any> {
  static mini = (roll: Roll) => roll.mini

  static getName(roll: Roll) {
    return roll.name
  }

  static EMPTY = new Roll({
    id: "",
    name: "",
    groupType: GroupType.UNKNOWN,
    permissions: [],
    lead: false,
  })

  static parse(params: any = {}): Roll {
    const id = params.id || params.roleId
    return Roll.EMPTY.copy({
      id: id != null ? String(id) : "",
      name: params.name != null ? String(params.name) : "",
      groupType: GroupType.parse(params.groupType),
      lead: Boolean(params.lead),
      permissions: (params.permissions || params.roleResources || []).map(Permission.parse),
    })
  }

  constructor(params: RollParams) {
    Object.assign(this, params)
  }

  @lazyVal
  get actions() {
    return Action.actionsFromPermissions(this.permissions)
  }

  get dataType(): DataType<any> {
    return DataTypes.ROLL
  }

  get mini(): Roll {
    return Roll.EMPTY.copy({id: this.id})
  }

  isDifferentGroupType(other: Roll) {
    return this.groupType !== other.groupType
  }

  get userActions() {
    return this.actions.find(e => e.resource.id === Resource.USER.id)
  }

  get loginAsActions() {
    return this.actions.find(e => e.resource.id === Resource.loginAs.id)
  }

  get featuresAccessActions() {
    return this.actions.filter(e => !e.resource.isLoginAsOrUserManagement)
  }

}
