import Fade, {FadeProps} from "@material-ui/core/Fade"
import * as React from "react"

export interface MenuItemFade extends FadeProps {
  children?: React.ReactNode
}

export function MenuItemFade({in: inn, style, children, ...rest}: MenuItemFade) {
  return <Fade in={inn} {...rest} style={{opacity: inn ? undefined : 1, ...style}}>
    {children}
  </Fade>
}
