import * as React from "react"
import {HTMLAttributes, ReactElement} from "react"
import {clone} from "./clone"

export interface OuterClickProps extends HTMLAttributes<any> {
  onOuterClick: any
  allowHidden?: boolean
  condition?: boolean
  children: React.ReactNode
}

export class OuterClick extends React.Component<OuterClickProps, any> {
  element: HTMLElement

  componentDidMount() {
    document.addEventListener("mousedown", this.onDocClick)
  }

  componentWillUnmount() {
    document.removeEventListener("mousedown", this.onDocClick)
  }

  onDocClick = (event: any) => {
    const {allowHidden, onOuterClick} = this.props
    const bounds = event.target.getBoundingClientRect()
    const condition = this.props.condition != null ? this.props.condition : true
    const visible = allowHidden || (bounds.width && bounds.height && (bounds.left >= 0 || bounds.right >= 0))
    if (visible && condition && !jQuery.contains(this.element, event.target)) {
      onOuterClick(event)
    }
  }

  handleRef = (element: any) => {
    this.element = element
  }

  render() {
    const {onOuterClick, allowHidden, condition, children, ...props} = this.props
    return <div ref={this.handleRef} {...props}>
      {children}
    </div>
  }
}
