import {ParamValue, ValueType} from "@revdesk/model/build/mediation"
import {dataClass, DataClass} from "data-class-copy"
import {ComposibleValidatable, ValidationResponse} from "formstate"
import {FieldState, firstErrorIfArray, FormState} from "../extensions/formstate"

export type ParamValueValidator<T extends ValueType = ValueType> =
  (field: ParamValueFieldState<T>) => ValidationResponse | ValidationResponse[] | Promise<ValidationResponse>


export type ParamValueFormState = FormState<ParamValueFieldState[]>

export interface ParamValueFieldStateParams<T extends ValueType = ValueType> {
  paramValue: ParamValue<T>
}

export interface ParamValueFieldStateConfig<T extends ValueType = ValueType> extends ParamValueFieldStateParams<T> {
  validators: ParamValueValidator<T>[]
}

export interface ParamValueFieldState<T extends ValueType = ValueType> extends ParamValueFieldStateParams<T>,
  DataClass<ParamValueFieldStateParams<T>> {
}

@dataClass
export class ParamValueFieldState<T extends ValueType = ValueType> extends FieldState<T>
  implements ComposibleValidatable<T> {

  static fromParamValues(paramsValues: ParamValue[], validators: ParamValueValidator[]): ParamValueFormState {
    return new FormState(
      paramsValues.map(e => ParamValueFieldState.fromParamValue(e, validators))
    )
  }

  static fromParamValue<T extends ValueType = ValueType>(
    paramValue: ParamValue<T>,
    validators: ParamValueValidator<T>[]
  ) {
    return new ParamValueFieldState({paramValue, validators})
  }

  static empty = new ParamValueFieldState<ValueType>({
    paramValue: ParamValue.empty,
    validators: []
  })

  constructor(params: ParamValueFieldStateConfig<T>) {
    super(params.paramValue.value)
    const {validators, ...rest} = params
    this.disableAutoValidation().validators(
      ...validators.map(validator => () => firstErrorIfArray(validator(this)))
    )
    Object.assign(this, rest)
  }

  get label() {
    return this.paramValue.param.name
  }

  get paramType() {
    return this.paramValue.param.type
  }

  get toParamValue() {
    return this.paramValue.copy({
      value: this.value
    })
  }

  clear() {
    this.value = this.paramValue.param.defaultValue
  }

  get stringifiedValue() {
    return this.paramValue.param.type.stringify(this.value)
  }

  validateParamValidations() {
    return this.paramValue.param.validators.map(validator => validator(this.value))
  }
}
