import ExpansionPanel, {ExpansionPanelProps} from "@material-ui/core/ExpansionPanel"
import ExpansionPanelDetails from "@material-ui/core/ExpansionPanelDetails"
import ExpansionPanelSummary from "@material-ui/core/ExpansionPanelSummary"
import Grid from "@material-ui/core/Grid"
import withStyles from "@material-ui/core/styles/withStyles"
import ExpandMoreIcon from "@material-ui/icons/ExpandMore"
import * as React from "react"
import {Color,Margin} from "../../layout"
import {Rotate} from "../components/animation/rotate"
import {ButtonBase} from "../components/buttonBase"
import {MenuItemFade} from "./menuItemFade"
import {SideMenuActions} from "@revdesk/state-actions/build/side-menu/sideMenuActions"
import {FontSize} from "../../layout/fontSize"

const PanelSummary = withStyles({
  root: {
    padding: "0 12px 0 16px",
  },
  content: {
    margin: 0,
    minWidth: 0,
    "&$expanded": {
      margin: "12px 0"
    },
    "& > :last-child": {
      paddingRight: 0
    },
  },
  expanded: {
    margin: 0
  }
})(ExpansionPanelSummary)

const PanelDetails = withStyles({
  root: {
    padding: "0 24px 12px"
  }
})(ExpansionPanelDetails)

export interface MenuItemExpansionPanelProps extends ExpansionPanelProps {
  actions: SideMenuActions
  summary?: React.ReactNode
  details?: React.ReactNode
  active: boolean
  onExpand: () => any
}

export class MenuItemExpansionPanel extends React.Component<MenuItemExpansionPanelProps> {
  render() {
    const {summary, details, actions, active, style, onExpand, expanded, ...rest} = this.props
    const {expanded: menuExpanded} = actions.state
    return <ExpansionPanel expanded={expanded} elevation={0} square
                           style={{background: "transparent", ...style}} {...rest}>
      <PanelSummary>
        <MenuItemFade in={active}>
          <Grid container direction="row" alignItems="center" wrap="nowrap" onClick={() => onExpand()}>
            <Grid item zeroMinWidth container style={{flex: 1}}>
              {summary}
            </Grid>

            {menuExpanded ? <Rotate in={expanded} timeout={150}>
              <Margin xsl="auto">
                <Grid item>
                  <ButtonBase>
                    <FontSize fontSize="18">
                      <Color color="white">
                        <ExpandMoreIcon viewBox="0 0 24 24"/>
                      </Color>
                    </FontSize>
                  </ButtonBase>
                </Grid>
              </Margin>
            </Rotate> : undefined}
          </Grid>
        </MenuItemFade>
      </PanelSummary>

      {details && <PanelDetails>
        <Grid container direction="column" wrap="nowrap">
          {details}
        </Grid>
      </PanelDetails>}
    </ExpansionPanel>
  }
}
