import {FormState as BaseFormState, ValidatableMapOrArray} from "formstate"
import {action, observable} from "mobx"
import {Submit} from "./submit"
import "./validateIsolateForm"
import forEach = require("lodash/forEach")

type ValidateResult = { hasError: true, error: string } | { hasError: false, error: undefined }

export class FormState<T extends ValidatableMapOrArray> extends BaseFormState<T> {
  @observable submitted = false

  constructor(_initValue: T) {
    super(_initValue)
    this.disableAutoValidation()
  }

  @action setSubmitted() {
    this.submitted = true
    forEach(this.$, e => {
      if (Submit.isSubmit(e)) {
        e.setSubmitted()
      }
    })
  }

  @action setUnsubmitted() {
    this.submitted = false
    forEach(this.$, e => {
      if (Submit.isSubmit(e)) {
        e.setUnsubmitted()
      }
    })
  }

  @action
  async setSubmittedAndValidate(): Promise<ValidateResult> {
    this.setSubmitted()
    const error = await this.validateIsolate()
    return error ? {
      error,
      hasError: true as true
    } : {
      error: undefined,
      hasError: false as false
    }
  }
}
