const idToConnectivityType: Record<string, ConnectivityType> = {}

export interface ConnectivityTypeParams {
  id: string
  name: string
}

export interface ConnectivityType extends ConnectivityTypeParams {

}

export class ConnectivityType {
  static parse(params: string) {
    return idToConnectivityType[params] || ConnectivityType.unknown
  }

  static unknown = new ConnectivityType({
    id: "",
    name: "Unknown connectivity"
  })

  static cell = new ConnectivityType({
    id: "Cellular",
    name: "Cellular Network"
  })

  static wifi = new ConnectivityType({
    id: "WIFI",
    name: "Wifi"
  })

  static selectable = [ConnectivityType.wifi, ConnectivityType.cell]

  constructor(params: ConnectivityTypeParams) {
    Object.assign(this, params)
    idToConnectivityType[params.id] = this
  }

  toJSON() {
    return this.id
  }
}
