import {EventEmitter} from "@revdesk/util"
import {applyMixins} from "ts-trait/build"
import {SelfStateHolder, StateHolder} from "./state.holder"

export interface ActionsParams<T> {
  stateHolder: StateHolder<T>;
}

export interface Actions<T> extends ActionsParams<T> {

}

export abstract class Actions<T> {
  emitter!: EventEmitter

  Actions$super(params: ActionsParams<T>) {
    this.stateHolder = params.stateHolder || new SelfStateHolder(this.initialState)
    this.emitter = new EventEmitter()
  }

  abstract get initialState(): T;

  get state() {
    return this.stateHolder.state
  }

  set state(t: T) {
    this.stateHolder.setState(t)
  }

  setState(t: T, event?: string) {
    // console.log(this.constructor.name, event, await measureTimeMillis(() => {
    //   this.stateHolder.setState(t, event)
    // }))
    this.stateHolder.setState(t, event)
    if (event) {
      this.emitter.emit(event, t)
    }
  }
}

const mixin = applyMixins([Actions])

export function actions(target: any) {
  mixin(target)
}
