const keyBy = require("lodash/keyBy")
const uniqBy = require("lodash/uniqBy")

export class CheckableList {
  checked: Record<string, any> = {}

  constructor(public trackBy: string, list: any[]) {
    this.checked = keyBy(list, trackBy)
  }

  put(item: any) {
    const {trackBy, checked} = this
    checked[item[trackBy]] = item
  }

  remove(item: any) {
    const {trackBy, checked} = this
    checked[item[trackBy]] = null
  }

  exists(item: any) {
    const {trackBy, checked} = this
    return !!checked[item[trackBy]]
  }

  toggleItem(item: any) {
    if (this.exists(item)) {
      this.remove(item)
    } else {
      this.put(item)
    }
  }

  toArray() {
    const {checked} = this
    return Object.keys(checked).map(key => checked[key]).filter(e => e)
  }

  unshiftTo(arr: any[]) {
    const {trackBy} = this
    return uniqBy([...this.toArray(), ...arr], trackBy)
  }
}
