import {NestedItem} from "@revdesk/util"
import {dataClass, DataClass} from "data-class-copy/lib"
import {applyMixins} from "ts-trait/build"
import {parseArray, parseString} from "tsla-util/lib/parse"
import {DataTypes} from "../dataTypes"
import {TPNIntegrationInstance, TPNParamInstance} from "../mediation"
import {Param} from "../mediation/param"
import {Searchable} from "../searchable"
import {NetworkType} from "./networkType"

export interface NetworkSchema {
  id: number | string
  name: string
  icon: string
  displayName: string
  networkType: string | number
  filterId: string | number
  disabled: boolean
  isVisible: boolean
  groupId: string | number
}

export interface NetworkInstance extends NetworkSchema {
  tpnIntegration?: TPNIntegrationInstance
  tpnParams?: TPNParamInstance[]
}

export interface NetworkParams {
  id: string
  key: string
  name: string
  icon: string
  filterId: number
  networkType: NetworkType
  tpnParams: Param[]
  groupId: string
}

export interface Network extends NetworkParams, NestedItem<any, any>, DataClass<Network> {

}

@dataClass
@applyMixins([NestedItem, Searchable])
export class Network implements NestedItem<any, any> {
  static EMPTY = new Network({
    id: "",
    key: "",
    name: "",
    icon: "",
    filterId: 0,
    networkType: NetworkType.unknown,
    tpnParams: [],
    groupId: ""
  })

  static parse(params: any = {}) {
    const name = params.displayName || params.name
    const key = params.key || params.name
    const filterId = params.filterId || params.targetingFilter
    return new Network({
      id: params.id != null ? String(params.id) : "",
      key: parseString(key),
      name: name != null ? String(name) : "",
      icon: parseString(params.icon),
      filterId: Number(filterId),
      networkType: NetworkType.parse(params.networkType),
      tpnParams: parseArray(params.tpnParams, Param.parse),
      groupId: parseString(params.groupId)
    })
  }

  get targetingFilter() {
    return this.filterId
  }

  constructor(params: NetworkParams) {
    Object.assign(this, params)
  }

  get mini(): Network {
    return Network.EMPTY.copy({
      id: this.id
    })
  }

  get parent(): any {
    return null
  }

  get children() {
    return [] as any[]
  }

  changeChildren() {
    return this
  }

  get type() {
    return "network"
  }

  get hash() {
    return this.type + "_" + this.id
  }

  get dataType() {
    return DataTypes.NETWORK
  }

  get isNotMediated() {
    return !this.isMediated
  }

  get isMediated() {
    return this.networkType.isMediated
  }

  get isProgMediation() {
    return this.networkType.isProgMediation
  }
}
