import {Constructor, Fun1} from "@revdesk/base-types";
import {isClass} from "./isClass";

export type Matcher<E, T> = [Guard<E>, Fun1<E, T>]

export type InstanceOf<E> = (e: any) => e is E
export type Guard<E> = Constructor<E> | InstanceOf<E>

export function typeMatch<T, E1=any>(
  m1: Matcher<E1, T>,
  orElse?: () => T
): Fun1<E1, T>;

export function typeMatch<T, E1=any, E2=any>(
  m1: Matcher<E1, T>,
  m2: Matcher<E2, T>,
  orElse?: () => T
): Fun1<E1 | E2, T>;

export function typeMatch<T, E1=any, E2=any, E3=any>(
  m1: Matcher<E1, T>,
  m2: Matcher<E2, T>,
  m3: Matcher<E3, T>,
  orElse?: () => T
): Fun1<E1 | E2 | E3, T>;

export function typeMatch<T>(...args: any[]) {
  const i = args.length - 1
  const lastIndex = typeof args[i] === "function" ? i : args.length
  const pattern = args.slice(0, lastIndex) as Matcher<any, T>[]
  const orElse = args[lastIndex]
  return (e: any): T => {
    for (const [type, fn] of pattern) {
      if (canDoInstanceOf(type) && e instanceof type || !isClass(type) && type(e)) {
        return (fn as any)(e)
      }
    }
    return orElse && orElse()
  }
}

function canDoInstanceOf(e: Guard<any>): e is Constructor<any> {
  return e.prototype != null
}
