const idToFreqUnit: Record<string, FreqUnit> = {}

export interface FreqUnitParams {
  id: string
  name: string
  plural: string
}

export interface FreqUnit extends FreqUnitParams {}

export class FreqUnit {
  static parse(params: string) {
    return idToFreqUnit[params] || FreqUnit.unknown
  }

  static unknown = new FreqUnit({
    id: "",
    name: "Unknown frequency time unit",
    plural: "Unknown frequency time unit",
  })

  static day = new FreqUnit({
    id: "1",
    name: "day",
    plural: "days"
  })

  static hour = new FreqUnit({
    id: "2",
    name: "hour",
    plural: "hours"
  })

  static minute = new FreqUnit({
    id: "3",
    name: "minute",
    plural: "minutes"
  })

  static second = new FreqUnit({
    id: "4",
    name: "second",
    plural: "seconds"
  })

  static selectable = [FreqUnit.second, FreqUnit.minute, FreqUnit.hour, FreqUnit.day]
  static pacingSelectable = [FreqUnit.second, FreqUnit.minute, FreqUnit.hour]

  constructor(params: FreqUnitParams) {
    Object.assign(this, params)
    idToFreqUnit[params.id] = this
  }

  get isUnknown() {
    return this === FreqUnit.unknown
  }

  toJSON() {
    return this.id
  }

  pluralize(value: number) {
    return value > 1 ? this.plural : this.name
  }
}
