export type AndroidMime = "video/webm" | "video/3gpp" | "video/3gpp2" | "video/mpeg" | "video/mpg";
export type iOSMime = "video/mp4" | "video/3gpp2" | "video/mov";
export type MobileWebMime = "video/mp4";

export type MimeValue = AndroidMime | iOSMime | MobileWebMime;

export interface Mime {
  id: MimeValue;
  name: string;
}

export class Mime {

  static getPreSelected(): any {
    return [];
  }

  static VIDEO_MP4 = new Mime({
    id: "video/mp4",
    name: "video/mp4",

  });

  static VIDEO_MOV = new Mime({
    id: "video/mov",
    name: "video/mov",

  });

  static VIDEO_WEBM = new Mime({
    id: "video/webm",
    name: "video/webm",

  });

  static VIDEO_3GPP = new Mime({
    id: "video/3gpp",
    name: "video/3gpp",

  });

  static VIDEO_3GPP2 = new Mime({
    id: "video/3gpp2",
    name: "video/3gpp2",

  });

  static VIDEO_MPEG = new Mime({
    id: "video/mpeg",
    name: "video/mpeg",

  });

  static VIDEO_MPG = new Mime({
    id: "video/mpg",
    name: "video/mpg",

  });

  id: MimeValue;
  name: string;

  constructor({id, name}: Mime) {
    this.id = id;
    this.name = name;
  }
}
