import {DataClass, dataClass} from "data-class-copy"
import {ParamValue} from "./paramValue"
import {TPNIntegration} from "./tpnIntegration"

export interface IntegrationParamParams {
  tpnIntegration: TPNIntegration
  paramValue: ParamValue
}

export interface IntegrationParam extends IntegrationParamParams, DataClass<IntegrationParamParams> {

}

@dataClass
export class IntegrationParam {
  static empty = () => new IntegrationParam({
    paramValue: ParamValue.empty,
    tpnIntegration: TPNIntegration.empty
  })

  static parse(params: any = {}) {
    return new IntegrationParam({
      paramValue: ParamValue.parse(params.paramValue),
      tpnIntegration: TPNIntegration.parse(params.tpnIntegration)
    })
  }

  constructor(params: IntegrationParamParams) {
    Object.assign(this, params)
  }
}
