import {Validator} from "formstate"

// tslint:disable
export const EMAIL_REGEXP = /^(?=.{1,254}$)(?=.{1,64}@)[-!#$%&'*+/0-9=?A-Z^_`a-z{|}~]+(\.[-!#$%&'*+/0-9=?A-Z^_`a-z{|}~]+)*@[A-Za-z0-9]([A-Za-z0-9-]{0,61}[A-Za-z0-9])?(\.[A-Za-z0-9]([A-Za-z0-9-]{0,61}[A-Za-z0-9])?)*$/
export const SPECIAL_REGEXP = /[!"#$%&'()*+,\-./:;<=>?@[\\\]^_`{|}~]/
export const SPACE_REGEXP = /\s+/
const ALPHA_NUMERIC_REGEXP = /^[a-zA-Z0-9.@\-]*$/
export const NON_UNICODE_REGEXP = /^[\u0000-\u007F]+$/i

// tslint:enable

export class CommonValidators {
  static messages = {
    required: "This field is required"
  }

  static nonEmptyCustom = (message: string) => (value: string) => (value.trim().length > 0 ? null : message)

  static skipValidationIfEmpty = <T>(validator: Validator<T>, fn: (t: T) => string): Validator<T> => (
    value => fn(value).trim().length > 0 ? validator(value) : null
  )

  static max = (limit: number) => (value: number) => (value > limit ? `Enter a number ${limit} or smaller` : null)
  static min = (limit: number) => (value: number) => (value < limit ? `Enter a number ${limit} or greater` : null)
  static isInteger = (value: number) => !Number.isInteger(value) && "Number must be an integer"

  static nonEmpty = CommonValidators.nonEmptyCustom(CommonValidators.messages.required)
  static required = CommonValidators.nonEmpty
  static notNull = <T>(value: T | null) => value == null ? CommonValidators.messages.required : null
  static minLength = (length: number) => (val: string) =>
    val && val.length < length && `Please enter at least ${length} characters`
  static maxLength = (length: number) => (val: string) =>
    val && val.length > length && `Please enter less then ${length} characters`
  static email = (val: string) => val && !EMAIL_REGEXP.test(val) && "Invalid e-mail format"
  static noSpecial = (value: string) =>
    SPECIAL_REGEXP.test(value) ? "This field should be without special characters" : null

  static alphaNumeric = (val: string) => !ALPHA_NUMERIC_REGEXP.test(val) && "Please enter only letters and numbers"
  static englishOnly = (val: string) => !NON_UNICODE_REGEXP.test(val) && "Please use only English characters"
  static noSpace = (val: string) => SPACE_REGEXP.test(val) && "This field should be without blank spaces"
  static delegate = <T>(
    other: Validator<T>,
    alternativeMessage: string
  ) => (value: T) => other(value) ? alternativeMessage : undefined
}
