import {Theme} from "@material-ui/core/styles/createMuiTheme"
import withStyles, {CSSProperties} from "@material-ui/core/styles/withStyles"
import * as classNames from "classnames"
import * as React from "react"
import {Classes} from "../util"

const hoverStyles: Record<string, CSSProperties> = {}

const colorStyles = (theme: Theme) => {
  const style = {
    darkBlue: {
      backgroundColor: theme.palette.custom.darkBlue
    },
    green: {
      backgroundColor: theme.palette.custom.green
    },
    grey: {
      backgroundColor: theme.palette.custom.grey
    },
    grey3: {
      backgroundColor: theme.palette.custom.grey3
    },
    grey6: {
      backgroundColor: theme.palette.custom.grey6
    },
    grey8: {
      backgroundColor: theme.palette.custom.grey8
    },
    lightGrey: {
      backgroundColor: theme.palette.custom.lightGrey
    },
    white: {
      backgroundColor: theme.palette.common.white
    },
    midGrey: {
      backgroundColor: theme.palette.custom.midGrey
    },
    unset: {
      backgroundColor: "unset"
    }
  }

  Object.keys(style).forEach(key => {
    hoverStyles[`${key}_hover`] = {
      "&:hover": {
        backgroundColor: style[key as keyof typeof style].backgroundColor,
      }
    }
  })

  return style
}

export interface BackgroundProps<P extends { className?: string }> {
  color?: keyof ReturnType<typeof colorStyles>
  hover?: boolean
  children: React.ReactElement<P>
  classes?: Classes<typeof colorStyles>
  className?: string
}

export const Background = withStyles(
  (theme: Theme) => ({...colorStyles(theme), ...hoverStyles}), {
    name: "Background",
    index: 1
  }
)(
  function Background<P extends { className?: string }>(props: BackgroundProps<P>) {
    const {color = "darkBlue", className: classNameProp, children, classes, hover} = props
    const {type: WrappedComponent, props: wrappedProps} = children
    const className = classNames(classNameProp, wrappedProps.className, classes![color], {
      [(classes as Classes<typeof hoverStyles>)[`${color}_hover`]]: hover
    })
    return <WrappedComponent
      {...wrappedProps}
      className={className}
    />
  }
)
