export interface SkipType {
  id: SkipTypeValue,
  name: string
}
export type SkipTypeValue = -1 | 0 | 5 | 10 | 15;

export class SkipType {

  static CANT_SKIP = new SkipType({
    id: -1,
    name: "No skip"
  });

  static IMMEDIATE = new SkipType({
    id: 0,
    name: "At start"
  });

  static FIVE = new SkipType({
    id: 5,
    name: "Short Form"
  });

  static TEN = new SkipType({
    id: 10,
    name: "After 10 seconds -- Not supported"
  });

  static FIFTEEN = new SkipType({
    id: 15,
    name: "After 15 seconds -- Not supported"
  });

  constructor(params: SkipType) {
    Object.assign(this, params)
  }
}
