import {applyMixins} from "ts-trait/build"
import {Unit} from "../../inventory/unit"
import {UnitType} from "../unitType";
import {BaseUnit, BaseUnitParams} from "./BaseUnit"
import {FilteredUnitJS} from "./FilteredUnit"

const {BANNER, INTERSTITIAL, MRECT} = UnitType

export interface DisplayUnitJS extends BaseUnitParams, FilteredUnitJS {
  isFloating: boolean;
  close?: boolean;
  hide?: number;
  refresh?: number;
  requiredHeight?: number;
  requiredWidth?: number;
}

export interface DisplayUnit extends BaseUnit, DisplayUnitJS {

}

@applyMixins([BaseUnit])
export class DisplayUnit implements BaseUnit {

  static fromJS(display: DisplayUnitJS = DisplayUnit.DEFAULT_DISPLAY_UNIT) {
    return new DisplayUnit(display)
  }

  static defaultByType(unitType: UnitType) {
    const id = unitType.id
    switch (id) {
      case BANNER.id:
      case INTERSTITIAL.id:
        return DisplayUnit.DEFAULT_BY_TYPE[id]
      default:
        return DisplayUnit.DEFAULT_DISPLAY_UNIT
    }
  }

  static DEFAULT_DISPLAY_UNIT: DisplayUnitJS = {
    ...BaseUnit.DEFAULT_BASE_UNIT,
    isFloating: false,
    hide: 5,
    requiredHeight: 250,
    requiredWidth: 300,
    filteredAPIs: []
  }

  static DEFAULT_BY_TYPE: any = {
    [BANNER.id]: {
      ...DisplayUnit.DEFAULT_DISPLAY_UNIT,
      close: false,
      fullScreen: false,
    },
    [INTERSTITIAL.id]: {
      ...DisplayUnit.DEFAULT_DISPLAY_UNIT,
      close: true,
      fullScreen: true,
      hide: null
    }
  }

  constructor(params: DisplayUnitJS) {
    Object.assign(this, params)
  }

  toJS(unit: Unit) {
    const display: DisplayUnit = {
      ...BaseUnit.prototype.toJS.call(this, unit),
      fullScreen: unit.isFullScreenUnit(),
    }
    if (display.fullScreen) {
      display.pos = 7
    }
    if (unit.refreshable() && this.refresh != null) {
      display.refresh = this.refresh
    }
    if (unit.closeable()) {
      display.close = this.close
    }
    if (unit.hideable()) {
      display.hide = this.hide
    }
    if (unit.unitType.id === MRECT.id) {
      display.requiredHeight = 250
      display.requiredWidth = 300
    }
    if (unit.supportsFloating()) {
      display.isFloating = this.isFloating
    }
    return display
  }

  toServerJS(unit: Unit) {
    const value = {
      ...BaseUnit.prototype.toServerJS.call(this, unit),
      fullScreen: unit.isFullScreenUnit()
    }
    if (value.fullScreen) {
      value.pos = 7
    }
    if (unit.unitType.id === MRECT.id) {
      value.requiredHeight = 250
      value.requiredWidth = 300
    }
    return value
  }
}
