import {DataClass, dataClass} from "data-class-copy"
import {FormState} from "formstate"
import {observable, action} from "mobx"
import {Filter, FilterValue} from "./filter"
import "./formStateToJSON"
import {Pagination} from "./pagination"
import {Sort} from "./sort"
import mapValues = require("lodash/mapValues")

export type Filters<T> = Record<keyof T, Filter<any>>

export interface QueryParams<T extends Filters<T>> {
  pagination: Pagination
  filters: FormState<T>
  defaultFilters: T
  sorts: Record<string, Sort>
}

export interface Query<T extends Filters<T>> extends QueryParams<T>,
  DataClass<QueryParams<T>> {
}

@dataClass
export class Query<T extends Filters<T>> {
  static empty = <T extends Filters<T>>(defaultFilters: T) => new Query<T>({
    pagination: Pagination.empty(),
    filters: new FormState(defaultFilters),
    defaultFilters,
    sorts: {}
  })

  static parse<T extends Filters<T>>(
    params: any = {},
    defaultFilters: T,
    providedFilters?: Record<keyof T, FilterValue>
  ) {
    return new Query<T>({
      pagination: Pagination.parse(params.pagination),
      defaultFilters,
      sorts: mapValues(
        params.sorts || {},
        Sort.parse
      ),
      filters: new FormState<T>(
        Object.assign(
          {},
          defaultFilters,
          mapValues(
            providedFilters,
            Filter.parse
          ) as T
        )
      ),
    })
  }

  @observable observable!: T

  constructor(params: QueryParams<T>) {
    Object.assign(this, params)
  }

  get limit() {
    return this.pagination.limit
  }

  changeLimit(limit: number) {
    return this.mutate({
      pagination: this.pagination.changeLimit(limit)
    })
  }

  @action reset() {
    return this.mutate({
      pagination: this.pagination.moveToPage(0),
      filters: new FormState(this.defaultFilters),
      sorts: {}
    })
  }
}

