import {Fun1} from "@revdesk/base-types"
import {AccessLevel} from "./access.level"
import {Action} from "./action"
import {ListResource} from "./listResource"
import {Permissions} from "./permissions"

export type ResourceID = keyof Permissions | ""

export interface ResourceParams<T> {
  id: ResourceID;
  name: string;
  toData: Fun1<any, T>;
  changeActionAccessLevel: (action: Action<T>, accessLevel: AccessLevel, newData?: T, defaultData?: T) => Action<T>;
  emptyData: () => T;
  canRead: (action: Action<T>, data: T) => boolean
  canWrite: (action: Action<T>, data: T) => boolean
}

export interface Resource<T> extends ResourceParams<T> {

}

function changeActionAccessLevel<T>(action: Action<T>, accessLevel: AccessLevel) {
  return action.changeAccessLevel(accessLevel)
}

const values: Record<ResourceID, Resource> = {} as any

export class Resource<T = any> {
  static get selectableValues() {
    return Object.keys(values).map((key) => values[key as ResourceID]).filter(k => k.id != "")
  }

  static UNKNOWN = new Resource({
    id: "",
    name: "",
    toData: () => "",
    changeActionAccessLevel: action => action,
    emptyData: () => "",
    canRead: (action) => action.read != null,
    canWrite: (action) => action.write != null
  })

  static USER = new Resource<string[]>({
    id: "user",
    name: "User Management",
    toData: t => t,
    emptyData: () => [],
    changeActionAccessLevel: (action, accessLevel, newData, defaultData) => {
      return new ListResource({action, accessLevel, newData: newData!, defaultData: defaultData!}).applyExclude()
    },
    canRead: (action, roles) => action.read != null && action.read.data.containsNone(roles),
    canWrite: (action, roles) => action.write != null && action.write.data.containsNone(roles),
  })

  static user = Resource.USER

  static ASSIGNMENT = new Resource({
    id: "assignment",
    name: "Assignment",
    toData: t => t,
    changeActionAccessLevel,
    emptyData: () => "",
    canRead: (action) => action.read != null,
    canWrite: (action) => action.write != null
  })

  static TIPALTI = new Resource({
    id: "tipalti",
    name: "Finance",
    toData: t => t,
    changeActionAccessLevel,
    emptyData: () => "",
    canRead: (action) => action.read != null,
    canWrite: (action) => action.write != null
  })

  static INVENTORY = new Resource({
    id: "inventory",
    name: "Inventory Management",
    toData: t => t,
    changeActionAccessLevel,
    emptyData: () => "",
    canRead: (action) => action.read != null,
    canWrite: (action) => action.write != null
  })

  static GDPR = new Resource({
    id: "gdpr",
    name: "GDPR",
    toData: t => t,
    changeActionAccessLevel,
    emptyData: () => "",
    canRead: (action) => action.read != null,
    canWrite: (action) => action.write != null
  })

  static DEALS = new Resource({
    id: "deals",
    name: "Deals",
    toData: t => t,
    changeActionAccessLevel,
    emptyData: () => "",
    canRead: (action) => action.read != null,
    canWrite: (action) => action.write != null
  })

  static loginAs = new Resource<string[]>({
    id: "loginAs",
    name: "Login As",
    toData: t => t,
    changeActionAccessLevel: (action, accessLevel, newData) => {
      return new ListResource({action, accessLevel, newData: newData!, defaultData: []}).applyInclude()
    },
    emptyData: () => [],
    canRead: (action, roles) => action.read != null && action.read.data.containsAll(roles),
    canWrite: (action, roles) => action.write != null && action.write.data.containsAll(roles),
  })

  static role = new Resource({
    id: "role",
    name: "Roles",
    toData: t => t,
    changeActionAccessLevel,
    emptyData: () => "",
    canRead: (action) => action.read != null,
    canWrite: (action) => action.write != null
  })

  static parse(params: string) {
    return isResourceId(params) ? values[params] : Resource.UNKNOWN
  }

  constructor(params: ResourceParams<T>) {
    Object.assign(this, params)
    values[params.id] = this
  }

  toJSON() {
    return this.id
  }

  get isLoginAsOrUserManagement() {
    return this.id === Resource.USER.id || this.id === Resource.loginAs.id
  }
}

function isResourceId(e: string): e is ResourceID {
  return values.hasOwnProperty(e)
}
