import {FormState, FieldState, ValidatableMapOrArray} from "formstate"
import {extension} from "ts-trait/build/extension"

declare module "formstate/lib/core/formState" {
  interface FormState<TValue extends ValidatableMapOrArray> extends FormStateToJSON<TValue> {}
}

@extension([FormState])
class FormStateToJSON<TValue extends ValidatableMapOrArray> {
  toJSON(this: FormState<TValue>) {
    return this.$
  }
}

