import {_} from "@revdesk/util"

export interface PublisherFeature {
  value: number
  name: string
  flag: number
}

export function createPublisherFeature(value: number, name: string): PublisherFeature {
  return {value, name, flag: Math.pow(2, value)};
}

const publisherFeatureList = [
  createPublisherFeature(0, "None"),
  createPublisherFeature(1, "FloorPrice"),
  createPublisherFeature(2, "AdQuality"),
  createPublisherFeature(4, "CampaignUploader"),
  createPublisherFeature(8, "ContentsManagedByConsole"),
  createPublisherFeature(9, "AudienceVault")
]

const publisherFeature = _.keyBy(publisherFeatureList, "name")
const publisherFeatureMapByValue = _.keyBy(publisherFeatureList, "value")

export function getListByValue(value: number) {
  let power = 0;
  const features: any = {};

  while (value > 0) {
    if (value % 2 === 1) {
      const feature = publisherFeatureMapByValue[power]
      if (feature) {
        features[feature.name] = feature;
      }
    }
    value = Math.floor(value / 2);
    power += 1;
  }

  return features
}

export function checkValue(value: number, feature: PublisherFeature): boolean {
  // tslint:disable-next-line
  return (value & feature.flag) === feature.flag;
}
