import {Theme} from "@material-ui/core/styles/createMuiTheme"
import themeListener from "@material-ui/core/styles/themeListener"

export function themed(ctor: any) {
  ctor.contextTypes = ctor.contextTypes || {}
  Object.assign(ctor.contextTypes, (themeListener as any).contextTypes)

  const Proto = ctor.prototype as any
  Object.defineProperty(Proto, "theme", {
    get(this: React.Component<any, any>) {
      return themeListener.initial(this.context)
    }
  })
}

export interface Themed {
  readonly theme: Theme
}
