import {lazyVal, NestedItem} from "@revdesk/util"
import {dataClass, DataClass} from "data-class-copy/lib"
import {applyMixins} from "ts-trait/build"
import {Country} from "../country";
import {DataTypes} from "../dataTypes"
import {Searchable} from "../searchable"
import {UserStatus} from "../userStatus"
import {App} from "./app"
import {PublisherFeature} from "./publisherFeature";

export interface PublisherSchema {
  id: string | number
  name: string
  groupId: string
  phone?: string
  phoneCountry?: string
  email: string
  status?: string
}

export interface PublisherParams {
  id: string;
  groupId: string;
  name: string;
  apps: Array<App>;
  country: string;
  email: string;
  features: Record<string, PublisherFeature>;
  phone: string;
  website: string;
  phoneCountry: string;
  fullCountry: Country;
  status: UserStatus
}

export interface Publisher extends PublisherParams, NestedItem<any, App>, DataClass<Publisher> {

}

@dataClass
@applyMixins([NestedItem, Searchable])
export class Publisher implements NestedItem<any, App> {
  static mini = (publisher: Publisher) => publisher.mini

  static getName(roll: Publisher) {
    return roll.name
  }

  static EMPTY = new Publisher({
    id: "",
    groupId: "",
    name: "",
    apps: [],
    country: "",
    email: "",
    features: {},
    phone: "",
    website: "",
    phoneCountry: "",
    fullCountry: Country.EMPTY,
    status: UserStatus.UNKNWON
  })

  static parse(params: any = {}) {
    const email = params.email || params.contact_email
    const country = params.country || params.homeCountry
    return Publisher.EMPTY.copy({
      id: params.id != null ? String(params.id) : "",
      groupId: params.groupId != null ? String(params.groupId) : "",
      name: params.name != null ? String(params.name) : "",
      country: country != null ? String(country) : "",
      email: email != null ? String(email) : "",
      apps: (params.apps || []).map(App.parse),
      phone: params.phone != null ? String(params.phone) : "",
      website: params.website != null ? String(params.website) : "",
      phoneCountry: params.phoneCountry != null ? String(params.phoneCountry) : "",
      features: params.features != null ? params.features : {},
      fullCountry: params.fullCountry != null ? Country.parse(params.fullCountry) : Country.EMPTY,
      status: UserStatus.parse(params.status)
    })
  }

  constructor(params: PublisherParams) {
    Object.assign(this, params)
  }

  get parent(): any {
    return null
  }

  get children() {
    return this.apps || []
  }

  changeChildren(apps: Array<App>) {
    return this.copy({apps})
  }

  setParentOnChildren() {
    this.apps = this.apps.map(app => app.copy({publisher: this}).setParentOnChildren())
    return this;
  }

  withoutChildren(): Publisher {
    return this.copy({
      apps: []
    })
  }

  get size() {
    const {apps} = this
    return apps.length + apps.reduce((a, b) => a + b.size, 0)
  }

  get type() {
    return "publisher"
  }

  get hash() {
    return this.type + "_" + this.id
  }

  get lite() {
    return {
      publisherId: this.id
    }
  }

  get mini(): Publisher {
    return Publisher.EMPTY.copy({
      id: this.id
    })
  }

  get dataType() {
    return DataTypes.PUBLISHER
  }

  get firstApp() {
    return this.apps[0] || App.EMPTY
  }

  @lazyVal
  get image() {
    return this.firstApp.imageUrl
  }

  get isActive() {
    return this.status.isActive
  }
}
