import createStyles from "@material-ui/core/styles/createStyles"
import withStyles from "@material-ui/core/styles/withStyles"
import * as classNames from "classnames"
import * as React from "react"
import {Classes} from "../util"

export interface Styleable {
  className?: string
  style?: React.CSSProperties
}

const styles = createStyles({
  root: {
    overflow: "hidden",
    textOverflow: "ellipsis",
    wordBreak: "break-all"
  },
  box: {
    WebkitLineClamp: 1,
    display: "-webkit-box",
    WebkitBoxOrient: "vertical"
  },
  noWrap: {
    whiteSpace: "nowrap"
  }
})

export interface MaxLinesProps<P extends Styleable> extends Styleable {
  children: React.ReactElement<P>
  classes?: Classes<typeof styles>
  maxLines?: number
}

export const MaxLines = withStyles(styles, {name: "MaxLines", index: 1})(
  function MaxLines<P extends Styleable>({children, classes, maxLines = 1, ...rest}: MaxLinesProps<P>) {
    const {type: WrappedComponent, props: wrappedProps, key} = children
    return <WrappedComponent
      key={key}
      {...rest}
      {...wrappedProps}
      style={{
        ...wrappedProps.style,
        ...rest.style,
      }}
      className={classNames(wrappedProps.className, rest.className, classes!.root, classes!.noWrap)}/>
  }
)
