import {initials, lazyVal, NestedItem} from "@revdesk/util"
import {dataClass, DataClass} from "data-class-copy"
import * as moment from "moment"
import {Nullable} from "tsla-util/lib/nullable"
import {Option} from "tsla-util/lib/option"
import {Action} from "./access/action"
import {Permission} from "./access/permission"
import {Permissions} from "./access/permissions"
import {Resource} from "./access/resource"
import {Roll} from "./access/roll"
import {Network} from "./demand/network"
import {Group, GroupInstance} from "./group"
import {GroupType} from "./groupType"
import {App} from "./inventory/app"
import {Publisher} from "./inventory/publisher"
import {Role} from "./role"
import {UserStatus} from "./userStatus"

export interface UserSchema {
  id: string | number
  email: string | number
  groupId: string | number
  fullName: string | number
  password: string
  salt: string
}

export interface UserInstance extends UserSchema {
  group?: GroupInstance
}

export interface UserParams {
  id: string;
  email: string;
  fullName: string;
  managedPublishers: Publisher[];
  managedApps: App[];
  managedNetworks: Network[];
  rolls: Roll[];
  superUserId: string;
  group: Group;
  status: UserStatus;
  color: string;
  hasApps: boolean;
  hasValidPasswordResetToken: boolean;
  creationTime: moment.Moment;
}

export interface PublisherQueryProps {
  publisherId: string[],
  superUserPublishers?: boolean
}

export interface User extends UserParams, DataClass<User> {

}

@dataClass
export class User {
  static EMPTY = new User({
    id: "",
    email: "",
    fullName: "",
    managedNetworks: [],
    managedPublishers: [],
    managedApps: [],
    rolls: [],
    superUserId: "",
    group: Group.EMPTY,
    status: UserStatus.UNKNWON,
    color: "",
    hasApps: false,
    hasValidPasswordResetToken: false,
    creationTime: moment(new Date(0))
  })

  static withId(id: string) {
    return User.EMPTY.copy({id})
  }

  static parse(params: any = {}) {
    const fullName = params.fullName || params.name
    const passExpired = params.hasValidPasswordResetToken || params.passwordResetTokenExpirationDate != null
    return User.EMPTY.copy({
      id: params.id != null ? String(params.id) : "",
      email: params.email != null ? String(params.email) : "",
      fullName: fullName != null ? String(fullName) : "",
      group: Group.parse(params.group),
      rolls: (params.rolls || params.roles || params.userRoles || []).map(Roll.parse),
      superUserId: params.superUserId != null ? String(params.superUserId) : "",
      managedPublishers: (params.managedPublishers || []).map(Publisher.parse),
      managedNetworks: (params.managedNetworks || []).map(Network.parse),
      managedApps: (params.managedApps || []).map(App.parse),
      status: UserStatus.parse(params.status),
      hasApps: Boolean(params.hasApps),
      hasValidPasswordResetToken: passExpired,
      creationTime: params.creationTime != null ? moment(new Date(params.creationTime)) : moment(new Date(0))
    })
  }

  constructor(params: UserParams) {
    Object.assign(this, params)
  }

  get publisherId() {
    return this.publisher && this.publisher.id
  }

  get publisher() {
    return this.group.publisher
  }

  get groupId() {
    return this.group.id
  }

  get groupType() {
    return this.group.groupType
  }

  get highestRole(): Role {
    return this.firstRole.groupType.oldRole
  }

  queryablePublisherId({publisherId, superUserPublishers}: PublisherQueryProps = {publisherId: []}, keyName = "id") {
    const $in = this.highestRole.toPublisherId(this, publisherId, superUserPublishers)
    return $in.length ? {[keyName]: $in} : {}
  }

  publisherIdQuery({publisherId, superUserPublishers}: PublisherQueryProps = {publisherId: []}) {
    const $in = this.highestRole.toPublisherId(this, publisherId, superUserPublishers)
    return $in.length ? {publisherId: {$in}} : {}
  }

  get isAdminOrSuperUser() {
    return this.highestRole.isAdminOrSuperUser
  }

  get isAdminOrSuperuser() {
    return this.isAdminOrSuperUser
  }

  get isPublisher() {
    return this.highestRoleId === Role.PUBLISHER.id
  }

  get isAdmin() {
    return this.highestRoleId === Role.ADMIN.id
  }

  get isSuperuser() {
    return this.highestRoleId === Role.SUPER_USER.id
  }

  get isSuperuserAsPublisher() {
    return this.isPublisher && Boolean(this.superUserId)
  }

  get isAdminSuOrSuAsPub() {
    return this.isAdminOrSuperUser || this.isSuperuserAsPublisher
  }

  get highestRoleId() {
    return this.highestRole.id
  }

  @lazyVal
  get permissions(): Permissions {
    return this.rolls.flatMap(role => role.permissions)
      .groupBy(
        p => p.resource.id,
        permissions => Action.fromPermissions(permissions[0].resource, permissions)
      )
  }

  @lazyVal
  get initials(): string {
    return initials(this.fullName)
  }

  get isGroupAdmin() {
    return this.id === this.group.adminId
  }

  canWriteUser(other: User) {
    return this.writeData(p => p.user).getOrElse([])
      .containsNone(other.rolls.map(role => role.id))
  }

  canSaveResource(resource: Resource) {
    const permission = this.permissions[resource.id as keyof Permissions]
    return permission != null && permission.write != null
  }

  canViewResource(resource: Resource) {
    const permission = this.permissions[resource.id as keyof Permissions]
    return permission != null && permission.read != null
  }

  get canSaveInventory() {
    return this.canSaveResource(Resource.INVENTORY)
  }

  get canViewInventory() {
    return this.canViewResource(Resource.INVENTORY)
  }

  get canSaveGDPR() {
    return this.canSaveResource(Resource.GDPR)
  }

  get canViewDeals() {
    return this.canViewResource(Resource.DEALS)
  }

  get canSaveDeals() {
    return this.canSaveResource(Resource.DEALS)
  }

  get canViewFinance() {
    return this.canViewResource(Resource.TIPALTI)
  }

  get canSaveFinance() {
    return this.canSaveResource(Resource.TIPALTI)
  }

  get canViewAudience() {
    if (this.isAdminOrSuperUser) {
      return true
    }
    return this.publisher != null && Object.keys(this.publisher.features).some(e => e === "AudienceVault")
  }

  toUserRecord() {
    return {
      id: this.id,
      email: this.email,
      fullName: this.fullName,
      groupId: this.group.id,
      status: this.id ? this.status.id : UserStatus.ACTIVATED.id,
    }
  }

  get toUserToGroupRecord() {
    return {
      userId: this.id,
      groupId: this.group.id,
      authId: this.highestRole.authId
    }
  }

  toUserRoleRecords() {
    return this.rolls.map(role => ({
      roleId: role.id,
      userId: this.id
    }))
  }

  get firstRole() {
    return this.rolls[0] || Roll.EMPTY
  }

  setRolesGroupTypeOnGroup() {
    return this.copy({
      group: this.group.copy({groupType: this.firstRole.groupType})
    })
  }

  setAsGroupAdminIfNewGroup() {
    return this.group.id ? this : this.copy({
      group: this.group.copy({
        adminId: this.id
      })
    })
  }

  toggleStatus() {
    return this.copy({
      status: this.status.toggle()
    })
  }

  get toManagePublisherRecords() {
    return this.managedPublishers.map(publisher => ({
      publisherId: publisher.id,
      userId: this.id,
      groupId: 0
    }))
  }

  get toManageAppRecords() {
    return this.managedApps.map(app => ({
      appId: app.id,
      userId: this.id,
    }))
  }

  get toManageNetworkRecords() {
    return this.managedNetworks.map(network => ({
      networkId: network.id,
      userId: this.id,
    }))
  }

  readUserFromGroupQuery(field = "groupId") {
    return this.internal ? {} : {
      [field]: this.group.id
    }
  }

  get mini() {
    return this.copy({
      rolls: this.rolls.map(Roll.mini),
      managedPublishers: this.managedPublishers.map(Publisher.mini),
      managedApps: this.managedApps.map(App.mini)
    })
  }

  get internal() {
    return this.group.internal
  }

  get external() {
    return this.group.external
  }

  get rolesQuery() {
    return {
      id: this.rolls.map(NestedItem.toID)
    }
  }

  changeRolls(rolls: Roll[]) {
    return this.copy({rolls})
  }

  setMemberOfIfExternal(user: User) {
    return user.external ? this.copy({
      group: user.group
    }) : this
  }

  get lead() {
    return this.firstRole.lead
  }

  get isExternalSupply() {
    return this.groupType.id === GroupType.SUPPLY.id
  }

  get isExternalDemand() {
    return this.groupType.id === GroupType.DEMAND.id
  }

  get name() {
    return this.fullName
  }

  get publisherName() {
    return this.publisher != null ? this.publisher.name : ""
  }

  get roles() {
    return [this.highestRole].map(e => e.id)
  }

  get menu() {
    const isExternalSupply = this.isExternalSupply
    return {
      dashboard: this.internal || isExternalSupply,
      inventory: isExternalSupply,
      demandControl: isExternalSupply,
      sdk: isExternalSupply,
      createApp: isExternalSupply,
      vault: this.internal || this.isExternalSupply
    }
  }

  get toJSID() {
    return {
      id: this.id
    }
  }

  @lazyVal
  get superUserPublishers() {
    return this.managedPublishers.map((e) => e.id)
  }

  permission<T>(fn: (p: Permissions) => Nullable<Action<T>>) {
    return Option.of(fn(this.permissions))
  }

  read<T>(fn: (p: Permissions) => Nullable<Action<T>>): Option<Permission<T>> {
    return this.permission(fn).map(e => e.read)
  }

  readData<T>(fn: (p: Permissions) => Nullable<Action<T>>): Option<T> {
    return this.read(fn).map(e => e.data)
  }

  write<T>(fn: (p: Permissions) => Nullable<Action<T>>): Option<Permission<T>> {
    return this.permission(fn).map(e => e.write)
  }

  writeData<T>(fn: (p: Permissions) => Nullable<Action<T>>): Option<T> {
    return this.write(fn).map(e => e.data)
  }

  @lazyVal
  get canLoginAs() {
    return this.read(p => p.loginAs).isNotEmpty
  }

  canLoginAsUser(other: User) {
    return this.canLoginAs && this.readData(p => p.loginAs).getOrElse([])
      .containsAll(other.rolls.map(r => r.id))
  }

  get isActive() {
    return this.status.isActive
  }

  @lazyVal
  get canSaveRoles() {
    return this.write(p => p.role).isNotEmpty
  }

  @lazyVal
  get canViewRoles() {
    return this.read(p => p.role).isNotEmpty
  }

  @lazyVal
  get canSaveUsers() {
    return this.write(p => p.user).isNotEmpty
  }

  @lazyVal
  get canViewUsers() {
    return this.read(p => p.user).isNotEmpty
  }

  get canSaveAppText() {
    return this.firstRole.id === "100"
  }

  canSaveUser(other: User) {
    return this.canSaveUsers && this.readData(p => p.user).getOrElse([])
      .containsNone(other.rolls.map(r => r.id))
  }
}
