import {DataClass, dataClass} from "data-class-copy"
import {action, observable} from "mobx"
import {SideMenuObject} from "./sideMenuObject"

export interface SideMenuBoxParams {
  icon: string,
  firstLevel: SideMenuObject,
  secondLevel: SideMenuObject[],
  id: string
}

export interface SideMenuBox extends SideMenuBoxParams, DataClass<SideMenuBoxParams> {

}

@dataClass
export class SideMenuBox {
  @observable expanded = false

  constructor(params: SideMenuBoxParams) {
    Object.assign(this, params)
  }

  @action.bound changeExpanded(expanded = !this.expanded) {
    this.expanded = expanded
  }
}
