import {Param, ParamValue, PlacementType, TPNIntegration, TPNPlacement} from "@revdesk/model/build/mediation"
import {DataClass, dataClass} from "data-class-copy"
import {action, computed, observable} from "mobx"
import {FormState} from "../extensions/formstate"
import {FetchState} from "../fetch-state"
import {ParamValueFieldState, ParamValueFormState, ParamValueValidator} from "../param-value-form"
import {TabsState} from "../tabs"
import {TPNPlacementState} from "../tpn-placement-form"
import {TPNPlacementListState} from "../tpn-placement-list"

export interface TpnAppStateParams {
  form: ParamValueFormState
  fetchState: FetchState
  tpnIntegration: TPNIntegration
  submitState: FetchState
  placements: TPNPlacementState
  placementTabs: TabsState<PlacementType>
  listState: TPNPlacementListState
  placementParams: Param[]
  showForm: boolean
  savedPlacements: TPNPlacement[]
  deletedPlacements: TPNPlacement[]
  saved: boolean
  placementsDeleted: boolean
}

export interface TpnAppState extends TpnAppStateParams, DataClass<TpnAppStateParams> {

}

@dataClass
export class TpnAppState {
  static empty = () => new TpnAppState({
    fetchState: FetchState.noData,
    form: new FormState([]),
    tpnIntegration: TPNIntegration.empty,
    submitState: FetchState.noData,
    placements: TPNPlacementState.empty(),
    placementTabs: TabsState.none(),
    listState: TPNPlacementListState.empty(),
    placementParams: [],
    showForm: false,
    savedPlacements: [],
    deletedPlacements: [],
    saved: false,
    placementsDeleted: false,
  })

  @observable tpnIntegration: TPNIntegration
  @observable fetchState: FetchState
  @observable submitState: FetchState
  @observable form!: ParamValueFormState
  @observable showForm!: boolean

  constructor(params: TpnAppStateParams) {
    Object.assign(this, params)
  }

  get toTPNIntegration() {
    return this.tpnIntegration.copy({
      paramValues: this.form.$.map(e => e.toParamValue),
      placements: this.placements.toTPNPlacements,
    })
  }

  get network() {
    return this.tpnIntegration.network
  }

  get networkId() {
    return this.tpnIntegration.networkId
  }

  get appAndPlacementsFormValid() {
    return !this.form.hasError && !this.placements.form.hasError
  }

  @computed
  get readOnly() {
    return (this.showForm && !!this.tpnIntegration.id) || this.submitState.isLoading
  }

  @action changeIntegrationId({id}: TPNIntegration) {
    return this.mutate({
      tpnIntegration: this.tpnIntegration.mutate({id}),
      placements: this.placements.mutate({
        tpnIntegrationId: id
      })
    })
  }

  @action changeParamValues(paramValues: ParamValue[], validators: ParamValueValidator[]) {
    return this.mutate({
      form: ParamValueFieldState.fromParamValues(paramValues, validators),
    })
  }
}
