import {VendorTypeValue} from "../vendorType";
import {Unit} from "../..";

export interface ViewabilityJS {
  playPct: number;
  pausePct: number;
  vendor: VendorTypeValue[];
  unit?: Unit;

}

const DEFAULT_VIEWABILITY: ViewabilityJS = {
  playPct: 50,
  pausePct: 50,
  vendor: []
};

export interface Viewability extends ViewabilityJS {

}

export class Viewability {

  static DEFAULT_VIEWABILITY = DEFAULT_VIEWABILITY;

  toJS(unit: Unit) {
    const value: Partial<ViewabilityJS> = {
      vendor: this.vendor
    };
    if (unit.supportsViewability()) {
      value.playPct = this.playPct;
      value.pausePct = this.pausePct;
    }
    return value;
  }
}
