import * as React from "react"
import Avatar, {AvatarProps} from "@material-ui/core/Avatar"
import {Initials} from "../../nfp/components/initials";

export interface TextImgAvatarProps extends AvatarProps {
  text: string;
  src?: string;
}

export function TextImgAvatar(props: TextImgAvatarProps) {
  const {text, src, ...rest} = props
  if (src) {
    return <Avatar src={src} {...rest} />
  }
  return (
    <Avatar {...props}>
      <Initials>{text}</Initials>
    </Avatar>
  )
}
