import {shallowStringify} from "@revdesk/util/build/query-string/shallowStringify"
import {RouterState} from "./routerState"
import {action} from "mobx"

export interface Location {
  pathname: string
  search: string
  hash: string
}

export type Interceptor = (
  path: string,
  params: Record<string, string>,
  hash: string,
  fromListener: boolean
) => NavInterception

export interface NavOptions {
  fromListener?: boolean
  inherit?: boolean
  replace?: boolean
  hash?: string
}

const defaultOptions = {
  fromListener: false,
  inherit: false
}

const emptyInterceptor: Interceptor = (path, params, hash) => ({
  path, params, hash, forcePush: false
})

export interface NavInterception {
  forcePush: boolean
  path: string
  params: Record<string, string>
  hash: string
}

export interface RouterActionsParams {
  history: {
    location: Location
    push(location: Location): void
    replace(location: Location): void
  }
  state: RouterState
  interceptor?: Interceptor
}

export interface RouterActions extends RouterActionsParams {
}

export class RouterActions {

  constructor(params: RouterActionsParams) {
    Object.assign(this, params)
    this.interceptor = this.interceptor || emptyInterceptor
  }

  search(params: Record<string, string> = {}) {
    return this.navTo(this.state.path, params, {
      inherit: true,
      replace: true
    })
  }

  changeHash(hash: string) {
    return this.navTo(this.state.path, this.state.params, {
      inherit: true,
      replace: true,
      hash
    })
  }

  @action.bound navTo(
    path: string,
    params: Record<string, string> = {},
    {fromListener = false, inherit = false, replace = false, hash = ""}: NavOptions = defaultOptions
  ) {
    if (fromListener && path === this.state.path) {
      return
    }

    const interception = this.interceptor(path, params, hash, fromListener)

    const fromUnknownPath = !this.state.hasPath(this.state.path)
    const samePath = interception.path === this.state.path

    this.state.current.navTo(interception, inherit)

    const next = {
      ...this.history.location,
      hash: this.state.hash,
      pathname: this.state.path,
      search: shallowStringify(this.state.params)
    }

    if (fromUnknownPath || samePath || replace) {
      this.history.replace(next)
    } else if (!fromListener || interception.forcePush) {
      this.history.push(next)
    }
  }
}
