export interface FreqTypeParams {
  id: string
  name: string
  bit: boolean
}

export interface FreqType extends FreqTypeParams {

}

export class FreqType {
  static capping = new FreqType({
    id: "1",
    name: "Capping",
    bit: false
  })

  static pacing = new FreqType({
    id: "2",
    name: "Pacing",
    bit: true
  })

  constructor(params: FreqTypeParams) {
    Object.assign(this, params)
  }

  toJSON() {
    return this.id
  }
}
