import * as React from "react"
import {stringMatch} from "@revdesk/util"
import {AppsIcon} from "./icons/appsIcon"
import {MonetizeIcon} from "./icons/monetizeIcon"
import {AudienceIcon} from "./icons/audienceIcon"
import {FinanceIcon} from "./icons/financeIcon"
import {SvgIconProps} from "@material-ui/core/SvgIcon"
import {DynamicReportsIcon} from "./icons/dynamicReportsIcon"
import {DocumentationIcon} from "./icons/documentationIcon"

export interface RenderIconProps extends SvgIconProps {
  value: string
  style?: React.CSSProperties
}

export class RenderIcon extends React.Component<RenderIconProps> {

  render() {
    const {value} = this.props
    return stringMatch({
      AppsIcon: () => <AppsIcon/>,
      MonetizeIcon: () => <MonetizeIcon/>,
      AudienceIcon: () => <AudienceIcon/>,
      DynamicReportsIcon: () => <DynamicReportsIcon/>,
      FinanceIcon: () => <FinanceIcon/>,
      DocumentationIcon: () => <DocumentationIcon/>,
      Else: () => null
    })(value)
  }
}
