export type TapTypeValue = "ctr" | "fullscreen" | "noAction";

export interface TapTypeJS {
  id: TapTypeValue;
  name: string;
}

export default class TapType {

  static CTR = new TapType({
    id: "ctr",
    name: "Go to advertiser page"
  });

  static FS = new TapType({
    id: "fullscreen",
    name: "Fullscreen video"
  });

  static NO_ACTION = new TapType({
    id: "noAction",
    name: "Advertiser settings"
  });
  id: TapTypeValue;
  name: string;

  constructor({id, name}: TapTypeJS) {
    this.id = id
    this.name = name
  }
}
