import {observable} from "mobx"
import {FieldState} from "../extensions/formstate"

export interface ToggleableFieldStateParams {
  fieldState: FieldState<string>
  isEditing: boolean
  isSubmitting: boolean
}

export interface ToggleableFieldState extends ToggleableFieldStateParams {

}

export class ToggleableFieldState {
  static empty = () => new ToggleableFieldState({
    fieldState: new FieldState<string>(""),
    isEditing: false,
    isSubmitting: false
  })

  @observable fieldState: FieldState<string>
  @observable isEditing: boolean
  @observable isSubmitting: boolean

  constructor(params: ToggleableFieldStateParams) {
    this.fieldState = params.fieldState
    this.isEditing = params.isEditing
    this.isSubmitting = params.isSubmitting
  }
}
