const idToRewardingType: Record<string, RewardingType> = {}

export interface RewardingTypeParams {
  id: string
  value: boolean
}

export interface RewardingType extends RewardingTypeParams {
}

export class RewardingType {
  static parse(params: string) {
    return idToRewardingType[params] || RewardingType.client
  }

  static fromValue(value: boolean) {
    return value ? RewardingType.server : RewardingType.client
  }

  static client = new RewardingType({
    id: "client",
    value: false,
  })

  static server = new RewardingType({
    id: "server",
    value: true
  })

  constructor(params: RewardingTypeParams) {
    this.id = params.id
    this.value = params.value
    idToRewardingType[params.id] = this
  }

  toJSON() {
    return this.id
  }
}
