const idToFilteredProtocol: Record<string, FilteredProtocol> = {}

export interface FilteredProtocolParams {
  id: string
  name: string
}

export interface FilteredProtocol extends FilteredProtocolParams {

}

export class FilteredProtocol {
  static parse(params: string) {
    return idToFilteredProtocol[params] || FilteredProtocol.unknown
  }

  static unknown = new FilteredProtocol({
    id: "",
    name: "Unknown protocol"
  })

  static vast = new FilteredProtocol({
    id: "2",
    name: "VAST 2.0"
  })

  static vastWrapper = new FilteredProtocol({
    id: "5",
    name: "VAST 2.0 Wrapper"
  })

  constructor(params: FilteredProtocolParams) {
    Object.assign(this, params)
    idToFilteredProtocol[params.id] = this
  }

  toJSON() {
    return this.id
  }
}
