import {Observe} from "@revdesk/util/build/observe"
import {FieldState as BaseFieldState} from "formstate"
import {action, computed, observable} from "mobx"
import {Observable} from "rxjs/Observable"
import "./validateIsolateField"

export class FieldState<T> extends BaseFieldState<T> {
  @observable focused = false
  @observable touched = false
  @observable submitted = false

  validationPromise: Promise<any> | null = null

  validationStream = new Observe(obs =>
    obs
      .debounce(t => Observable.timer(t))
      .switchMap(() => this.baseValidate())
  ).next

  baseValidate: () => Promise<any>

  constructor(_initValue: T) {
    super(_initValue)
    this.baseValidate = this.validate
    this.disableAutoValidation()
    this.validate = async () => {
      this.validating = true
      return this.validationStream(0)
    }
  }

  @action.bound setTouchedAndValidate() {
    this.onBlur()
    this.validating = true
    this.validationPromise = this.validationStream(0)
    this.touched = true
  }

  @action.bound setTouched() {
    this.onBlur()
    this.touched = true
  }

  @computed get showErrorHelper() {
    return !this.validating && this.hasError && (this.submitted || (this.touched && !this.focused))
  }

  @action.bound validateStream() {
    this.validating = true
    this.validationPromise = this.validationStream(750)
  }

  @action.bound onChangeValidate(value: T) {
    this.onChange(value)
    this.validating = true
    this.validationPromise = this.validationStream(750)
  }

  @action.bound onFocus() {
    this.focused = true
  }

  @action.bound onBlur() {
    this.focused = false
  }

  @action setSubmitted() {
    this.touched = true
    this.submitted = true
  }

  @action setUnsubmitted() {
    this.touched = false
    this.submitted = false
  }

  silentValidate() {
    return this.validationStream(0)
  }
}
