export interface StatusCodeErrorParams {
  message?: string
  code: number
}

export interface StatusCodeError extends StatusCodeErrorParams {
  message: string
}

export class StatusCodeError extends Error {
  static isStatusCodeError(e: any): e is StatusCodeError {
    return typeof e.code === "number" && e instanceof Error
  }

  name = "StatusCodeError"

  constructor({message = "An error occurred", ...params}: StatusCodeErrorParams) {
    super(message)
    Object.assign(this, params)
  }
}
