import {Level, StrategyTree} from "./tree"
import "@revdesk/array"
const flatMap = require("lodash/flatMap")

export interface PlaceIQItem {
  type: Level;

  [key: string]: any;
}

export class PlaceIQTree {
  constructor(public tree: StrategyTree<PlaceIQItem>, public autoAppend = true) {
  }

  addToTree(node: PlaceIQItem) {
    const {autoAppend, tree} = this
    if (autoAppend) {
      tree.bulkAdd(this.flatItem(node))
    } else {
      tree.addToTree(node)
    }
  }

  flatItem(item: PlaceIQItem): PlaceIQItem[] {
    const {tree} = this
    const {nodeTypes, idProp} = tree
    const {subItemsProp} = nodeTypes.byID[item.type]
    return [item, ...flatMap(item[subItemsProp], (e: any) => this.flatItem(e))]
  }

  removeWithDummy(node: PlaceIQItem) {
    const dummyItem = this.findDummyItem(node)
    if (dummyItem != null) {
      const dummyNode = this.findDummyNode(node)
      dummyNode && this.tree.removeFromTree(dummyNode)
    }
    this.tree.removeFromTree(node)
  }

  findDummyNode(node: PlaceIQItem) {
    const {tree} = this
    const {nodeTypes, idProp} = tree
    const dummyItem = this.findDummyItem(node)
    const {parentProp, childrenProp} = nodeTypes.byID[node.type]

    if (dummyItem != null) {
      const parent = node[parentProp]
      const {childrenProp} = nodeTypes.byID[parent.type]
      const branch = parent[childrenProp]
      return branch[dummyItem[idProp]]
    }
    return null
  }

  findDummyItem(item: PlaceIQItem) {
    const {tree} = this
    const {nodeTypes, idProp} = tree
    const {parentProp} = nodeTypes.byID[item.type]
    if (parentProp) {
      const parent = item[parentProp]
      const {subItemsProp} = nodeTypes.byID[parent.type]
      return parent[subItemsProp].find((e: any) => e[idProp] === parent[idProp])
    }
    return null
  }

  toggleItem(node: PlaceIQItem, branch: Record<string, any>) {
    const {tree} = this
    const {nodeTypes, idProp} = tree
    const exists = branch && branch[node[idProp]]
    if (exists) {
      const dummyItem = this.findDummyItem(node)
      if (dummyItem != null) {
        const dummyNode = branch[dummyItem[idProp]]
        dummyNode && this.tree.removeFromTree(dummyNode)
      }
      this.tree.removeFromTree(branch[node[idProp]])
    } else {
      this.addToTree(node)
    }
  }
}
