import {UserSession} from "@revdesk/model"

export const pathToRoute: Record<string, AppRoute> = {}

export interface AppRouteParams {
  name: string
  path: string
  params: Record<string, string>,
  authorized?: (userSession: UserSession) => boolean
}

export interface AppRoute extends AppRouteParams {

}

export class AppRoute {
  static userHomePage(userSession: UserSession) {
    const {user} = userSession
    if (user.isExternalSupply) {
      return AppRoute.apps.path
    }
    if (user.isExternalDemand) {
      return AppRoute.bids.path
    }
    return AppRoute.overview.path
  }

  static restorePassword = new AppRoute({
    name: "Restore Password",
    path: "/restore-password",
    params: {
      publisherId: ""
    },
  })

  static resetPassword = new AppRoute({
    name: "Reset Password",
    path: "/reset-password",
    params: {
      id: "",
      token: "",
      invite: ""
    }
  })

  static login = new AppRoute({
    name: "Login",
    path: "/login",
    params: {
      redirect: ""
    }
  })

  static demandSource = new AppRoute({
    name: "Demand Source",
    path: "/demand-source",
    params: {
      appId: "",
      networkId: "",
      type: ""
    }
  })

  static apps = new AppRoute({
    name: "Apps",
    path: "/apps",
    params: {
      publisherId: ""
    },
    authorized: userSession => userSession.user.isPublisher && userSession.canSessionViewInventory
  })

  static addApp = new AppRoute({
    name: "Add App",
    path: "/add-app",
    params: {
      publisherId: ""
    },
    authorized: userSession => userSession.user.isPublisher && userSession.canSessionSaveInventory
  })

  static editApp = new AppRoute({
    name: "Edit App",
    path: "/edit-app",
    params: {
      publisherId: "",
      appId: "",
    }
  })

  static signUp = new AppRoute({
    name: "Sign Up",
    path: "/sign-up",
    params: {}
  })

  static addPlacement = new AppRoute({
    name: "Add Placement",
    path: "/add-placement",
    params: {
      appId: ""
    }
  })

  static editPlacement = new AppRoute({
    name: "Edit Placement",
    path: "/edit-placement",
    params: {
      id: "",
      appId: "",
      networkId: ""
    }
  })

  static placements = new AppRoute({
    name: "Placements",
    path: "/placements",
    params: {
      appId: ""
    }
  })

  static overview = new AppRoute({
    name: "Mediation Overview",
    path: "/reports/overview",
    params: {},
    authorized: userSession => userSession.user.internal || userSession.user.isPublisher
  })

  static programmatic = new AppRoute({
    name: "Programmatic Drill-down",
    path: "/reports/programmatic",
    params: {},
    authorized: userSession => userSession.user.internal || userSession.user.isPublisher
  })

  static bids = new AppRoute({
    name: "Buyer's Report",
    path: "/reports/bids",
    params: {},
    authorized: userSession => userSession.user.internal || userSession.user.isExternalDemand
  })

  static blockingControls = new AppRoute({
    name: "Blocking Controls",
    path: "/blocking-controls",
    params: {}
  })

  static users = new AppRoute({
    name: "Users",
    path: "/users",
    params: {
      userId: "",
      page: "",
      limit: "",
      search: "",
      sort: "",
      direction: ""
    },
    authorized: userSession => userSession.user.canViewUsers
  })

  static audience = new AppRoute({
    name: "Audience",
    path: "/audience",
    params: {},
    authorized: userSession => userSession.canSessionViewAudience
  })

  static financeDetails = new AppRoute({
    name: "Payment Details",
    path: "/finance/details",
    params: {},
    authorized: userSession => userSession.canSaveFinance
  })

  static financeHistory = new AppRoute({
    name: "Payment History",
    path: "/finance/history",
    params: {},
    authorized: userSession => userSession.canViewFinance
  })

  static financeInvoices = new AppRoute({
    name: "Invoices",
    path: "/finance/invoices",
    params: {},
    authorized: userSession => userSession.canViewFinance
  })

  static userProfile = new AppRoute({
    name: "Profile",
    path: "/profile",
    params: {},
  })

  static changePassword = new AppRoute({
    name: "Change Password",
    path: "/change-password",
    params: {},
  })

  constructor(params: AppRouteParams) {
    Object.assign(this, params)
    pathToRoute[params.path] = this
  }

  cleanParams(params: Record<string, string>) {
    for (const key of Object.keys(params)) {
      if (this.params[key] == null) {
        delete params[key]
      }
    }
    return params
  }
}
