import "@revdesk/array"
import {Map} from "immutable"
import "rxjs/add/operator/merge"
import {actions, Actions, ActionsParams, FunctionStateHolder, SelfStateHolder, StateHolder} from "../old-actions"
import {FormControlActions} from "./formControlActions"
import {FormState} from "./formState"
import {ValidationError} from "./validator"

export interface FormActionsParams extends ActionsParams<FormState> {
  formControlActions: Record<string, Partial<FormControlActions>>
}

export interface FormActions extends FormActionsParams, Actions<FormState> {

}

@actions
export class FormActions implements Actions<FormState> {
  controlsActions: Map<string, FormControlActions> = Map()

  constructor(params: FormActionsParams) {
    Object.assign(this, params)
    this.Actions$super(params)
    this.controlsActions = Map(this.state.controls.map((v, name) =>
      new FormControlActions({
        validators: params.formControlActions[name].validators,
        formActions: this,
        stateHolder: new FunctionStateHolder(
          () => this.state.getControl(name),
          (control, event) => {
            this.setState(this.state.changeControl(name, control), event)
          }
        )
      })
    ))
  }

  get initialState() {
    return FormState.EMPTY
  }

  async validationPromise() {
    const errors = await Promise.all(
      this.controlsActions.map(c => c.validationPromise()).toArray()
    )
    return errors.flatMap(errors => errors)
  }

  clearValidations() {
    this.controlsActions.forEach(control => control.clearValidations())
  }

  changeSubmitted() {
    this.setState(this.state.changeSubmitted())
  }

  getControlActions(name: string) {
    return this.controlsActions.get(name)
  }

  stopValidation() {
    this.controlsActions.forEach(actions => {
      actions.stopValidation()
    })
  }
}
