import {Option} from "tsla-util/lib/option"
import {CreativeType} from "../mediation"
import {UnitType} from "./unitType"

export type UnitFormatValue = "Display" | "Video" | "VideoAndDisplay" | "Native" | "";

export interface UnitFormatParams {
  id: UnitFormatValue;
  name: string;
  matchingCreativeTypes: CreativeType[][]
  creativeTypes: CreativeType[]
}

export interface UnitFormat extends UnitFormatParams {

}

const values: UnitFormat[] = []
const idToFormat: Record<string, UnitFormat> = {}

export class UnitFormat {

  toJSON() {
    return this.id
  }

  static parse(params: string) {
    return idToFormat[params] || UnitFormat.UNKNOWN
  }

  static fromCreativeTypes(creativeTypes: CreativeType[]) {
    return Option.of(
      UnitFormat.values.find(e => e.matchesCreativeTypes(creativeTypes))
    ).getOrElse(UnitFormat.UNKNOWN)
  }

  static DISPLAY = new UnitFormat({
    id: "Display",
    name: "Display",
    creativeTypes: [CreativeType.static, CreativeType.banner, CreativeType.mrec],
    matchingCreativeTypes: [
      [CreativeType.static],
      [CreativeType.banner],
      [CreativeType.mrec],
    ]
  });

  static VIDEO = new UnitFormat({
    id: "Video",
    name: "Video",
    creativeTypes: [CreativeType.rewardedVideo, CreativeType.video],
    matchingCreativeTypes: [
      [CreativeType.video],
      [CreativeType.rewardedVideo],
    ]
  });

  static VIDEO_AND_DISPLAY = new UnitFormat({
    id: "VideoAndDisplay",
    name: "Display and Video",
    creativeTypes: [CreativeType.video, CreativeType.static],
    matchingCreativeTypes: [
      [CreativeType.static, CreativeType.video],
    ]
  });

  static NATIVE = new UnitFormat({
    id: "Native",
    name: "Native",
    matchingCreativeTypes: [],
    creativeTypes: []
  });

  static UNKNOWN = new UnitFormat({
    id: "",
    name: "Unknown",
    matchingCreativeTypes: [],
    creativeTypes: []
  });

  static values = values

  constructor(params: UnitFormatParams) {
    Object.assign(this, params)
    values.push(this)
    idToFormat[params.id] = this
    if (params.creativeTypes) {
      params.creativeTypes.forEach(
        (creativeType: CreativeType) => creativeType.unitFormatsIDs.push(params.id)
      )
    }
  }

  matchesCreativeTypes(other: CreativeType[]) {
    return this.matchingCreativeTypes.some(
      creativeTypes => creativeTypes.containsAll(other, e => e.id)
    )
  }

  toCreativeTypes(unitType: UnitType) {
    return this.matchingCreativeTypes.findOne(e => unitType.creativeTypes.containsAll(e)).getOrElse([])
  }
}
