import {tryGet} from "./try.get"
import * as statuses from "statuses"
import {DataClass, dataClass} from "data-class-copy"

export interface ErrorResponseParams {
  message: string
  statusCode: number
  name: string
  sensitive: boolean
}

export interface ErrorResponse extends ErrorResponseParams, DataClass<ErrorResponseParams> {

}

@dataClass
export class ErrorResponse implements Error  {
  static empty = new ErrorResponse({
    statusCode: 500,
    message: "",
    name: "ErrorResponse",
    sensitive: false
  })

  static code(statusCode = 500) {
    const message = statuses[statusCode]
    return ErrorResponse.empty.copy({statusCode, message})
  }

  static message(message: string, statusCode = 500, sensitive = false) {
    return ErrorResponse.empty.copy({message, statusCode, sensitive})
  }

  static safeMessage(err: Error, statusCode: number): string {
    return tryGet(
      () => err instanceof ErrorResponse ? err.message : void 0,
      statuses[statusCode] || String(statusCode)
    )!
  }

  static fromError(err: Error, statusCode: number) {
    return ErrorResponse.empty.copy({
      message: err.message, statusCode
    })
  }

  constructor(params: ErrorResponseParams) {
    Object.assign(this, params)
    this.message = params.message || statuses[params.statusCode] || ""
  }
}
