import {boolMatch} from "../patternMatch"
import escapeRegExp = require("lodash/escapeRegExp")

export interface ShallowParseOptions {
  ignorePrefix?: boolean
  prefix?: string,
  pairDelimiter?: string
  keyValDelimiter?: string
}

export function shallowParse(
  str: string,
  {
    ignorePrefix = true, prefix = "?",
    pairDelimiter = "&", keyValDelimiter = "="
  }: ShallowParseOptions = {},
  decode = decodeURIComponent
) {
  if (str === "") {
    return {}
  }
  return boolMatch({
    True: () => str.replace(new RegExp(`^${escapeRegExp(prefix)}`), ""),
    False: () => str
  })(ignorePrefix)
    .split(pairDelimiter)
    .map(kv => kv.split(keyValDelimiter).map(decode))
    .reduce((prev, [key, val]) => {
      prev[key] = val
      return prev
    }, {} as Record<string, any>)
}
