import {DataClass, dataClass} from "data-class-copy"
const idToBiddingType: Record<string, BiddingType> = {}

export interface BiddingTypeParams {
  id: string
  name: string
  override: (value?: number) => number | undefined | null
}

export interface BiddingType extends BiddingTypeParams, DataClass<BiddingTypeParams> {

}

@dataClass
export class BiddingType {
  static parse(params: string) {
    return idToBiddingType[params] || BiddingType.unknown
  }

  static unknown = new BiddingType({
    id: "",
    name: "Unknown Bidding Type",
    override: () => null,
  })

  static fixed = new BiddingType({
    id: "fixed",
    name: "Fixed CPM",
    override: value => value,
  })

  static auto = new BiddingType({
    id: "auto",
    name: "Auto CPM",
    override: () => null,
  })

  static selectable = [BiddingType.auto, BiddingType.fixed]

  constructor(params: BiddingTypeParams) {
    Object.assign(this, params)
    idToBiddingType[params.id] = this
  }

  get isFixed() {
    return this === BiddingType.fixed
  }
}
