import {App} from "@revdesk/model/build/inventory"
import {User} from "@revdesk/model/build/user"
import {UserSession} from "@revdesk/model/build/userSession"
import {CollinsCluster} from "@revdesk/util/build/collinsCluster"
import {EventEmitter} from "events"
import * as FontFaceObserver from "fontfaceobserver"
import {History} from "history"
import {action, runInAction} from "mobx"
import {RemoteTextStore} from "remote-text/lib/store/remoteTextStore"
import {block} from "tsla-util/lib/block"
import {loadStylesheet} from "tsla-util/lib/dom/loadStylesheet"
import {HasEmitter, hasEmitter} from "tsla-util/lib/has-emitter"
import {Option} from "tsla-util/lib/option"
import {delay} from "tsla-util/lib/promise/delay"
import "tsla-util/lib/promise/timeout"
import {TPNClient} from "../api"
import {AppManagementActions} from "../app-management/appManagementActions"
import {AppManagementState} from "../app-management/appManagementState"
import {AppRoute} from "../app-route/appRoute"
import {AppTextDocument, createDefaultTexts} from "../app-text/createDefaultTexts"
import {CollinsFetch, IStateServiceBridge} from "../client"
import {FetchState} from "../fetch-state/fetchState"
import {AsyncValidators, Validator} from "../form"
import {PageContainerActions} from "../page-container/pageContainerActions"
import {PageContainerState} from "../page-container/pageContainerState"
import {RouterActions} from "../router"
import {AppState} from "./appState"
import {RouterInterceptor} from "./routerInterceptor"
import {changeWalkMeUserId, loadWalkMeScript} from "./walkme"

export interface AppActionsParams {
  collinsFetch: CollinsFetch
  collinsCluster: CollinsCluster
  cookies: {
    get: (key: string) => string
    set: (key: string, value: string) => void
  },
  location: {
    search: () => Record<string, string>
    pathname: () => string
    hash: () => string
  },
  history: History
  state: AppState
  tpnClient: TPNClient
  palette: string[]
  stateService: IStateServiceBridge
}

export interface AppActions extends AppActionsParams, HasEmitter {
  on(type: "user" | "route" | "history", fn: (state: AppState) => any): EventEmitter
}

@hasEmitter
export class AppActions implements AsyncValidators {
  emitter = new EventEmitter()

  router: RouterActions

  pageContainerActions: PageContainerActions

  remoteTextStore: RemoteTextStore<AppTextDocument>

  constructor(params: AppActionsParams) {
    Object.assign(this, params)
    this.router = new RouterActions({
      history: this.history,
      interceptor: this.routerInterceptor,
      state: this.state.router
    })

    this.pageContainerActions = new PageContainerActions({
      onLogout: this.handleLogoutClick,
      state: PageContainerState.empty(),
      router: this.router,
      appActions: this
    })

    this.remoteTextStore = new RemoteTextStore({
      fetch: () => this.collinsFetch.get("/api/v2/app-text", {
        namespace: "default",
        lang: "en"
      }),
      save: value => this.collinsFetch.post("/api/v2/app-text", value),
      document: createDefaultTexts()
    })

  }

  @action
  async startSession() {
    try {
      this.remoteTextStore.loadDocument()
      loadWalkMeScript(this.collinsCluster !== CollinsCluster.prod)
      this.state.changeFetchState(FetchState.loading)
      await block(async () => {
        await Promise.all([
          loadStylesheet("https://cdn2.inner-active.mobi/RevenueDesk/web-assets/fonts/AvertaCY/stylesheet.css"),
          loadStylesheet("https://cdn2.inner-active.mobi/RevenueDesk/web-assets/style/react-virtualized.css")
        ])
        const weights = [300, 600, "normal"]
        const fontPromise = weights.map((weight) => new FontFaceObserver("AvertaCY", {weight}).load("hdf"))
        await this.resolveSession()
        await Promise.all([fontPromise, delay(500)])
      }).timeout(30 * 1000)
    } catch (e) {
      console.error(e)
      this.resolveRoute()
      this.state.mutate({err: "Server error occurred"})
    }
    runInAction(() => (
      this.state.changeFetchState(FetchState.noData)
    )) // will make the logo animation to exit
  }

  @action
  async fetchGlobalApp(appId: string) {
    if (appId !== this.state.menuApp.id) {
      this.state.menuApp = App.EMPTY
      try {
        const raw = await this.collinsFetch.get("/api/v2/app", {appId})
        runInAction(() => this.state.menuApp = App.parse(raw))
      } catch (e) {
        (window as any).maciej = e
        console.log(e.stack)
      }
    }
  }

  async setUserByToken() {
    const {accessToken} = this.location.search()

    if (accessToken != null) {
      this.cookies.set("token", accessToken)
    }
    const bearerToken = this.cookies.get("token")

    if (bearerToken) {
      const userSession = UserSession.parse(await this.collinsFetch.get("/api/v2/users/me"))
      this.setCurrentUser(userSession)
    } else {
      const {key, token} = this.location.search()
      if (key != null && token != null) {
        this.state.changeFetchState(FetchState.loading)
        const userSession = UserSession.parse(await this.collinsFetch.post("/auth/access"))
        this.setCurrentUser(userSession)
      }
    }
    changeWalkMeUserId(this.state.userSession.user.email)
  }

  async resolveSession() {
    try {
      await this.setUserByToken()
    } catch (e) {
      console.log(e)
    }
    this.resolveRoute()
  }

  resolveRoute() {
    const {pathname, search, hash} = this.location
    this.router.navTo(pathname(), search(), {
      hash: hash(),
    })
  }

  setCurrentUser(userSession: UserSession) {
    this.state.changeUserSession(userSession)
  }

  routerInterceptor = RouterInterceptor(this)

  httpInterceptor = (statusCode: number, body: Error | null) => {
    if (statusCode === 400 || statusCode === 404 || statusCode >= 500) {
      this.state.changeError(
        Option.of<Error>(body).map(e => e.message).getOrElse("Server error occurred")
      )
    } else if (statusCode === 401 && this.state.router.path !== AppRoute.login.path) {
      this.state.changeError("You've been logged out")
      this.logout()
    }
    return true
  }

  closeError = () => {
    this.state.changeError(null)
  }

  handleLogin = async () => {
    await this.setUserByToken()
    const {redirect} = this.state.router.params
    if (redirect != null) {
      const {path, params, hash} = block(() => {
        try {
          return JSON.parse(redirect)
        } catch (e) {
        }
        return {}
      })
      this.router.navTo(path, params, {hash})
    } else {
      this.router.navTo(
        AppRoute.userHomePage(this.state.userSession)
      )
    }
  }

  resolveLoading = () => {
    this.state.changeFetchState(FetchState.resolved)
  }

  @action clearSessionState() {
    this.state.menuApp = App.EMPTY
    this.pageContainerActions.sideMenuActions.reset()
    this.state.changeUserSession(UserSession.EMPTY)
    this.cookies.set("token", "")
  }

  @action.bound
  logout() {
    this.clearSessionState()
    const {path, params, hash} = this.state.router
    this.router.navTo("/login", {
      redirect: JSON.stringify({path, params, hash})
    })
  }

  @action.bound handleLogoutClick() {
    this.clearSessionState()
    this.router.navTo("/login")
  }

  createAppManagementActions() {
    const actions = new AppManagementActions({
      collinsFetch: this.collinsFetch,
      location: this.location,
      state: AppManagementState.empty(),
      appActions: this
    })

    actions.browseActions.on("result", action(() => {
      if (actions.browseActions.state.result.records.isNotEmpty) {
        this.state.userSession = this.state.userSession.changeHasApps(true)
      }
    }))

    actions.on("menuApp", (app: App) => {
      runInAction(() => {
        this.pageContainerActions!.sideMenuActions.state.app.changeExpanded(true)
        this.state.menuApp = app
      })
    })

    return actions
  }

  @action.bound
  addMessage(message: string) {
    this.state.messages.push(message)
  }

  @action.bound
  removeMessage(message: string) {
    this.state.messages = this.state.messages.filter(e => e !== message)
  }

  onLoginAs = async (userId: string) => {
    try {
      const res = await this.collinsFetch.post("/auth/login-as", {userId})
      this.cookies.set("token", "")
      this.cookies.set("token", res.token)
      await this.setUserByToken()
      await this.stateService.go("main.dashboard")
    } catch (e) {
      console.log(e)
    }
  }

  uniqueEmail = new Validator<string>({
    name: "uniqueEmail",
    message: "The email address you have entered is already registered",
    fn: email => this.collinsFetch.post("/api/users/checkEmail", {email})
  })

  get stateParams(): Record<string, string> {
    return this.state.router.params
  }

  @action
  async authFromLocalStorage() {
    const userSession = UserSession.parse(await this.collinsFetch.get("/api/v2/users/me"))
    this.state.changeUserSession(userSession)
    this.emit("user", this.state)
  }

  async refreshSelfUser(user: User) {
    if (user.id === this.state.user.id) {
      return this.authFromLocalStorage()
    }
  }

  get userSession() {
    return this.state.userSession
  }
}
