import Fade from "@material-ui/core/Fade"
import Grid from "@material-ui/core/Grid"
import {observer} from "mobx-react"
import * as React from "react"
import {Href} from "../../button"
import {Background, Padding} from "../../layout"
import {MaxLines} from "../../layout/maxLines"
import {ButtonBase} from "../components/buttonBase"
import {RevDeskTypography} from "../revDeskTypography"
import {SideMenuActions} from "@revdesk/state-actions/build/side-menu/sideMenuActions"
import {SideMenuObject} from "@revdesk/state-actions/build/side-menu/sideMenuObject"

export interface SideMenuSubItemProps {
  actions: SideMenuActions
  value: SideMenuObject
  params?: Record<string, any>
}

const styles = {
  indicator: {
    position: "absolute",
    top: 0,
    bottom: 0,
    left: 0,
    width: "1px"
  } as React.CSSProperties
}

@observer
export class SideMenuSubItem extends React.Component<SideMenuSubItemProps> {
  state = {
    expanded: false
  }

  render() {
    const {value, actions, params} = this.props
    const {state, router} = actions
    const {current} = router.state
    const isActive = current.path === value.href

    return <Fade in={isActive} style={{opacity: isActive ? undefined : 1}}>
      <Grid container direction="row" wrap="nowrap" style={{position: "relative"}}>

        <Grid item container style={styles.indicator}>
          <Grid container item style={{backgroundColor: "rgba(0,0,0,0.2)"}}/>
        </Grid>

        {isActive && <Padding xstb={2}>
          <Grid item container style={styles.indicator}>
            <Background color="white">
              <Grid container item/>
            </Background>
          </Grid>
        </Padding>}


        <Padding xsl={24}>
          <ButtonBase item container>
            <MaxLines>
              <Href path={value.href} params={params}>
                <RevDeskTypography variant={isActive ? "s13w600" : "s13w300"} color="white" line="22">
                  {value.name}
                </RevDeskTypography>
              </Href>
            </MaxLines>
          </ButtonBase>
        </Padding>
      </Grid>
    </Fade>
  }
}
