import {Spot} from "@revdesk/model/build/inventory/spot"
import {PlacementType, TPNPlacement} from "@revdesk/model/build/mediation"
import {DataClass, dataClass} from "data-class-copy"
import {observable, computed} from "mobx"
import {BrowseState, Filter} from "../browse"

export interface DeletedPlacementsFilters {
  search: Filter<string>
}

export interface TPNPlacementFilters {
  search: Filter<string>
}

export interface TPNPlacementListStateParams {
  placementType: PlacementType
  deleting: boolean
  browse: BrowseState<Spot, DeletedPlacementsFilters>
  tpn: BrowseState<TPNPlacement, TPNPlacementFilters>
}

export interface TPNPlacementListState extends TPNPlacementListStateParams, DataClass<TPNPlacementListStateParams> {

}

@dataClass
export class TPNPlacementListState {
  static empty = () => new TPNPlacementListState({
    placementType: PlacementType.INTERSTITIAL,
    deleting: false,
    browse: BrowseState.empty<Spot, DeletedPlacementsFilters>({
      search: new Filter("")
    }),
    tpn: BrowseState.empty<TPNPlacement, TPNPlacementFilters>({
      search: new Filter("")
    })
  })

  @observable deleting!: boolean

  constructor(params: TPNPlacementListStateParams) {
    Object.assign(this, params)
  }

  @computed get placements() {
    return this.tpn.result.records
  }

  set placements(records: TPNPlacement[]) {
    this.tpn.result.records = records
  }
}
