import * as React from "react"
import {Loader} from "../../loader"

const styles = {
  root: {
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
    minHeight: "100vh",
    background: `linear-gradient(135deg, #00d691 0%, #1d0047 100%)`
  }
}

export interface SplashProps extends React.HTMLAttributes<HTMLDivElement> {
  state?: "entered" | "exited" | "exiting"
}

export class Splash extends React.Component<SplashProps> {
  render() {
    const {style, state, ...props} = this.props
    return <div style={{
      ...styles.root,
      ...style,
    }} {...props}>
      <Loader state={state}/>
    </div>
  }
}
