import {DataClass, dataClass} from "data-class-copy"
import {Route, RouteParams} from "./route"
import {action} from "mobx"

export interface RouterStateParams {
  current: Route
  routes: Record<string, unknown>
}

export interface RouterState extends RouterStateParams, DataClass<RouterStateParams> {

}

@dataClass
export class RouterState {
  static empty = () => new RouterState({
    current: Route.empty(),
    routes: []
  })

  constructor(params: RouterStateParams) {
    Object.assign(this, params)
  }

  hasPath(path: string) {
    return this.routes[path] != null
  }

  get path() {
    return this.current.path
  }

  get params() {
    return this.current.params
  }

  get hash() {
    return this.current.hash
  }

  @action navTo({path, params, hash}: RouteParams, inherit = false) {
    return this.mutate({
      current: this.current.navTo({path, params, hash}, inherit)
    })
  }

  @action pushHistory(params: Record<string, string>, inherit = false) {
    return this.mutate({
      current: this.current.pushHistory(params, inherit)
    })
  }
}
