import * as React from "react"

export interface SuspensePageProps<T extends React.ComponentType<any>> {
  promise: T
  fallback: React.ReactNode
}

export class SuspensePage<T extends React.ComponentType<any>> extends React.Component<SuspensePageProps<T>> {
  render() {
    const {promise, fallback} = this.props
    const Type = promise as React.ComponentType<any>
    return <React.Suspense fallback={fallback}>
      <Type />
    </React.Suspense>
  }
}
