import {createConsumer} from "contextual-component/lib/createConsumer"
import {observer} from "mobx-react"
import * as React from "react"
import {Transition} from "react-transition-group"
import {WithRemoteText} from "remote-text/lib/react/withRemoteText"
import {Href} from "../../button"
import {providesContext} from "../../components/withContext"
import {ResultSnack} from "../../result-snack"
import {OnboardDarkProvider} from "../../theme"
import {WithAppActions} from "../containers/with-app-actions/withAppActions"
import {WithUserSession} from "../containers/withUserSession"
import {Routes} from "../routes"
import {Splash} from "../splash"
import {AppActions} from "@revdesk/state-actions/build/app/appActions"
import {GlobalMessages} from "./globalMessages"
import * as uuid from "uuid"

export interface AppProps {
  actions: AppActions
}

const AppActionsContext = React.createContext<AppActions>(null!)

Href.Consumer = createConsumer(AppActionsContext.Consumer, value => value.router)
WithUserSession.contextType = AppActionsContext
WithAppActions.contextType = AppActionsContext
WithRemoteText.contextType = AppActionsContext

@providesContext("appActions")
@observer
export class App extends React.Component<AppProps> {
  getChildContext() {
    return {
      appActions: this.props.actions
    }
  }

  componentDidMount() {
    this.props.actions.startSession()

    window.document.body.onerror = function handleError(msg, url, line, col, error) {
      const {name, message, stack} = error
      console.log('error')
      // debugger
      console.log(error.message)
      console.log(error.stack)
      // error.uuid = "MACIEJKOMOROWSKI"

      const errorJson = JSON.stringify({
        ...error, name, message, stack,
      })

      fetch('http://localhost:7000/retrace-error', { method: "POST", body: errorJson, headers: { "Content-Type": "application/json" } })
    }
  }

  render() {
    const {actions} = this.props
    const value = actions.state

    // window.document.body.addEventListener('error', function handleError(event) {
    //   console.log(event)
    // })

    return <React.Fragment>
      <Transition
        in={value.fetchState.isLoading}
        timeout={750}
        onExited={actions.resolveLoading}
        unmountOnExit
      >
        {(state: any) => <Splash id="splash" state={state}/>}
      </Transition>

      {value.fetchState.isResolved && <React.Fragment>

        <AppActionsContext.Provider value={actions}>
          <Routes actions={actions}/>
        </AppActionsContext.Provider>


        <OnboardDarkProvider>
          <GlobalMessages actions={actions}/>
          <ResultSnack open={value.err != null} variant="error"
                       onClose={actions.closeError} autoHideDuration={7000}
                       message={<span>{value.err}</span>}/>
        </OnboardDarkProvider>
      </React.Fragment>}
    </React.Fragment>
  }
}
