import {Bucket} from "./bucket"
import {StrategyTree} from "./tree"
import "@revdesk/array"

export interface HasType {
  type: string;
}

export interface FlatListTreeParams<T> {
  getKey: (t: T) => string;
  tree: StrategyTree<any>;
}

export interface FlatListTree<T extends HasType> extends FlatListTreeParams<T> {
  // buckets: Bucket<Bucket<T>>;
  buckets: Record<string, Record<string, T>>;

}

export class FlatListTree<T extends HasType> {
  constructor(params: FlatListTreeParams<T>) {
    Object.assign(this, params)
    this.buckets = params.tree.toBuckets()
  }

  getBucket(item: T) {
    return this.buckets[item.type]
  }

  remove(item: T) {
    const {tree} = this
    tree.removeFromTree(item)
    this.buckets = tree.toBuckets()
  }

  toggleItem(item: T) {
    const {tree} = this
    const bucket = this.getBucket(item)
    const key = this.getKey(item)
    if (bucket && bucket[key]) {
      tree.removeFromTree(bucket[key])
    } else {
      tree.addToTree(item)
    }
    this.buckets = tree.toBuckets()
  }

  unshiftTo(data: T[]) {
    const promoted = this.tree.rootAndFlat().flatMap(a => a.items)
    return [...promoted, ...data].distinctBy(this.getKey)
  }
}
