import {ToggleableFieldState} from "./toggleableFieldState"
import {action} from "mobx"

export interface ToggleableFieldActionsParams {
  onSubmit: () => unknown
  state: ToggleableFieldState
  defaultValue: () => string
}

export interface ToggleableFieldActions extends ToggleableFieldActionsParams {

}

export class ToggleableFieldActions {
  constructor(params: ToggleableFieldActionsParams) {
    Object.assign(this, params)
  }

  @action.bound handleKeyPress(key: string) {
    if (key === "Enter") {
      this.onSubmit()
    }
  }

  @action.bound handleKeyUp(key: string) {
    if (key === "Escape") {
      this.endEditing()
    }
  }

  @action.bound handleBlur() {
    this.state.fieldState.touched = true
    this.onSubmit()
  }

  @action.bound handleFocus() {
    // will make the error visible before the blur event which submits
    this.state.fieldState.touched = true
  }

  @action.bound endEditing() {
    this.state.fieldState.error = undefined
    this.state.fieldState.setUnsubmitted()
    this.state.isEditing = false
  }

  @action.bound startEditing() {
    const {fieldState} = this.state
    fieldState.onChange(this.defaultValue())
    this.state.isEditing = true
  }

  @action.bound endSubmitting() {
    this.state.isSubmitting = false
  }

  @action.bound startSubmitting() {
    this.state.isSubmitting = true
  }
}
