import {CreativeType} from "../mediation"

export type UnitTypeValue = "interstitial" | "mrect" | "rewarded" | "native" | "banner";

export interface UnitTypeParams {
  id: string,
  name: string
  creativeTypes: CreativeType[]
}

export interface UnitType extends UnitTypeParams {

}

const values: UnitType[] = []
const idToUnitType: Record<string, UnitType> = {}

export class UnitType {

  toJSON() {
    return this.id
  }

  static parse(params: string) {
    return idToUnitType[params] || UnitType.UNKNOWN
  }

  static INTERSTITIAL = new UnitType({
    id: "interstitial",
    name: "Interstitial",
    creativeTypes: []
  })

  static LANDSCAPE = new UnitType({
    id: "landscape",
    name: "Landscape",
    creativeTypes: []
  })

  static SQUARE = new UnitType({
    id: "square",
    name: "Square",
    creativeTypes: []
  })

  static MRECT = new UnitType({
    id: "mrect",
    name: "MREC",
    creativeTypes: []
  })

  static REWARDED = new UnitType({
    id: "rewarded",
    name: "Rewarded",
    creativeTypes: []

  })

  static NATIVE = new UnitType({
    id: "native",
    name: "Native",
    creativeTypes: []
  })

  static BANNER = new UnitType({
    id: "banner",
    name: "Banner",
    creativeTypes: []
  })

  static VERTICAL = new UnitType({
    id: "vertical",
    name: "Vertical",
    creativeTypes: []
  })

  static UNKNOWN = new UnitType({
    id: "",
    name: "Unknown",
    creativeTypes: []
  })

  static values = values

  constructor(params: UnitTypeParams) {
    Object.assign(this, params)
    values.push(this)
    idToUnitType[params.id] = this
  }
}
