import {Fun2} from "@revdesk/base-types";
import "whatwg-fetch"
import {StatusCodeError} from "./statusCodeError"

export interface CollinsRequestInit extends RequestInit {
  query?: Record<string, string>;
}

export class CollinsFetch<GET extends string=string,
  POST extends string=string,
  PUT extends string=string,
  DELETE extends string=string> {
  static $inject = ["$cookies"]

  interceptors: Fun2<number, Error | null | undefined, boolean>[] = [];

  constructor(private cookies: any) {
  }

   async fetchLink(formData: FormData) {
    const {cookies} = this
    const res = await fetch("/api/apps/icon-upload/", {
        method: "POST",
        body: formData,
        credentials: "same-origin",
        headers: {
          "Accept": "application/json, text/plain, */*",
          "Accept-Encoding": "gzip, deflate, br",
          "Accept-Language": "en-US,en;q=0.9",
          "X-XSRF-TOKEN": cookies.get("XSRF-TOKEN"),
          "Authorization": `Bearer ${cookies.get("token")}`,
        }
      }
    )
    await
    console.log("res us " + res)
  }

  async fetch(url: string, config: CollinsRequestInit = {}) {
    const {cookies} = this
    const {query = {}, headers, ...rest} = config
    const qs = `?${Object.keys(query)
      .filter(key => query[key] != null)
      .map(key => `${key}=${encodeURIComponent(query[key])}`)
      .join("&")}`
      .replace(/\?$/, "")
    const res = await fetch(url + qs, {
      ...rest,
      credentials: "same-origin",
      headers: {
        "Accept": "application/json, text/plain, */*",
        "Accept-Encoding": "gzip, deflate, br",
        "Accept-Language": "en-US,en;q=0.9",
        "X-XSRF-TOKEN": cookies.get("XSRF-TOKEN"),
        "Authorization": `Bearer ${cookies.get("token")}`,
        ...headers
      }
    })

    const body = await  Promise.resolve(res.json()).catch(() => null)
    if (res.status < 200 || res.status >= 300) {
      const handled = this.interceptors.isNotEmpty && this.interceptors.reduce((p, c) => p && c(res.status, body), true)
      throw new StatusCodeError({
        code: res.status,
        message: body ? body.message : "An error occurred"
      })
    }
    return body
  }

  async get(url: GET, query: Record<string, string> = {}, config: CollinsRequestInit = {}) {
    return this.fetch(url, {
      method: "GET",
      ...config,
      query,
      headers: {
        "Content-Type": "application/json;charset=UTF-8"
      }
    })
  }

  submit = (method: string) => (url: string, body: any = {}, config: CollinsRequestInit = {}) =>
    this.fetch(url, {
      method,
      ...config,
      body: JSON.stringify(body),
      headers: {
        "Content-Type": "application/json;charset=UTF-8"
      }
    })

  post = this.submit("POST")
  put = this.submit("PUT")
  delete = this.submit("DELETE")
}
