import Grid, {GridClassKey, GridProps} from "@material-ui/core/Grid"
import {Theme} from "@material-ui/core/styles/createMuiTheme"
import withStyles from "@material-ui/core/styles/withStyles"
import * as classNames from "classnames"
import {observer} from "mobx-react"
import * as React from "react"
import {isOut, Transitioned} from "../../components/transitioned"
import {Padding, Margin} from "../../layout"
import {Classes} from "../../util"
import {AppMenuItem} from "./appMenuItem"
import {FyberLogo} from "./icons/fyberLogo"
import {FyberShortLogo} from "./icons/fyberShortLogo"
import {MenuItem} from "./menuItem"
import {MenuItemExpandable} from "./menuItemExpandable"
import {SideMenuActions} from "@revdesk/state-actions/build/side-menu/sideMenuActions"
import {SideMenuSubItem} from "./sideMenuSubItem"


const styles = (theme: Theme) => {
  const {green, darkBlue} = theme.palette.custom
  return {
    root: {
      overflow: "hidden",
      background: `linear-gradient(225deg, ${green} 0%, ${darkBlue} 100%)`,
      transition: "width 300ms cubic-bezier(0.4, 0, 0.2, 1) 0ms",
      flexShrink: 0
    },
  }
}

const style = {
  icon: {
    height: "33px",
    width: "auto"
  }
}

export interface SideMenuProps extends GridProps {
  actions: SideMenuActions
  classes?: Classes<typeof styles> & Record<GridClassKey, string>
}

export const SideMenu: React.FunctionComponent<SideMenuProps> = (withStyles(styles) as any)(
  observer(
    function SideMenu(props: SideMenuProps) {
      const {actions, className, classes, ...rest} = props
      const {userSession, menuApp} = actions.appActions.state
      const {state: sideMenu} = actions
      const {expanded} = sideMenu
      return <Transitioned in={expanded} timeout={300}>
        {state => <Grid container direction="column" wrap="nowrap"
                        className={classNames(className, classes!.root)}
                        style={{width: isOut(state) ? "69px" : "201px", overflow: "auto"}}
                        {...rest}>
          <Padding xsrl={16} xstb={24}>
            <Grid item id="fyber_logo">
              {expanded ?
                <FyberLogo fill="white" style={style.icon}/> :
                <FyberShortLogo fill="white" style={style.icon}/>}
            </Grid>
          </Padding>

          <Grid item container direction="column" wrap="nowrap" style={{flex: 1}}>
            {userSession.user.isPublisher && userSession.canSessionViewInventory && <Grid item>
              <MenuItem value={sideMenu.apps} actions={actions}/>
            </Grid>}

            {menuApp.id && <Grid item>
              <AppMenuItem value={sideMenu.app} actions={actions}/>
            </Grid>}

            <Grid item>
              <MenuItemExpandable value={sideMenu.reports} actions={actions}>
                <>
                  {(userSession.user.internal || userSession.user.isExternalSupply) &&
                  <SideMenuSubItem actions={actions} value={sideMenu.overview}/>}
                  {(userSession.user.internal || userSession.user.isExternalSupply) &&
                  <SideMenuSubItem actions={actions} value={sideMenu.programmatic}/>}
                  {(userSession.user.internal || userSession.user.isExternalDemand) &&
                  <SideMenuSubItem actions={actions} value={sideMenu.bids}/>}
                </>
              </MenuItemExpandable>
            </Grid>

            <Grid item>
              <MenuItemExpandable value={sideMenu.monetize} actions={actions}>
                <>
                  {userSession.user.isExternalSupply &&
                  <SideMenuSubItem actions={actions} value={sideMenu.blockingControls}/>}
                  {userSession.user.canViewUsers && <SideMenuSubItem actions={actions} value={sideMenu.users}/>}
                </>
              </MenuItemExpandable>
            </Grid>

            {userSession.canSessionViewAudience && <Grid item>
              <MenuItem value={sideMenu.audience} actions={actions}/>
            </Grid>}

            {userSession.canViewFinance && <Grid item>
              <MenuItemExpandable value={sideMenu.finance} actions={actions}>
                <>
                  {userSession.canSaveFinance && <SideMenuSubItem actions={actions} value={sideMenu.financeDetails}/>}
                  <SideMenuSubItem actions={actions} value={sideMenu.financeInvoices}/>
                  <SideMenuSubItem actions={actions} value={sideMenu.financeHistory}/>
                </>
              </MenuItemExpandable>
            </Grid>}

            <Margin xst="auto">
              <Grid item>
                <MenuItem external value={sideMenu.documentation} actions={actions}/>
              </Grid>
            </Margin>

          </Grid>
        </Grid>}
      </Transitioned>
    }
  )
)
