import {IPromise, IScope} from "angular";
import {IState, IStateOptions, IStateService} from "angular-ui-router"

export interface IStateServiceBridge {
  go(to: any, params?: {}, options?: IStateOptions): Promise<any>
  search(params?: {}, options?: IStateOptions): Promise<any>
}

export interface StateServiceBridge {
}

export class StateServiceBridge implements IStateServiceBridge {
  constructor(private $scope: IScope, private $state: IStateService) {
  }

  go(to: string, params?: {}, options?: IStateOptions) {
    const retVal = this.$state.go(to, params, options)
    this.$scope.$applyAsync()
    return retVal as any as Promise<any>
  }

  search(params?: {}, options?: IStateOptions) {
    const retVal = this.$state.go(".", params, options)
    this.$scope.$applyAsync()
    return retVal as any as Promise<any>
  }
}
