import CircularProgress from "@material-ui/core/CircularProgress"
import * as React from "react"
import {Margin} from "../../layout"
import {MenuPage} from "./menuPage"
import {SuspensePage} from "./suspensePage"
import {TitleProps} from "../app-bar/title"

export interface SuspenseMenuPageProps<T extends React.ComponentType<any>> {
  promise: T
  title: string
  titleProps?: TitleProps
}

export class SuspenseMenuPage<T extends React.ComponentType<any>> extends React.Component<SuspenseMenuPageProps<T>> {
  render() {
    const {promise, title, titleProps} = this.props
    return (
      <MenuPage title={title} titleProps={titleProps}>
        <SuspensePage
          fallback={
            <Margin xs="auto">
              <CircularProgress disableShrink />
            </Margin>
          }
          promise={promise}
        />
      </MenuPage>
    )
  }
}
