import * as React from "react"
import {MaxLines, MaxLinesProps, Styleable} from "../../layout/maxLines"
import {RevDeskTypography} from "../revDeskTypography"
import {Tooltip} from "./tooltip"

export interface ReadingProps<P extends Styleable> extends Partial<MaxLinesProps<P>> {
  text: string
  children: React.ReactElement<P>
}

export function Reading<P extends Styleable>(props: ReadingProps<P>) {
  const {text, ...rest} = props
  return <Tooltip
    placement="bottom-start"
    enterDelay={500}
    title={
      <RevDeskTypography variant="s12w400" color="white">
        {text}
      </RevDeskTypography>
    }>
    <MaxLines {...rest} />
  </Tooltip>
}
