import {Theme} from "@material-ui/core/styles/createMuiTheme"
import withStyles from "@material-ui/core/styles/withStyles"
import * as classNames from "classnames"
import * as React from "react"
import {breakpointStyles} from "./breakpointStyles"
import {SizeProps} from "./size"

const sizes = [
  -100, 2, -4, -8, -12, -24,
  ...new Array(15).fill(null).map((_, i) => i * 4),
  "auto"
]

const styles = (theme: Theme) => ({
  ...breakpointStyles(
    theme,
    sizes,
    ["xs"],
    "padding",
    (size, ) => ({padding: size})
  ),
  ...breakpointStyles(
    theme,
    sizes,
    ["xs"],
    "paddingLeftRight",
    (size) => ({paddingLeft: size, paddingRight: size})
  ),
  ...breakpointStyles(
    theme,
    sizes,
    ["xs"],
    "paddingTopBottom",
    (size) => ({paddingTop: size, paddingBottom: size})
  ),
  ...breakpointStyles(
    theme,
    sizes,
    ["xs"],
    "paddingBottom",
    (size) => ({paddingBottom: size})
  ),
  ...breakpointStyles(
    theme,
    sizes,
    ["xs"],
    "paddingTop",
    (size) => ({paddingTop: size})
  ),
  ...breakpointStyles(
    theme,
    sizes,
    ["xs"],
    "paddingRight",
    (size) => ({paddingRight: size})
  ),
  ...breakpointStyles(
    theme,
    sizes,
    ["xs"],
    "paddingLeft",
    (size) => ({paddingLeft: size})
  ),
})

export interface PaddingProps<P extends { className?: string }> extends SizeProps {
  children: React.ReactElement<P>
  classes?: Record<string, string>
  className?: string
}

export const Padding = withStyles(styles, {name: "Padding", index: 1})(
  <P extends { className?: string }>(props: PaddingProps<P>) => {
    const {
      children, classes, className: classNameProp, xs, xsb, xsl, xsr, xsrl,
      xst, xstb, ...rest
    } = props
    const {type: ComponentType, props: componentProps} = children
    const className = classNames(classNameProp, componentProps.className, {
      [classes![`padding-xs-${xs}`]]: xs != null,
      [classes![`paddingLeftRight-xs-${xsrl}`]]: xsrl != null,
      [classes![`paddingTopBottom-xs-${xstb}`]]: xstb != null,
      [classes![`paddingBottom-xs-${xsb}`]]: xsb != null,
      [classes![`paddingTop-xs-${xst}`]]: xst != null,
      [classes![`paddingRight-xs-${xsr}`]]: xsr != null,
      [classes![`paddingLeft-xs-${xsl}`]]: xsl != null,
    })
    return <ComponentType {...rest} {...componentProps} className={className}/>
  }
)
