import {Unit} from "../../inventory/unit"

const DEFAULT_FILTERED_UNIT: FilteredUnitJS = {
  filteredProtocols: [],
  filteredAPIs: []
}

export interface FilteredUnitJS {
  filteredProtocols?: number[]
  filteredAPIs?: number[]
}

export interface FilteredUnit extends FilteredUnitJS {
}

export class FilteredUnit {
  static DEFAULT_FILTERED_UNIT = DEFAULT_FILTERED_UNIT

  constructor(filtered: FilteredUnitJS) {
    this.filteredAPIs = filtered.filteredAPIs
    this.filteredProtocols = filtered.filteredProtocols
  }

  toJS(unit: Unit) {
    return {
      filteredProtocols: this.filteredProtocols,
      filteredAPIs: this.filteredAPIs
    }
  }
}
