export class Bucket<T> {
  map: Record<string, T> = {}

  getCreate(key: string, value: () => T) {
    const {map} = this
    if (!this.exists(key)) {
      map[key] = value()
    }
    return map[key]
  }

  exists(key: string) {
    return !!this.map[key]
  }

  put(key: string, item: T) {
    const {map} = this
    map[key] = item
  }

  remove(key: string) {
    const {map} = this
    map[key] = null!
  }

  toggle(key: string, item: T) {
    if (this.exists(key)) {
      this.remove(key)
    } else {
      this.put(key, item)
    }
  }

  get(key: string) {
    return this.map[key]
  }
}
