import {SvgIconProps} from "@material-ui/core/SvgIcon"
import Android from "@material-ui/icons/Android"
import Web from "@material-ui/icons/Web"
import {Platform} from "@revdesk/model/build/platform"
import * as React from "react"
import {block} from "tsla-util/lib/block"
import {Apple} from "../../icons/apple"

export interface PlatformIconProps extends SvgIconProps {
  platform: Platform
}

export function PlatformIcon(props: PlatformIconProps) {
  const {platform, ...rest} = props
  const ComponentType = block(() => {
    switch (platform.id) {
      case Platform.iOS.id: return Apple
      case Platform.ANDROID.id: return Android
      case Platform.MOBILE_WEB.id: return Web
      default: return Web
    }
  })

  return <ComponentType {...rest} />
}
