const idToFilteredAPI: Record<string, FilteredAPI> = {}

export interface FilteredAPIParams {
  id: string
  name: string
}

export interface FilteredAPI extends FilteredAPIParams {

}

export class FilteredAPI {
  static parse(params: string) {
    return idToFilteredAPI[params] || FilteredAPI.unknown
  }

  static unknown = new FilteredAPI({
    id: "",
    name: "Unknown API"
  })

  static vpaid = new FilteredAPI({
    id: "2",
    name: "VPAID 2.0"
  })

  static mraid = new FilteredAPI({
    id: "5",
    name: "MRAID-2"
  })

  constructor(params: FilteredAPIParams) {
    Object.assign(this, params)
    idToFilteredAPI[params.id] = this
  }

  toJSON() {
    return this.id
  }
}
