import "@revdesk/array"

export function plus(delta: number) {
  return ` +${delta}`
}

export function andMore(delta: number) {
  return ` and ${delta} more`
}

export function plusMore(delta: number) {
  return ` + ${delta} more`
}

export function remaining(arr: string[], max: number = 1, fn = plus) {
  return arr.length > max ? fn(arr.length - max) : ""
}

export function shortMultiLine(arr: string[], max: number = 1, fn = plus) {
  const concat = remaining(arr, max, fn)
  return arr.take(max).join(", ") + concat
}
