export interface InventoryRulesParams {
  publishers: Array<string>;
  units: Array<string>;
}

export interface InventoryRules extends InventoryRulesParams {

}

export class InventoryRules {
  static EMPTY = new InventoryRules({
    publishers: [],
    units: []
  })

  static fromTree(tree: any) {
    const publishers = tree.branches.records
    return {
      publishers: publishers.filter((p: any) => p.init).map((p: any) => p.id).toArray(),
      units: publishers.toList()
        .flatMap((node: any) => node.leafs())
        .map((u: any) => u.id)
        .toArray()
    }
  }

  constructor(params: InventoryRulesParams) {
    this.publishers = params.publishers
    this.units = params.units
  }
}
