import {UnitFormatValue} from "../spots/unitFormat";
import {UnitType} from "../spots/unitType"
import {PlacementType} from "./placementType"

const idToCreativeType: Record<string, CreativeType> = {}
const nameToCreativeType: Record<string, CreativeType> = {}

export interface CreativeTypeParams {
  id: string,
  name: string
  placementType: PlacementType
  format: "video" | "static" | ""
  oldType: UnitType
  unitFormatsIDs: UnitFormatValue[]
}

export interface CreativeType extends CreativeTypeParams {
}

export class CreativeType {
  static parse(params: string) {
    return idToCreativeType[params] || CreativeType.unknown
  }

  static getTypeFromName(name: string) {
    return nameToCreativeType[name.toLowerCase()]
  }

  static video : CreativeType = new CreativeType({
    id: "1",
    name: "Video",
    placementType: PlacementType.interstitial,
    oldType: UnitType.INTERSTITIAL,
    format: "video",
    unitFormatsIDs: []
  })

  static static: CreativeType = new CreativeType({
    id: "2",
    name: "Display",
    placementType: PlacementType.interstitial,
    format: "static",
    oldType: UnitType.INTERSTITIAL,
    unitFormatsIDs: []
  })

  static rewardedVideo : CreativeType= new CreativeType({
    id: "3",
    name: "Rewarded Video",
    placementType: PlacementType.rewarded,
    format: "video",
    oldType: UnitType.REWARDED,
    unitFormatsIDs: []
  })

  static banner : CreativeType= new CreativeType({
    id: "4",
    name: "Banner",
    placementType: PlacementType.banner,
    oldType: UnitType.BANNER,
    format: "static",
    unitFormatsIDs: []
  })

  static mrec : CreativeType= new CreativeType({
    id: "5",
    name: "Mrec",
    placementType: PlacementType.banner,
    format: "static",
    oldType: UnitType.MRECT,
    unitFormatsIDs: []
  })

  static unknown : CreativeType= new CreativeType({
    id: "",
    name: "Unknown",
    placementType: PlacementType.unknown,
    oldType: UnitType.UNKNOWN,
    format: "",
    unitFormatsIDs: []
  })

  constructor(params: CreativeTypeParams) {
    Object.assign(this, params)
    idToCreativeType[params.id] = this
    nameToCreativeType[params.name.toLowerCase()] = this
    this.placementType.creativeTypes.push(this)
    this.oldType.creativeTypes.push(this)
  }

  toJSON() {
    return this.id
  }

  get isNotUnknown() {
    return this !== CreativeType.unknown
  }

  get toRecord() {
    return this.id ? this.id : null
  }

  get isUnknown() {
    return this === CreativeType.unknown
  }

  get isMREC() {
    return this === CreativeType.mrec
  }
}
