import {DataType, NestedItem} from "@revdesk/util"
import {dataClass, DataClass} from "data-class-copy/lib"
import {applyMixins} from "ts-trait/build"
import {DataTypes} from "../dataTypes"
import {HasID, Searchable} from "../searchable"
import {Publisher} from "./publisher"

export interface ContentParams extends HasID {
  name: string;
  publisherId: string;
  publisher?: Publisher | null;
}

export interface Content extends ContentParams, Searchable, NestedItem<Publisher, any>, DataClass<Content> {

}

@dataClass
@applyMixins([NestedItem, Searchable])
export class Content implements NestedItem<Publisher, any> {
  static EMPTY = new Content({
    id: "",
    name: "",
    publisherId: "",
    publisher: null
  })

  static parse(params: any = {}) {
    return Content.EMPTY.copy({
      id: params.id != null ? String(params.id) : "",
      name: params.name != null ? String(params.name) : "",
      publisherId: params.publisherId,
      publisher: params.publisher && Publisher.parse(params.publisher)
    })
  }

  constructor(params: ContentParams) {
    Object.assign(this, params)
  }

  get parent() {
    return this.publisher
  }

  get children() {
    return [] as any[]
  }

  changeChildren() {
    return this
  }

  get type() {
    return "content"
  }

  get hash() {
    return this.type + "_" + this.id
  }

  get dataType() {
    return DataTypes.CONTENT
  }
}
