import {App} from "@revdesk/model/build/inventory"
import {AppStatus} from "@revdesk/model/build/inventory/appStatus"
import {PlacementType} from "@revdesk/model/build/mediation"
import {dataClass, DataClass} from "data-class-copy/lib"
import {observable} from "mobx"

export interface AppCardStateParams {
  app: App
  removing: boolean
  placementsTypes: Array<PlacementType>
}

export interface AppCardState extends AppCardStateParams, DataClass<AppCardState> {

}

@dataClass
export class AppCardState {
  static empty = new AppCardState({
    app: App.EMPTY,
    removing: false,
    placementsTypes: []
  })

  static parse(params: any = {}) {
    return AppCardState.empty.copy({
      app: App.parse(params.app),
      placementsTypes: (params.placementsTypes || []).map(PlacementType.parse)
    })
  }

  @observable app!: App
  @observable removing!: boolean

  constructor(params: AppCardStateParams) {
    Object.assign(this, params)
  }

  get isActive() {
    return this.app.status != null && this.app.status.id == AppStatus.ACTIVE.id
  }

  get id () {
    return this.app.id
  }
}
