import {parseString} from "tsla-util/lib/parse"
import {FieldState} from "../extensions/formstate"

export type FilterValue = string | string[]

export interface FilterParams<T extends FilterValue = FilterValue> {
  value: T
}

export interface Filter<T extends FilterValue = FilterValue> extends FilterParams<T> {

}

export class Filter<T extends FilterValue = FilterValue> extends FieldState<T> {
  static parse(params: FilterValue | undefined) {
    return new Filter(
      Array.isArray(params) ?
        params.map(parseString) :
        parseString(params)
    )
  }

  toJSON() {
    return this.value
  }
}
