import {observer} from "mobx-react"
import * as React from "react"
import {Tile} from "../../components/tile"
import {Color} from "../../layout"
import {FontSize} from "../../layout/fontSize"
import {MaxLines} from "../../layout/maxLines"
import {RevDeskTypography} from "../revDeskTypography"
import {MenuItemBG} from "./menuItemBG"
import {MenuItemExpansionPanel} from "./menuItemExpansionPanel"
import {SideMenuActions} from "@revdesk/state-actions/build/side-menu/sideMenuActions"
import {SideMenuBox} from "@revdesk/state-actions/build/side-menu/sideMenuBox"
import {RenderIcon} from "./renderIcon"

export interface MenuItemExpandableProps {
  actions: SideMenuActions
  value: SideMenuBox
  children?: React.ReactNode
}


export const MenuItemExpandable = observer(
  (
    function MenuItemExpandable(props: MenuItemExpandableProps) {
      const {value, actions, children} = props
      const {expanded: menuExpanded} = actions.state
      const {icon} = value
      const {current} = actions.router.state
      const isActive = current.path === value.firstLevel.href ||
        value.secondLevel.some(e => e.href === current.path)

      return <MenuItemBG isActive={isActive}>
        <MenuItemExpansionPanel
          expanded={menuExpanded && value.expanded}
          onExpand={() => actions.changeMenuItemExpanded(value)}
          actions={actions}
          active={isActive}
          summary={
            <Tile
              spaceBetween={8}
              avatar={
                <FontSize fontSize="24">
                  <Color color="white">
                    <RenderIcon value={icon}/>
                  </Color>
                </FontSize>
              }
              header={
                menuExpanded ? <MaxLines>
                  <RevDeskTypography variant="s15w400" color="white">
                    {value.firstLevel.name}
                  </RevDeskTypography>
                </MaxLines> : undefined
              }
            />
          }
          details={children}
        />
      </MenuItemBG>
    }
  )
)
