import {DataClass, dataClass} from "data-class-copy"
import {Option} from "tsla-util/lib/option"
import {parseBool, parseString} from "tsla-util/lib/parse"
import {Network, NetworkInstance} from "../demand"
import {Spot} from "../inventory"
import {CreativeType} from "./creativeType"
import {ParamValue} from "./paramValue"
import {PlacementType} from "./placementType"
import {TPNPlacement} from "./tpnPlacement"

export interface TPNIntegrationSchema {
  id: number
  appId: number | string
  networkId: number | string
  updateAt: Date
  enabled: boolean
}

export interface TPNIntegrationInstance extends TPNIntegrationSchema {

}

export interface TPNIntegrationParams {
  id: string
  appId: string
  networkId: string
  paramValues: ParamValue[]
  placements: TPNPlacement[]
  network: Network
  placementTypes: PlacementType[]
  creativeTypes: CreativeType[]
  enabled: boolean
}

export interface TPNIntegration extends TPNIntegrationParams, DataClass<TPNIntegrationParams> {

}

@dataClass
export class TPNIntegration {
  static empty = new TPNIntegration({
    id: "",
    appId: "",
    networkId: "",
    paramValues: [],
    placements: [],
    network: Network.EMPTY,
    placementTypes: [],
    creativeTypes: [],
    enabled: false
  })

  static parse(params: any = {}) {
    return TPNIntegration.empty.copy({
      id: parseString(params.id),
      appId: parseString(params.appId),
      networkId: parseString(params.networkId),
      paramValues: (params.paramValues || []).map(ParamValue.parse),
      network: Option.of(params.network).map(Network.parse).getOrElse(Network.EMPTY),
      placements: (params.placements || []).map(TPNPlacement.parse),
      placementTypes: (params.placementTypes || []).map(PlacementType.parse),
      creativeTypes: (params.creativeTypes || []).map(CreativeType.parse),
      enabled: parseBool(params.enabled)
    })
  }

  static parseFromNetwork(params: NetworkInstance, appId: string) {
    const network = Network.parse(params)
    if (params.tpnIntegration == null) {
      return TPNIntegration.fromNetwork(network, appId)
    }
    return TPNIntegration.parse(params.tpnIntegration).mutate({
      network
    })
  }

  static fromNetwork(network: Network, appId: string) {
    return TPNIntegration.empty.copy({
      network, networkId: network.id, appId
    })
  }

  constructor(params: TPNIntegrationParams) {
    Object.assign(this, params)
  }

  get toLite() {
    const retVal: Partial<TPNIntegrationParams> = {
      id: this.id,
      appId: this.appId,
      networkId: this.networkId,
      enabled: this.enabled
    }

    if (this.paramValues.isNotEmpty) {
      retVal.paramValues = this.paramValues.map(e => e.toLite as ParamValue)
    }

    if (this.placements.isNotEmpty) {
      retVal.placements = this.placements.map(e => e.toLite as TPNPlacement)
    }

    return retVal
  }

  get supportsPlacements() {
    return this.placementTypes.isNotEmpty
  }

  get supportsUnits() {
    return this.creativeTypes.isNotEmpty
  }

  get hasUnsavedParam() {
    return this.paramValues.some(e => !e.paramId)
  }

  get toggleStatus() {
    return this.copy({
      enabled: !this.enabled
    })
  }

  get disabled() {
    return !this.enabled
  }

  toggleEnabled(enabled = !this.enabled) {
    return this.copy({enabled})
  }

  toGenericPlacement(placement: Spot) {
    return TPNPlacement.empty.copy({
      name: "Generic",
      paramValues: this.paramValues,
      placementType: placement.placementType,
      prog: this.isProgMediation,
      tpnIntegrationId: this.id
    })
  }

  get isProgMediation() {
    return this.network.isProgMediation
  }

  get enabledAndComplete() {
    return this.enabled && this.completeAppParams
  }

  get completeAppParams() {
    return this.paramValues.every(e => e.complete)
  }

  supportsPlacementType(placementType: PlacementType) {
    return this.placementTypes.indexOf(placementType) !== -1
  }
}
