import {Constructor, Fun} from "@revdesk/base-types";

const rgx = /^class\s/
const classConvention = /^[A-Z]/

export function isClass(e: Fun | Constructor<any>): e is Constructor<any> {
  return typeof e === "function" && (
    rgx.test(Function.prototype.toString.call(e)) || classConvention.test(e.name)
  )
}
