import {dataClass, DataClass} from "data-class-copy/lib"
import {App} from "./app"
import {Publisher} from "./publisher"
import {Spot} from "./spot"
import {Unit} from "./unit"

export interface InventorySearchResultParams {
  publishers: Array<Publisher>;
  apps: Array<App>;
  spots: Array<Spot>;
  units: Array<Unit>;
}

export interface InventorySearchResult extends InventorySearchResultParams, DataClass<InventorySearchResult> {

}

@dataClass
export class InventorySearchResult {
  static EMPTY = new InventorySearchResult({
    publishers: [],
    apps: [],
    spots: [],
    units: []
  })

  static parse(params: any = {}) {
    return InventorySearchResult.EMPTY.copy({
      publishers: (params.publishers || []).map(Publisher.parse),
      apps: (params.apps || []).map(App.parse),
      spots: (params.spots || []).map(Spot.parse),
      units: (params.units || []).map(Unit.parse),
    })
  }

  constructor(params: InventorySearchResultParams) {
    Object.assign(this, params)
  }

  flatten() {
    const {publishers, apps, spots, units} = this
    return [...publishers, ...apps, ...spots, ...units]
  }
}
