import {UserSession} from "@revdesk/model/build/userSession"
import * as assert from "assert"
import * as React from "react"
import {Component, ComponentClass, FunctionComponent} from "react"

export interface WithUserSessionContext {
  userSession: UserSession
}

export interface WithUserSessionProps {
  children: (userSession: UserSession) => React.ReactElement<any>
}

export class WithUserSession extends React.Component<WithUserSessionProps> {
  static contextType: React.Context<WithUserSessionContext>
  context!: WithUserSessionContext

  render() {
    return this.props.children(this.context.userSession)
  }
}

export function withUserSessionFP<P extends WithUserSessionContext>(FunctionComponent: FunctionComponent<P>) {
  return function WithUserSessionHOC(props: P) {
    return <WithUserSession>
      {
        userSession => <FunctionComponent userSession={userSession} {...props}/>
      }
    </WithUserSession>
  }
}

export function withUserSession(ComponentClass: ComponentClass) {
  ComponentClass.contextType = WithUserSession.contextType
  assert(WithUserSession.contextType != null)
  Object.defineProperty(ComponentClass.prototype, "userSession", {
    get(this: Component<any>) {
      return this.context.state.userSession
    }
  })
}
