import {DataType} from "./dataType"
import {applyMixins} from "ts-trait/build";

export type AnyNested = NestedItem<any, any>

export interface NestedItemParams {
  id: string;
  name: string;
}

export interface NestedItem<P extends AnyNested, C extends AnyNested, R=any> extends NestedItemParams {
}

export class NestedItem<P extends AnyNested, C extends AnyNested, R=any> {

  static EMPTY = new NestedItem({
    id: "",
    name: ""
  })

  constructor(params: NestedItemParams) {
    Object.assign(this, params)
  }

  static toName = (e: AnyNested) => e.name
  static toID = (e: AnyNested) => e.id

  get parent(): P {
    return null!
  }

  get children(): C[] {
    return []
  }

  get size(): number {
    return 0
  }

  get type() {
    return this.dataType.id
  }

  get dataType() {
    return DataType.ANY
  }

  changeChildren(children: Array<C>): NestedItem<P, C> {
    return this
  }

  withoutChildren(): NestedItem<P, C> {
    return this
  }

  topToBottom(): NestedItem<AnyNested, AnyNested> {
    let data = this as AnyNested
    while (data.parent != null) {
      data = data.parent.changeChildren([data])
    }
    return data
  }

  leafs(): Array<AnyNested> {
    return this.children.length ? this.children.flatMap(c => c.leafs()) : [this]
  }

  get hash() {
    return this.type + "_" + this.id
  }

  search(q: string = ""): boolean {
    return this.id == q || this.name.toLowerCase().indexOf(q.toLowerCase()) !== -1
  }
}

export const nestedItem = applyMixins([NestedItem])
