import {TPNPlacement} from "@revdesk/model/build/mediation"
import {dataClass, DataClass} from "data-class-copy"
import {ValidatableMapOrArray, ValidationResponse} from "formstate"
import {action} from "mobx"
import {FieldState, FormState} from "../extensions/formstate"

import {ParamValueFieldState, ParamValueFormState, ParamValueValidator} from "../param-value-form"

export type PlacementValidator = (form: TPNPlacementFormState) => ValidationResponse | Promise<ValidationResponse>

export type TPNPlacementFormStateFields = ValidatableMapOrArray & {
  placementName: FieldState<string>
  paramValues: ParamValueFormState
}

export interface TPNPlacementFormStateParams {
  placement: TPNPlacement
}

export interface TPNPlacementFormStateConfig extends TPNPlacementFormStateParams {
  validators: ParamValueValidator[]
  placementValidators: PlacementValidator[]
}

export interface TPNPlacementFormState extends TPNPlacementFormStateParams, DataClass<TPNPlacementFormStateParams> {

}

@dataClass
export class TPNPlacementFormState extends FormState<TPNPlacementFormStateFields> {
  static empty = new TPNPlacementFormState({
    placement: TPNPlacement.empty,
    validators: [],
    placementValidators: []
  })

  constructor(params: TPNPlacementFormStateConfig) {
    super({
      placementName: new FieldState(params.placement.name).disableAutoValidation()
        .validators(
          ...params.placementValidators.map(
            validator => () => validator(this)
          )
        ),
      paramValues: ParamValueFieldState.fromParamValues(params.placement.paramValues, params.validators)
    })
    this.placement = params.placement
  }

  get toPlacement() {
    return this.placement.copy({
      name: this.$.placementName.value
    })
  }

  @action clear() {
    this.$.placementName.value = ""
    this.$.paramValues.$.forEach(e => e.clear())
  }
}
