import {Constructor, Fun1, Method, Method1} from "@revdesk/base-types"

export function delegate<T, E>(derived: Constructor<T>, by: Method<E, T>, customHandler?: Method1<E>) {
  return function (ctor: any) {
    for (const key of Object.getOwnPropertyNames(derived.prototype)) {
      if (key !== "constructor") {
        const desc = Object.getOwnPropertyDescriptor(derived.prototype, key)

        const delegationFn = function (this: any, ...args: any[]) {
          const targetMember = by.call(this)
          const targetFn = targetMember[key]
          const retVal = targetFn.apply(targetMember, args)
          return customHandler ? customHandler.call(this, retVal) : retVal
        }

        const delegationDesc = ["value", "get", "set"]
          .filter((k) => desc![k as keyof PropertyDescriptor])
          .reduce((a, b) => ({
            ...a,
            [b]: delegationFn
          }), {})

        Object.defineProperty(ctor.prototype, key, {
          ...desc,
          ...delegationDesc
        })
      }
    }
  }
}
