import {Fun1, Fun2} from "@revdesk/base-types";
import {Option} from "tsla-util/lib/option"

const _uniqBy = require("lodash/uniqBy")
const _keyBy = require("lodash/keyBy")
const _differenceBy = require("lodash/differenceBy")
const _groupBy = require("lodash/groupBy")
const _orderBy = require("lodash/orderBy")
const _mapValues = require("lodash/mapValues")
const _identity = require("lodash/identity")
const _difference = require("lodash/difference")
const _take = require("lodash/take")
const _map = require("lodash/map")

export type CallFn<T, U> = (value: T, index: number, array: T[]) => U

declare global {
  interface Array<T> {
    distinctBy<E>(fn: (t: T) => E): Array<T>;

    mergeBy<K>(other: Array<T>, keyFn: (t: T) => K, fn: (t1: T, t2: T) => T): Array<T>;

    flatMap<U>(cb: CallFn<T, U | U[]>): U[];

    joinLeft<E, V>(array: Array<E>, keyFn1: (t: T) => any, keyFn2: (e: E) => any, joiner: (t: T, e?: E) => V): Array<V>;

    rightJoin<E>(array: Array<E>, keyFn1: (t: T) => any, keyFn2: (e: E) => any, joiner: (t: T, e?: E[]) => T): Array<T>;

    keyBy<E = T>(keyFn: string | ((t: T, i: number) => any), mapFn?: Fun1<T, E>): Record<string, E>;

    differenceBy<K>(other: Array<T>, fn?: (t: T) => K): Array<T>;

    orderBy(iteratees?: Array<Fun1<T, any>>,
            orders?: Array<"asc" | "desc">): T[];

    groupBy<E = T[]>(keyFn: string | ((t: T) => any), mapFn?: Fun1<T[], E>): Record<string, E>;

    groupBy(keyFn: string | ((t: T) => any)): Record<string, Array<T>>;

    bucketBy<E = T[]>(keyFn: string | ((t: T) => any), mapFn?: Fun1<T[], E>): Array<E>;

    isEmpty: boolean;

    toQueryString(): Record<string, string>;

    concatSelf(): T[];

    difference(other: T[]): T[];

    containsAll<K>(other: T[], fn?: (t: T) => K): boolean;

    containsNone(other: T[]): boolean;

    isNotEmpty: boolean;

    take(n?: number): T[];

    delimit<E>(delimiter: (t: T) => E): Array<T | E>;

    zipWithIndex: [T, number][];

    toMap<K, V = T>(key: Fun1<T, V>, value?: Fun1<T, V>): Map<K, V>;

    first(): Option<T>

    replaceFrom(from: number, ...replacement: T[]): T[]

    replaceAt(predicate: (value: T, index: number, obj: T[]) => boolean, ...replacement: T[]): T[]
    pushAt(predicate: (value: T, index: number, obj: T[]) => boolean, ...replacement: T[]): T[]

    findOne(predicate: (value: T, index: number, obj: T[]) => boolean, thisArg?: any): Option<T>
  }
}

export function distinctBy<T, E>(this: Array<T>, fn: (t: T) => E) {
  return _uniqBy(this as any, fn)
}

export function mergeBy<T, K>(this: Array<T>, other: Array<T>, keyFn: (t: T) => K, fn: (t1: T, t2: T) => T) {
  const map = _keyBy(other, keyFn)
  return this.map(e => {
    const exists = map[keyFn(e)]
    return exists != null ? fn(e, exists) : e
  }).concat(other).distinctBy(keyFn)
}

export function flatMap<T, U>(this: Array<T>, cb: CallFn<T, U>): U[] {
  return Array.prototype.concat.apply([], this.map(cb))
}

export function joinLeft<T, E, V>(
  this: Array<T>,
  array: Array<E>,
  keyFn1: (t: T) => any,
  keyFn2: (e: E) => any,
  joiner: (t: T, e?: E) => V
): Array<V> {
  const map = array.keyBy(keyFn2)
  return this.map(e => joiner(e, map[keyFn1(e)]))
}

export function rightJoin<T, E>(
  this: Array<T>,
  array: Array<E>,
  keyFn1: Fun1<T, any>,
  keyFn2: (e: E) => any,
  joiner: (t: T, e?: E[]) => T
): Array<T> {
  const map = _groupBy(array, keyFn2)
  return this.map(e => joiner(e, map[keyFn1(e)]))
}

export function differenceBy<T, K>(this: Array<T>, other: Array<T>, fn?: (t: T) => K): Array<T> {
  return _differenceBy(this, other, fn)
}

export function orderBy<T>(this: Array<T>,
                           iteratees?: Array<Fun1<T, any>>,
                           orders?: Array<"asc" | "desc">) {
  return _orderBy(this, iteratees, orders)
}

export function groupBy<T, E = T>(this: T[], keyFn: string | ((t: T) => any), mapFn: Fun1<T[], E> = _identity) {
  return _mapValues(_groupBy(this, keyFn), mapFn)
}

export function keyBy<T, E = T>(
  this: Array<T>,
  keyFn: string | Fun2<T, number, any>,
  mapFn: Fun1<T, E> = _identity
): Record<string, T> {
  return _mapValues(_keyBy(this, keyFn), mapFn)
}

export function bucketBy<T, E = T>(this: T[], keyFn: string | ((t: T) => any), mapFn: Fun1<T[], E> = _identity) {
  return _map(_groupBy(this, keyFn), mapFn)
}

export function concatSelf<T>(this: T[]) {
  return this.concat(this)
}

export function isNotEmpty<T>(this: T[]) {
  return !!this.length
}

function isEmpty<T>(this: Array<T>) {
  return !this.length
}

export function difference<T>(this: T[], other: T[]) {
  return _difference(this, other)
}

export function containsAll<T, K>(this: T[], other: T[], fn?: (t: T) => K) {
  return other.differenceBy(this, fn).length === 0
}

export function containsNone<T>(this: T[], other: T[]) {
  return other.difference(this).length === other.length
}

export function take<T>(this: T[], num: number) {
  return _take(this, num)
}

export function delimit<T, E>(this: T[], delimiter: (t: T) => E) {
  const stop = this.length - 1
  return this.flatMap((t, i) => i === stop ? t : [t, delimiter(t)])
}

export function zipWithIndex<T>(this: T[]) {
  return this.map((e, i) => [e, i])
}

export function toMap<T, K, V = T>(this: T[], key: Fun1<T, K>, value: Fun1<T, V> = _identity): Map<K, V> {
  const entries = this.map((t) => [key(t), value(t)] as [K, V])
  return new Map(entries)
}

export function first<T>(this: T[]): Option<T> {
  return Option.of(this[0])
}

export function findOne<T>(
  this: T[],
  predicate: (value: T, index: number, obj: T[]) => boolean,
  thisArg?: any
): Option<T> {
  return Option.of(this.find(predicate, thisArg))
}

export function replace<T>(this: T[], from: number, ...replacement: T[]) {
  return [
    ...[...this].splice(0, from),
    ...replacement,
    ...[...this].splice(from + replacement.length, this.length)
  ]
}

export function replaceAt<T>(this: T[], predicate: (value: T, index: number, obj: T[]) => boolean, ...replacement: T[]) {
  return this.replaceFrom(this.findIndex(predicate), ...replacement)
}

export function pushAt<T>(this: T[], predicate: (value: T, index: number, obj: T[]) => boolean, ...replacement: T[]) {
  const index = this.findIndex(predicate)
  return this.replaceFrom(index === -1 ? this.length : index, ...replacement)
}

const ArrayProto = Array.prototype as any
ArrayProto.distinctBy = distinctBy
ArrayProto.mergeBy = mergeBy
ArrayProto.flatMap = flatMap
ArrayProto.joinLeft = joinLeft
ArrayProto.rightJoin = rightJoin
ArrayProto.differenceBy = differenceBy
ArrayProto.orderBy = orderBy
ArrayProto.groupBy = groupBy
ArrayProto.bucketBy = bucketBy
ArrayProto.toQueryString = function <T>(this: Array<T>) {
  return this.reduce((a, b: any) => {
    const qs = typeof b.toQueryString === "function" ? b.toQueryString() : b
    return {...a, ...qs}
  }, {})
}

ArrayProto.keyBy = keyBy
ArrayProto.concatSelf = concatSelf

Object.defineProperty(ArrayProto, "isNotEmpty", {
  get: isNotEmpty
})

Object.defineProperty(ArrayProto, "isEmpty", {
  get: isEmpty
})

ArrayProto.difference = difference
ArrayProto.containsAll = containsAll
ArrayProto.containsNone = containsNone
ArrayProto.take = take
ArrayProto.delimit = delimit

Object.defineProperty(ArrayProto, "zipWithIndex", {
  get: zipWithIndex
})

ArrayProto.toMap = toMap
ArrayProto.first = first
ArrayProto.replaceFrom = replace
ArrayProto.replaceAt = replaceAt
ArrayProto.findOne = findOne
ArrayProto.pushAt = pushAt
