import {FormState, ValidatableMapOrArray} from "formstate/lib/core/formState"
import {applyValidators, ComposibleValidatable, Validator} from "formstate/lib/core/types"
import {extension} from "ts-trait/build/extension"

declare module "formstate/lib/core/types" {
  interface ComposibleValidatable<TValue> {
    validateIsolate(): Promise<string | null | undefined>
  }
}

declare module "formstate/lib/core/formState" {
  interface FormState<TValue extends ValidatableMapOrArray> extends ValidateIsolateFormState<TValue> {
  }
}

interface FormStatePrivate<TValue extends ValidatableMapOrArray> {
  getValues: () => ComposibleValidatable<any>[]
  _validators: Validator<TValue>[]
}

@extension([FormState])
class ValidateIsolateFormState<TValue extends ValidatableMapOrArray> {
  async validateIsolate(this: FormState<TValue>) {
    const values = (this as any as FormStatePrivate<TValue>).getValues()
    const fieldsResult = await Promise.all(values.map((value) => value.validateIsolate()))
    const error = fieldsResult.find(f => !!f)
    if (error) {
      return error
    }
    return applyValidators(this.$, (this as any as FormStatePrivate<TValue>)._validators || [])
  }
}
