const idToNetworkType: Record<string, NetworkType> = {}

export interface NetworkTypeParams {
  id: string
  name: string
}

export interface NetworkType extends NetworkTypeParams {

}

export class NetworkType {
  static parse(params: string) {
    return idToNetworkType[params] || NetworkType.unknown
  }

  static unknown = new NetworkType({
    id: "",
    name: ""
  })

  static rtb = new NetworkType({
    id: "0",
    name: "RTB"
  })

  static static = new NetworkType({
    id: "2",
    name: "Static"
  })

  static video = new NetworkType({
    id: "1",
    name: "Video"
  })

  static pmn = new NetworkType({
    id: "3",
    name: "Programmatic Mediation"
  })

  static mediation = new NetworkType({
    id: "4",
    name: "Mediation"
  })

  constructor(params: NetworkTypeParams) {
    Object.assign(this, params)
    idToNetworkType[params.id] = this
  }

  toJSON() {
    return this.id
  }

  get isMediated() {
    return this === NetworkType.pmn || this === NetworkType.mediation
  }

  get isProgMediation() {
    return this === NetworkType.pmn
  }

  get isNotProgrammatic() {
    return this === NetworkType.mediation
  }
}
