import {autoBind} from "@revdesk/util"
import {Option} from "tsla-util/lib/option"
import {UnitType} from "../spots/unitType"
import {CreativeType} from "./creativeType"
import {Platform} from "../platform"

export type PlacementTypeValue = "1" | "2" | "3" | ""

const idToPlacementType: Record<string, PlacementType> = {}
const nameToToPlacementType: Record<string, PlacementType> = {}

export interface PlacementTypeParams {
  id: PlacementTypeValue,
  name: string
  creativeTypes: CreativeType[],
  oldType: (creativeTypes: CreativeType[]) => UnitType
  documentationPlacementType: (platform: Platform) => string
}

export interface PlacementType extends PlacementTypeParams {
}

export class PlacementType {
  static parse(params: string) {
    return idToPlacementType[params] || PlacementType.UNKNOWN
  }

  static getPlacementTypeByName(name: string) {
    return nameToToPlacementType[name] || PlacementType.UNKNOWN
  }

  static values: PlacementType[]

  static INTERSTITIAL = new PlacementType({
    id: "2",
    name: "Interstitial",
    creativeTypes: [],
    oldType: () => UnitType.INTERSTITIAL,
    documentationPlacementType: (platform: Platform) => "interstitial"
  })

  static interstitial = PlacementType.INTERSTITIAL

  static REWARDED = new PlacementType({
    id: "3",
    name: "Rewarded",
    creativeTypes: [],
    oldType: () => UnitType.REWARDED,
    documentationPlacementType: (platform: Platform) =>
      platform.value === Platform.iOS.value ? "rewarded-ads" : "rewarded-video"
  })

  static rewarded = PlacementType.REWARDED

  static BANNER = new PlacementType({
    id: "1",
    name: "Banner",
    creativeTypes: [],
    oldType: creativeTypes => Option.of(
      [UnitType.MRECT, UnitType.BANNER].find(
        e => e.creativeTypes.containsAll(creativeTypes, e => e.id)
      )
    ).getOrElse(UnitType.UNKNOWN),
    documentationPlacementType: (platform: Platform) => "banner"
  })

  static banner = PlacementType.BANNER

  static UNKNOWN = new PlacementType({
    id: "",
    name: "Unknown",
    creativeTypes: [],
    oldType: () => UnitType.UNKNOWN,
    documentationPlacementType: (platform: Platform) => ""
  })

  static unknown = PlacementType.UNKNOWN

  @autoBind
  getCreativeTypes() {
    return this.creativeTypes
  }

  static selectable = [PlacementType.INTERSTITIAL, PlacementType.REWARDED, PlacementType.BANNER]

  constructor(params: PlacementTypeParams) {
    Object.assign(this, params)
    idToPlacementType[params.id] = this
    nameToToPlacementType[params.name.toLowerCase()] = this
  }

  toJSON() {
    return this.id
  }

  get toRecord() {
    return this.id ? this.id : null
  }

  get isUnknown() {
    return this === PlacementType.unknown
  }

  toLink(platform: Platform) {
    return `${platform.documentationURL}/${this.documentationPlacementType(platform)}`
  }
}
