import {lazyVal} from "@revdesk/util"
import {dataClass, DataClass} from "data-class-copy/lib"
import {AccessLevel} from "./access.level"
import {Resource} from "./resource"

export interface PermissionParams<T=any> {
  resource: Resource<T>;
  accessLevel: AccessLevel;
  resourceData: T;
}

export interface Permission<T=any> extends PermissionParams<T>, DataClass<Permission<T>> {

}

function parseDataJson(dataJSON: string) {
  try {
    return JSON.parse(dataJSON)
  } catch (err) {
    throw Error(`Permission.data(): unable to parse JSON ${dataJSON}: ${err.message}`)
  }
}

@dataClass
export class Permission<T=any> {
  static EMPTY = new Permission<any>({
    resource: Resource.UNKNOWN,
    accessLevel: AccessLevel.UNKNOWN,
    resourceData: ""
  })

  static parse(params: any = {}) {
    const resource = Resource.parse(params.resource)
    return Permission.EMPTY.copy({
      resource,
      accessLevel: AccessLevel.parse(params.accessLevel),
      resourceData: resource.toData(params.resourceData != null ? params.resourceData : parseDataJson(params.data))
    })
  }

  constructor(params: PermissionParams<T>) {
    Object.assign(this, params)
  }

  get data() {
    return this.resourceData
  }

  get resourceIDAccessLevelID() {
    return this.resource.id + "_" + this.accessLevel.id
  }

  toResourceRecords(roleId: number) {
    return {
      roleId,
      resource: this.resource.id,
      accessLevel: this.accessLevel.id,
      data: JSON.stringify(this.resourceData)
    }
  }
}
