import {dataClass, DataClass} from "data-class-copy"

export interface PairParams<T, E> {
  first: T;
  second: E;
}

export interface Pair<T, E> extends PairParams<T, E>, DataClass<Pair<T, E>> {

}

@dataClass
export class Pair<T, E> {
  static of<T, E>(first: T, second: E) {
    return new Pair({first, second})
  }

  constructor(params: PairParams<T, E>) {
    this.first = params.first
    this.second = params.second
  }
}
