import {parseBool, parseNumber, parseString} from "tsla-util/lib/parse"

export type ValueType = boolean | number | string

export interface ParamTypeParams<T extends ValueType = ValueType> {
  id: string
  name: string
  parse: (value: ValueType) => T
  stringify: (t: T) => string
  validators: ((val: T) => string | null | false)[]
}

export interface ParamType<T extends ValueType = ValueType> extends ParamTypeParams<T> {

}

const idToParamType: Record<string, ParamType<any>> = {}

export class ParamType<T extends ValueType = ValueType> {
  static parse(params: string) {
    return idToParamType[params] || ParamType.unknown
  }

  static boolean = new ParamType<boolean>({
    id: "3",
    name: "Boolean",
    parse: value => typeof value === "boolean" ? value : parseBool(value),
    stringify: t => t ? t.toString() : "",
    validators: [(t) => typeof t !== "boolean" && "Field must be either Yes or No"]
  })

  static number = new ParamType<string>({
    id: "2",
    name: "Number",
    parse: parseString,
    stringify: t => t.toString(),
    validators: [(t) => isNaN(Number(t)) && "Field must be a number"]
  })

  static string = new ParamType<string>({
    id: "1",
    name: "String",
    parse: parseString,
    stringify: t => t.toString(),
    validators: []
  })

  static enum = new ParamType<string>({
    id: "4",
    name: "Enum",
    parse: parseString,
    stringify: t => t.toString(),
    validators: []
  })

  static unknown = new ParamType({
    id: "",
    name: "Unknown",
    parse: e => {
      return e
    },
    stringify: t => {
      return t.toString()
    },
    validators: []
  })

  constructor(params: ParamTypeParams<T>) {
    Object.assign(this, params)
    idToParamType[params.id] = this
  }

  toJSON() {
    return this.id
  }
}
