import {Fun1} from "@revdesk/base-types"
import {Group, Roll} from "@revdesk/model"
import {Validator} from "./validator"

// tslint:disable
export const EMAIL_REGEXP = /^(?=.{1,254}$)(?=.{1,64}@)[-!#$%&'*+/0-9=?A-Z^_`a-z{|}~]+(\.[-!#$%&'*+/0-9=?A-Z^_`a-z{|}~]+)*@[A-Za-z0-9]([A-Za-z0-9-]{0,61}[A-Za-z0-9])?(\.[A-Za-z0-9]([A-Za-z0-9-]{0,61}[A-Za-z0-9])?)*$/;
const NAME_MAX_LENGHT = 30

// tslint:enable

export class Validators {
  static email = new Validator<string>({
    name: "email",
    message: "Please enter a valid E-mail address",
    fn: value => {
      if (!value) {
        return true;
      }
      return EMAIL_REGEXP.test(value)
    }
  })

  static required = new Validator<any>({
    name: "required",
    message: "This field is required",
    fn: value => {
      return !!value
    }
  })

  static delay = new Validator<any>({
    name: "delay",
    message: "delay",
    fn: value => new Promise<boolean>(resolve => setTimeout(() => resolve(false), 5))
  })

  static notEmpty = new Validator<any[]>({
    name: "notEmpty",
    message: "This field is required",
    fn: value => {
      return value && !!value.length
    }
  })

  static validId = new Validator<any>({
    name: "group",
    message: "This field is required",
    fn: value => {
      return value && !!value.id
    }
  })

  static sameGroupType = new Validator<Roll[]>({
    name: "sameGroupType",
    message: "Illegal role value",
    fn: value => {
      const groupType = value[0].groupType
      return value.every(e => e.groupType === groupType)
    }
  })

  static every = <T>(fn: Fun1<T, boolean>) => new Validator<T[]>({
    name: "every",
    message: "Illegal role value",
    fn: value => value.every(fn)
  })

  static groupAdmin = new Validator<Group>({
    name: "groupAdmin",
    message: "Cannot set administrator to existing account",
    fn: (value, form) => {
      const roleFormControl = form.getControl("role")
      const role = roleFormControl.value as Roll[]
      if (role[0] && role[0].lead) {
        // return !value.id
      }
      return true
    }
  })
}
