import ClickAwayListener from "@material-ui/core/ClickAwayListener"
import Popper, {PopperProps} from "@material-ui/core/Popper"
import * as React from "react"

export interface WithPopperProps extends Partial<PopperProps> {
  anchor: (onClick: React.MouseEventHandler<any>) => React.ReactNode
  children: (onClose: () => any) => React.ReactNode
}

export class WithPopper extends React.Component<WithPopperProps> {
  state = {
    anchorEl: null as any,
  }

  handleClose = () => {
    this.setState({anchorEl: null})
  }

  handleAnchorClick: React.MouseEventHandler<HTMLElement> = (e) => {
    this.setState({anchorEl: e.currentTarget})
  }

  render() {
    const {children, anchor, ...rest} = this.props
    return <React.Fragment>
      {anchor(this.handleAnchorClick)}

      <Popper open={Boolean(this.state.anchorEl)} anchorEl={this.state.anchorEl}  {...rest}>
        <ClickAwayListener onClickAway={this.handleClose}>
          {children(this.handleClose)}
        </ClickAwayListener>
      </Popper>
    </React.Fragment>
  }
}
