import {observable, action} from "mobx"
import {dataClass, DataClass} from "data-class-copy"
import {parseNumber} from "tsla-util/lib/parse"

export interface PaginationParams {
  limit: number
  page: number
}

export interface Pagination extends PaginationParams, DataClass<PaginationParams> {

}

@dataClass
export class Pagination {
  static empty = () => new Pagination({
    limit: 10,
    page: 1
  })

  static parse(params: any = {}) {
    return new Pagination({
      limit: parseNumber(params.limit),
      page: parseNumber(params.page),
    })
  }

  @observable limit!: number
  @observable page!: number

  constructor(params: PaginationParams) {
    Object.assign(this, params)
  }

  get total() {
    const {limit, page} = this
    return limit * page
  }

  changeOffset(offset: number) {
    return this.mutate({
      page: Math.floor(offset / this.limit) + 1
    })
  }

  effectiveLimit(maxLimit = Infinity) {
    const {limit} = this
    return limit > 0 ? limit : maxLimit
  }

  eagerLimitQuery() {
    const limit = this.effectiveLimit()
    return limit === Infinity ? {} : this.limitQuery()
  }

  limitQuery(maxLimit?: number) {
    const {page} = this
    const limit = this.effectiveLimit(maxLimit)
    return {
      limit,
      offset: limit * (page - 1)
    }
  }

  toQueryString(): Record<string, string> {
    return {
      page: String(this.page),
      limit: String(this.limit)
    }
  }

  @action changeLimit(limit: number) {
    return this.mutate({limit})
  }

  @action nextPage() {
    return this.mutate({
      page: this.page + 1
    })
  }

  @action prevPage() {
    return this.mutate({
      page: this.page - 1
    })
  }

  @action moveToPage(page = 0) {
    return this.mutate({page})
  }

  get offset() {
    const {limit, page} = this
    return limit * (page - 1)
  }

  pageBounds(page: number) {
    const {limit} = this
    const from = limit * (page - 1)
    return {
      from,
      to: from + limit
    }
  }

  pageOf(index: number) {
    return Math.floor(index / this.limit) + 1
  }
}
