import {DataClass, dataClass} from "data-class-copy"
import {Publisher} from "./inventory/publisher"
import {User} from "./user"

export interface TermsSingingParams {
  user: User;
  singedAt: Date;
  ip: string;
  version: string;
}

export interface TermsSinging extends TermsSingingParams, DataClass<TermsSingingParams> {

}

@dataClass
export class TermsSinging {
  static EMPTY = new TermsSinging({
    user: User.EMPTY,
    ip: "",
    version: "",
    singedAt: new Date(0)
  })

  static parse(params: Record<string, any> = {}) {
    return TermsSinging.EMPTY.copy({
      user: User.parse(params.user),
      ip: params.ip != null ? String(params.ip) : "",
      version: params.version != null ? String(params.version) : "",
      singedAt: params.createdAt != null ? new Date(params.createdAt) : new Date()
    })
  }

  constructor(params: TermsSingingParams) {
    Object.assign(this, params)
  }

  get publisher() {
    return this.user.publisher
  }
}
