import {Fun} from "@revdesk/base-types"
import {Observe} from "@revdesk/util/build/observe"
import {Observable} from "rxjs/Observable"
import "rxjs/add/observable/from"
import "rxjs/add/observable/fromPromise"
import "rxjs/add/observable/timer"
import "rxjs/add/operator/debounce"
import "rxjs/add/operator/debounceTime"
import "rxjs/add/operator/switchMap"
import "rxjs/add/operator/takeUntil"
import {Actions, actions, ActionsParams} from "../old-actions"
import {FormActions} from "./formActions"
import {FormControl} from "./formControl"
import {ValidationError, Validator} from "./validator"

export interface FormControlActionsParams extends ActionsParams<FormControl> {
  formActions: FormActions;
  validators: Validator<any>[];
}

export interface FormControlActions extends FormControlActionsParams, Actions<FormControl> {

}

const conf = 250

const imm = 0

@actions
export class FormControlActions implements Actions<FormControl> {
  _validationPromise: Promise<ValidationError[]> | null = null;
  resolve: any

  observer = (errors: ValidationError[]) => {
    if (errors.length) {
      this.state = this.state.changeErrors(errors)
    } else {
      this.state = this.state.persistPendingValue()
    }
    this.resolve(this.state.errors)
  }

  stopValidationO = Observe.simple()

  validationO = this.newValidationO

  constructor(params: FormControlActionsParams) {
    Object.assign(this, params)
    this.Actions$super(params)
  }

  get newValidationO() {
    return new Observe(
      obs => obs.debounce((t = conf) => Observable.timer(t))
        .switchMap(() => Observable.fromPromise(this.validateControl()))
        .takeUntil(this.stopValidationO.obs),
      this.observer
    )
  }

  validateControl = async () => {
    const values = await Promise.all(
      this.validators.map(
        validator => validator.validate(this.state.pendingValue, this.formActions && this.formActions.state)
      )
    )
    return values.filter(e => e != null)
  }

  changePendingValue(value: any) {
    this.state.changePendingValue(value)
  }

  changeBlur() {
    this.runValidation()
    // if (this._validationPromise == null || this.state.diff) {

    // }
  }

  pendAndValidate(value: any) {
    this.state = this.state.changePendingValue(value)
    this.runValidation()
  }

  changeFocus() {
    this.setState(this.state.changeFocus())
  }

  setResolver = (resolve: Fun) => {
    this.resolve = resolve
  }

  runValidation(t = conf) {
    this._validationPromise = new Promise(this.setResolver)
    this.validationO.next(t)
    return this._validationPromise
  }

  validationPromise() {
    return this._validationPromise ? this._validationPromise : this.runValidation(imm)
  }

  clearValidations() {
    this._validationPromise = null
    this.runValidation(imm)
  }

  validateNow() {
    this.runValidation(imm)
  }

  stopValidation() {
    this.state = this.state.changeErrors([])
    this._validationPromise = null
    this.stopValidationO.next()
    this.validationO = this.newValidationO
  }

  get initialState() {
    return FormControl.EMPTY
  }
}
