import {DataClass, dataClass} from "data-class-copy"

type URLConstructor = new(url: string, base?: string | URL) => URL

const idToCollinsCluster: Record<string, CollinsCluster> = {}

export interface CollinsClusterParams {
  id: string
  revenueDesk: string
  revenueDeskFyber: string
  mui: string
  pecan: string
  pecanFyber: string
}

export interface CollinsCluster extends CollinsClusterParams, DataClass<CollinsClusterParams> {

}

@dataClass
export class CollinsCluster {
  static parse(params: string) {
    return idToCollinsCluster[params] || CollinsCluster.dev
  }

  static prod = new CollinsCluster({
    id: "collins",
    revenueDesk: "https://revenueDesk.inner-active.com",
    revenueDeskFyber: "https://revenueDesk.fyber.com",
    mui: "https://console.fyber.com",
    pecan: "https://pecan.inner-active.com",
    pecanFyber: "https://pecan.fyber.com"
  })

  static staging = new CollinsCluster({
    id: "collins-testing",
    revenueDesk: "https://collins-testing.inner-active.com",
    revenueDeskFyber: "https://collins-testing.fyber.com",
    mui: "https://mui-beta.fyber.com",
    pecan: "https://testing-pecan.inner-active.com/",
    pecanFyber: "https://pecan-testing.fyber.com"
  })

  static qa = new CollinsCluster({
    id: "collins-qa",
    revenueDesk: "https://console3-qa.inner-active.com",
    revenueDeskFyber: "https://collins-qa.fyber.com",
    mui: "https://production.com",
    pecan: "https://testing-pecan.inner-active.com/",
    pecanFyber: "https://pecan-testing.fyber.com"
  })

  static dev = new CollinsCluster({
    id: "collins-dev",
    revenueDesk: "https://collins-qa-testing.inner-active.com",
    revenueDeskFyber: "https://collins-qa-testing.fyber.com",
    mui: "https://mui-dev.fyber.com",
    pecan: "https://testing-pecan.inner-active.com/",
    pecanFyber: "https://pecan-testing.fyber.com"
  })

  static local = new CollinsCluster({
    id: "collins-local",
    revenueDesk: "http://localhost:5000",
    revenueDeskFyber: "http://localhost:5000",
    mui: "http://localhost:8080",
    pecan: "http://localhost:9095",
    pecanFyber: "http://localhost:9095",
  })

  constructor(params: CollinsClusterParams) {
    this.id = params.id
    this.mui = params.mui
    this.revenueDesk = params.revenueDesk
    this.revenueDeskFyber = params.revenueDeskFyber
    this.pecan = params.pecan
    this.pecanFyber = params.pecanFyber
    idToCollinsCluster[params.id] = this
  }

  // URL exists globally on modern browsers, but in NodeJS must be imported
  asRevenueDeskFyberURL(URL: URLConstructor) {
    return new URL(this.revenueDeskFyber)
  }

  asMuiURL(URL: URLConstructor) {
    return new URL(this.mui)
  }

  toJSON() {
    return this.id
  }
}
