const byID: Record<string, SelectMode> = {}
const values: Array<SelectMode> = []

export type SelectOptionValue = "include" | "exclude"

export interface SelectModeParams {
  id: string;
  icon: string;
  icon2: Record<"true" | "false", string>;
  itemIcon: string;
  title: string;
  passive: string;
  asciiIcon: string;
}

export interface SelectMode extends SelectModeParams {

}

export class SelectMode {
  static fromJS(id: SelectOptionValue) {
    return byID[id] || SelectMode.INCLUDE
  }

  static INCLUDE = new SelectMode({
    id: "include",
    icon: "fa-plus-square",
    icon2: {
      true: "fa-square",
      false: "fa-check"
    },
    itemIcon: "fa-check",
    title: "Include",
    passive: "included",
    asciiIcon: "&#10003;"
  })

  static EXCLUDE = new SelectMode({
    id: "exclude",
    icon: "fa-minus-square-o",
    icon2: {
      true: "fa-square",
      false: "fa-times"
    },
    itemIcon: "fa-times",
    title: "Exclude",
    passive: "excluded",
    asciiIcon: "&#10007;"
  })

  static values = values

  constructor(params: SelectModeParams) {
    Object.assign(this, params)
    byID[params.id] = this
    values.push(this)
  }

  toJS() {
    return this.id
  }
}
