export type PlaybackMethodValue = 1 | 2 | 3

export interface PlaybackMethodBools  {
  autoPlay: boolean,
  muted?: boolean
}

function boolsEqual(bools1: any, bools2: any) {
  return bools1.autoPlay === bools2.autoPlay && bools1.muted === bools2.muted
}

export interface PlaybackMethodJS {
  id: PlaybackMethodValue,
  bools: PlaybackMethodBools
}

export interface PlaybackMethod extends PlaybackMethodJS {

}

const values: PlaybackMethod[] = []

export class PlaybackMethod {

  static AUTO_PLAY_UNMUTED = new PlaybackMethod({
    id: 1,
    bools: {
      autoPlay: true,
      muted: false
    }
  })

  static AUTO_PLAY_MUTED = new PlaybackMethod({
    id: 2,
    bools: {
      autoPlay: true,
      muted: true
    }
  })

  static NO_AUTO_PLAY = new PlaybackMethod({
    id: 3,
    bools: {
      autoPlay: false,
    }
  })

  static fromBools(bools: PlaybackMethodBools) {
    return !bools.autoPlay ? PlaybackMethod.NO_AUTO_PLAY : values.find(v => boolsEqual(bools, v.bools))
  }

  constructor(params: PlaybackMethodJS) {
    this.id = params.id
    this.bools = params.bools
    values.push(this)
  }
}
